import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { CCBoolean } from 'cc';
import { _decorator, AudioClip, AudioSource, CCFloat, CCString, Component, Node, sys } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('AudioClipData')
class AudioClipData {
    @property({type: CCString})
    name: string ;
    @property({type: AudioClip})
    audioClips: AudioClip[] = [];
    @property({type: CCFloat})
    delayBetweenClips: number = 0;
    @property({type: CCFloat})
    volume: number = 1;
    @property({type: CCBoolean})
    loop: boolean;
    isLoopPlaying: boolean;
    lastPlayedTime: number = 0;


    clipIndex = 0;
    GetClip(): AudioClip {
        let clip = this.audioClips[this.clipIndex];
        this.clipIndex = (this.clipIndex + 1) % this.audioClips.length;
        if(!this.CanPlay()){
            return;  
        }
        this.lastPlayedTime = Date.now();
        return clip;
    }

    CanPlay(): boolean {
        return Date.now() - this.lastPlayedTime > this.delayBetweenClips * 1000;
    }
}
@ccclass('AudioManager')
export class AudioManager extends Component {
    public static instance: AudioManager = null;
    @property({type: AudioClipData})
    audioClipData: AudioClipData[] = [];
    @property({type: AudioSource})
    backgroundAudioSource: AudioSource = null;
    localStorageAudioEnabledKey = "SNLAudioEnabled";
    @property({type: Prefab})
    audioSourcePrefab: Prefab = null;
    audioSources: AudioSource[] = [];

    _isAudioEnabled: 'true' | 'false' = 'true';
    public get IsAudioEnabled(): 'true' | 'false' {
        return this._isAudioEnabled;
    }
    protected onLoad(): void {
        AudioManager.instance = this;
        this.ReadAudioEnabledFromLocalStorage();
    }
    protected start(): void {
        this.SetAudioEnabled(this.IsAudioEnabled);
    }
    
    public playSound(name: string) {
        if (this.IsAudioEnabled == 'false') {
            return;
        }
        let audioClipData = this.audioClipData.find((audioClipData) => audioClipData.name == name);
        if (audioClipData == null) {
            // console.warn("Audio Clip not found for ", name);
            return;
        }
        let clip = audioClipData.GetClip();
        if (clip) {
            if(audioClipData.isLoopPlaying) return;
            // console.log("---Playing Sound: ", name);
            var emptyAudioSource = this.getEmptyAudioSource();
            // console.log("emptyAudioSource: ", emptyAudioSource.uuid);
            emptyAudioSource.loop = audioClipData.loop;
            audioClipData.isLoopPlaying = audioClipData.loop;
            emptyAudioSource.volume = audioClipData.volume;
            emptyAudioSource.clip = clip;
            if(audioClipData.loop){
                emptyAudioSource.play();
            }
            else{
                emptyAudioSource.playOneShot(clip);
            }
            // console.log("Playing Sound: ", emptyAudioSource.playing, emptyAudioSource.uuid);
        }
        else{
            console.warn("Audio Clip is in use ", name);
        }
    }

    public stopSound(name: string) {
        // console.warn("Stop Sound: ", name);
        let audioClipData = this.audioClipData.find((audioClipData) => audioClipData.name == name);
        if (!audioClipData) {
            console.warn("Audio Clip not found for ", name);
            return;
        }
        this.audioSources.forEach((audioSource) => {
            if (audioSource.clip == audioClipData.audioClips[audioClipData.clipIndex]) {
                audioClipData.isLoopPlaying = false;
                audioSource.stop();
            }
        });
    }

    public stopAllSounds() {
        this.audioSources.forEach((audioSource) => {
            audioSource.stop();
        });
        this.audioClipData.forEach((audioClipData) => {
            audioClipData.isLoopPlaying = false;
        });
    }

    public ToggleAudio() {
        if(this.IsAudioEnabled == 'true') {
            this.SetAudioEnabled('false');
        }
        else {
            this.SetAudioEnabled('true');
        }
        
    }
    public SetAudioEnabled(enabled: 'true' | 'false') {
        this._isAudioEnabled = enabled;
        this.UpdateBackgroundMusic();
        this.WriteAudioEnabledToLocalStorage();
    }
    WriteAudioEnabledToLocalStorage() {
        sys.localStorage.setItem(this.localStorageAudioEnabledKey, this.IsAudioEnabled);
    }
    ReadAudioEnabledFromLocalStorage() {
        const value = sys.localStorage.getItem(this.localStorageAudioEnabledKey);
        if(value == null) {
            this._isAudioEnabled = 'true';
        }
        else
            this._isAudioEnabled = value;
    }
    public UpdateBackgroundMusic() {
        if(this.IsAudioEnabled == 'true') {
            this.backgroundAudioSource?.play();
        }
        else {
            this.backgroundAudioSource?.stop();
        }
    }

    printAllPlayingAudioSources() {
        this.audioSources.forEach((audioSource) => {
            if(audioSource.playing)
            console.log("Playing audio source: ", audioSource.playing, audioSource.clip.name, audioSource.uuid);
        });
    }

    getEmptyAudioSource() {
        let audioSource = this.audioSources.find((audioSource) => !audioSource.playing);
        if(audioSource == null) {
            audioSource = this.createAudioSource();
        }
        return audioSource;
    }

    createAudioSource() {
        let audioSource = instantiate(this.audioSourcePrefab).getComponent(AudioSource);
        audioSource.node.setParent(this.node);
        this.audioSources.push(audioSource);
        console.log("==== Created new audio source: ", this.audioSources.length);
        return audioSource;
    }
}

