import { _decorator, Component, EventTarget, CCBoolean } from 'cc';
import Colyseus from 'db://colyseus-sdk/colyseus.js';
import { ServerMessageType } from '../types/NetworkTypes';
import { PlayerType } from '../types/PlayerTypes';
import { PbSaveSystem } from '../pb-cocos-utils/PbSaveSystem';
import { NetworkConfig } from './NetworkConfig';
import { NetworkEvents, ConnectionState } from '../types/NetworkTypes';
import { BasePlayerData } from '../schema/BasePlayerData';
import { PBLogger } from '../pb-cocos-utils/PBLogger';
import { BaseRoomState } from '../schema/BaseRoomState';
import { BaseRoomSettings } from '../schema/BaseRoomSettings';
const { ccclass, property } = _decorator;

//  Data structure for storing reconnection information
interface ReconnectionData {
    roomId: string;
    reconnectionToken: string;
}

//  Default reconnection data used as fallback
const DEFAULT_RECONNECTION_DATA: ReconnectionData = {
    roomId: 'testroom1',
    reconnectionToken: 'eyJ',
};

// Event emitters for network-related events
export const ConnectionStateChangeEvent = new EventTarget();
export const ServerRoomStateChangeEvent = new EventTarget();
export const ServerMessageEvent = new EventTarget();

//  NetworkManager handles all network communication with the Colyseus server
//  including connection, reconnection, and message handling.
@ccclass('NetworkManager')
export class NetworkManager extends Component {
    @property({ type: CCBoolean, tooltip: 'Enable development mode for additional logging' })
    public isDevMode: boolean;

    private static _instance: NetworkManager | null = null;

    @property({ type: NetworkConfig, tooltip: 'Network configuration settings' })
    private networkConfig: NetworkConfig = null!;

    private _client!: Colyseus.Client;
    private _room!: Colyseus.Room;
    private _currentConnectionState: ConnectionState = ConnectionState.INITIALIZING;
    private _reconnectionData: ReconnectionData = { ...DEFAULT_RECONNECTION_DATA };
    
    //  Singleton instance accessor
    //  @throws Error if instance is not initialized
    public static get Instance(): NetworkManager {
        if (!this._instance) {
            throw new Error('NetworkManager instance is not initialized.');
        }
        return this._instance;
    }

    //  Get the current Colyseus room
    public get CurrentRoom(): Colyseus.Room {
        return this._room;
    }

    //  Get the current connection state
    public get CurrentConnectionState(): ConnectionState {
        return this._currentConnectionState;
    }

    //  Initialize the NetworkManager when the component loads
    protected onLoad(): void {
        if (NetworkManager._instance) {
            this.log('NetworkManager instance already exists. Destroying duplicate.');
            this.node.destroy();
            return;
        }
        
        NetworkManager._instance = this;
        this.networkConfig.parseQueryParams();
        if(!this.isDevMode) {
            PBLogger.setProductionMode(true);
        }
    }

    /**
     * Initialize the client when the component starts
     */
    start(): void {
        this.initializeClient();
        PBLogger.info("============ Initializing client =========");
        if(this.networkConfig.assignedFromQueryParams) {
            PBLogger.info("============ Connecting to room =========");
            this.connect(this.networkConfig.roomId, this.networkConfig.userId);
        }
    }

    /**
     * Initialize the Colyseus client with the configured server details
     */
    private initializeClient(): void {
        const protocol = this.networkConfig.useSSL ? 'wss' : 'ws';
        let endpoint = "";
        if(this.networkConfig.serverPort==0) {
            endpoint = `${protocol}://${this.networkConfig.serverHost}`;
        } else {
            endpoint = `${protocol}://${this.networkConfig.serverHost}:${this.networkConfig.serverPort}`;
        }
        
        this.log(`Initializing client with endpoint: ${endpoint}`);
        this._client = new Colyseus.Client(endpoint);
    }

    /**
     * Check if we should attempt to rejoin a previous room
     */
    private shouldRejoin(): boolean {
        if(this.isDevMode) {
            return false;
        }
        this._reconnectionData = PbSaveSystem.LoadData(this.networkConfig.gameName);
        if (!this._reconnectionData || !this._reconnectionData.roomId || !this._reconnectionData.reconnectionToken) {
            return false;
        }
        return this._reconnectionData.roomId === this.networkConfig.roomId;
    }

    /**
     * Connect to an existing room
     * @param roomId - The ID of the room to join
     * @param playerName - The name of the player
     * @throws Error if connection fails
     */
    async connect(roomId: string, playerName: string): Promise<void> {
        PBLogger.info("============ Connecting to room from connect function =========");
        this.updateConnectionState(ConnectionState.CONNECTING);

        if(this.shouldRejoin()) {
            this.log('Rejoining room', roomId);
            await this.rejoin();
            return;
        }
        
        try {
            const playerData = this.createPlayerData(playerName);
            this.log("Joining with options ", { player: playerData });
            
            this._room = await this._client.joinById(roomId, {
                player: playerData
            });
            
            this.initAfterJoin(roomId);
        } catch (error) {
            this.handleConnectionError(error);
            throw error;
        }
    }
    
    /**
     * Initialize room after successful join
     * @param roomId - The ID of the joined room
     */
    private initAfterJoin(roomId: string): void {
        this.updateConnectionState(ConnectionState.JOINED_ROOM);
        this.log(`Joined room ${roomId} with sessionId: ${this._room.sessionId}`);
        this.initializeRoomCallbacks();
        this.saveReconnectionData();
    }
    
    /**
     * Create and join a new room
     * @param roomId - The ID for the new room
     * @param playerName - The name of the player
     */
    async createAndJoinRoom(roomId: string, playerName: string): Promise<void> {
        this.log('Creating room', roomId);
        this.updateConnectionState(ConnectionState.CONNECTING);
        
        try {
            const playerData = this.createPlayerData(playerName);
            
            this._room = await this._client.create("PictionaryRoom", { 
                roomId: roomId, 
                player: playerData,
                roomSettings: {
                    maxClients: 10,
                }
            });
            
            this.initAfterJoin(roomId);
        } catch (error) {
            this.handleConnectionError(error);
            throw error;
        }
    }

    /**
     * Create player data object
     * @param playerName - The name of the player
     */
    private createPlayerData(playerName: string): Partial<BasePlayerData> {
        return {
            userId: playerName,
            name: playerName,
            avatar: "randomProfilePicture",
            type: PlayerType.HUMAN,
        };
    }

    /**
     * Initialize callbacks for room events
     */
    private initializeRoomCallbacks(): void {
        console.log("============ Initializing room callbacks =========");
        if (!this._room) {
            this.log('Cannot initialize callbacks: Room is not defined');
            return;
        }

        this._room.onStateChange((state) => {
            ServerRoomStateChangeEvent.emit(NetworkEvents.SERVER_ROOM_STATE_CHANGE, state);
        });

        this._room.onLeave((code) => {
            if (code === 4001) {
                this.updateConnectionState(ConnectionState.KICKED_OUT);
            } else {
                this.updateConnectionState(ConnectionState.DISCONNECTED);
            }
        });

        this._room.onMessage(ServerMessageType.KICKED_OUT, () => {
            this.updateConnectionState(ConnectionState.KICKED_OUT);
        });
        
        this._room.onMessage("*", (type, message) => {
            ServerMessageEvent.emit(NetworkEvents.SERVER_MESSAGE, type, message);
        });
    }

    /**
     * Update the connection state and emit change event
     * @param nextState - The new connection state
     */
    private updateConnectionState(nextState: ConnectionState): void {
        if (this._currentConnectionState === nextState) return;
        
        const previousState = this._currentConnectionState;
        this._currentConnectionState = nextState;
        
        this.log(`Connection state changed: ${ConnectionState[previousState]} -> ${ConnectionState[nextState]}`);
        ConnectionStateChangeEvent.emit(NetworkEvents.CONNECTION_STATE_CHANGE, previousState, nextState);
    }

    /**
     * Attempt to reconnect to the room
     * @param attempt - Current attempt number
     * @param maxAttempts - Maximum number of attempts
     * @param interval - Delay between attempts in milliseconds
     */
    public async reconnect(attempt: number = 1, maxAttempts: number = 10, interval: number = 2000): Promise<void> {
        if (this._currentConnectionState !== ConnectionState.DISCONNECTED && 
            this._currentConnectionState !== ConnectionState.RECONNECTING) {
            this.log(`Cannot reconnect: Current state is ${ConnectionState[this._currentConnectionState]}`);
            return;
        }

        // Get max attempts from room settings if available
        if (this._room && this._room.state) {
            const baseRoomState: BaseRoomState<BaseRoomSettings, BasePlayerData> = this._room.state;
            if (baseRoomState.roomSettings && baseRoomState.roomSettings.maxReconnectionAttempts) {
                maxAttempts = baseRoomState.roomSettings.maxReconnectionAttempts;
            }
        }

        this.updateConnectionState(ConnectionState.RECONNECTING);
        this.log(`Reconnect attempt ${attempt}/${maxAttempts}`);

        try {
            await this.delay(interval);
            
            if (!this._room || !this._room.reconnectionToken) {
                throw new Error('Reconnection token not available');
            }
            
            const reconnectedRoom = await this._client.reconnect(this._room.reconnectionToken);
            this._room = reconnectedRoom;
            
            this.saveReconnectionData();
            this.initializeRoomCallbacks();
            this.updateConnectionState(ConnectionState.JOINED_ROOM);
            
            this.log('Reconnected successfully.');
        } catch (error) {
            this.handleReconnectionError(error, attempt, maxAttempts, interval);
        }
    }

    /**
     * Attempt to rejoin a room using stored reconnection data
     * @param attempt - Current attempt number
     * @param maxAttempts - Maximum number of attempts
     * @param interval - Delay between attempts in milliseconds
     */
    private async rejoin(attempt: number = 1, maxAttempts: number = 10, interval: number = 2000): Promise<void> {
        this.log(`Rejoin attempt ${attempt}/${maxAttempts}`);
        
        try {
            await this.delay(interval);
            
            if (!this._reconnectionData || !this._reconnectionData.reconnectionToken) {
                throw new Error('Reconnection data not available');
            }
            
            // Format the reconnection token correctly as "roomId:reconnectionToken"
            const formattedToken = `${this._reconnectionData.roomId}:${this._reconnectionData.reconnectionToken}`;
            PBLogger.info("Using formatted reconnection token: ", formattedToken);
            
            const reconnectedRoom = await this._client.reconnect(formattedToken);
            this._room = reconnectedRoom;
            
            this.saveReconnectionData();
            this.initializeRoomCallbacks();
            this.updateConnectionState(ConnectionState.JOINED_ROOM);
            
            this.log('Rejoined successfully.');
        } catch (error) {
            this.handleReconnectionError(error, attempt, maxAttempts, interval, 'reJoin');
        }
    }

    /**
     * Create a delay using a promise
     * @param ms - Milliseconds to delay
     */
    private async delay(ms: number): Promise<void> {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    /**
     * Handle connection errors
     * @param error - The error that occurred
     */
    private handleConnectionError(error: unknown): void {
        const errorMessage = (error as Error)?.message || 'Unknown error';
        this.log('Error connecting to room:', errorMessage);
        this.updateConnectionState(ConnectionState.DISCONNECTED);
    }

    /**
     * Handle reconnection errors and retry if appropriate
     * @param error - The error that occurred
     * @param attempt - Current attempt number
     * @param maxAttempts - Maximum number of attempts
     * @param interval - Delay between attempts in milliseconds
     * @param method - The reconnection method being used
     */
    private handleReconnectionError(
        error: unknown, 
        attempt: number, 
        maxAttempts: number, 
        interval: number, 
        method: 'reconnect' | 'reJoin' = 'reconnect'
    ): void {
        const errorMessage = (error as Error)?.message || 'Unknown error';
        this.log(`${method} attempt ${attempt} failed:`, errorMessage);
        
        if (attempt < maxAttempts || maxAttempts === -1) {
            if (method === 'reconnect') {
                this.reconnect(attempt + 1, maxAttempts, interval);
            } else {
                this.rejoin(attempt + 1, maxAttempts, interval);
            }
        } else {
            this.log('Max reconnection attempts reached. Please check your network or contact support.');
            this.updateConnectionState(ConnectionState.NOT_FOUND);
        }
    }

    /**
     * Save reconnection data for future use
     */
    private saveReconnectionData(): void {
        PBLogger.info("============ Saving reconnection data =========");
        if (!this._room || !this._room.reconnectionToken) {
            this.log('Cannot save reconnection data: Room or reconnection token not available');
            return;
        }
        
        // Extract just the token part if it contains roomId:token format
        let tokenToSave = this._room.reconnectionToken;
        if (tokenToSave.includes(':')) {
            tokenToSave = tokenToSave.split(':')[1];
        }
        
        PBLogger.info("saving Reconnection token: ", tokenToSave);
        
        this._reconnectionData = {
            roomId: this.networkConfig.roomId,
            reconnectionToken: tokenToSave,
        };
        
        PbSaveSystem.SaveData(this.networkConfig.gameName, this._reconnectionData);
        PBLogger.info('Reconnection data saved');
    }

    /**
     * Leave the current room
     */
    public leaveRoom(): void {
        if (!this._room) {
            this.log('Cannot leave room: Room is not defined');
            return;
        }
        
        this.log('Leaving room');
        this._room.leave(true)
            .then(() => this.log('Left room successfully'))
            .catch(error => this.log('Error leaving room:', error?.message || 'Unknown error'));
    }

    public getMySessionId(): string {
        return this._room.sessionId;
    }

    /**
     * Send a message to the server
     * @param type - The type of message
     * @param message - The message data
     */
    public sendMessage(type: string, message: unknown): void {
        if (!this._room) {
            this.log('Cannot send message: Room is not defined');
            return;
        }
        
        if (this.isDevMode) {
            this.log('Sending message:', type, message);
        }
        
        this._room.send(type, message);
    }

    /**
     * Log a message with optional data
     * @param message - The message to log
     * @param data - Optional data to log
     */
    public log(message: string, ...data: unknown[]): void {
        if (this.isDevMode) {
            if (data.length > 0) {
                PBLogger.info(`[NetworkManager] ${message}`, ...data);
            } else {
                PBLogger.info(`[NetworkManager] ${message}`);
            }
        }
    }
}