import { Node } from "cc";
import { Player, PlayerState } from "../Player";
import {PlayerBaseState } from "./PlayerBaseState";
import { PlayerStateMachine } from "./PlayerStateMachine";
import { Piece } from "../Piece";
import { SNLRoomSettings } from "../schema/SNLRoomSettings";
import { GameMessageType } from "../type/GameMessageType";
import { Vec3 } from "cc";
import { PawnHandler } from "../PawnHandler";
import { UIManager } from "../../UIPack/Scripts/UIManager";
import { GameManager } from "../GameManager";
import { NetworkManager } from "../../pulse/networking/NetworkManager";
export class MovePieceState extends PlayerBaseState{
    stateMachine: PlayerStateMachine;
    _stateName : PlayerState = PlayerState.MOVING_PIECE;
    player: Player;
    moveOptionsOffset : Vec3 = new Vec3(0, 120, 0);
    constructor(stateMachine: PlayerStateMachine) {
        super();
        this.stateMachine = stateMachine;
        this.player = this.stateMachine.player;
    }

    enterState(): void {
        console.log("Entered Moving Piece State");
        console.log("Rolls : ", this.player.outComesUIManager.getOutComes());
        const currentPlayerData = this.stateMachine.currentServerState.playersData[this.player.playerNumber];
        this.player.outComesUIManager.reset(currentPlayerData.rollsList);
        this.player.diceRoot.active = true;
        this.player.timer.stop();
        this.player.timer.setTotalTime(this.stateMachine.currentServerState.gameOptions.movePiecesWaitTime);
        this.player.timer.run();
        this.player._pawnHandler.StartSelectAnimationsForValidMovePieces();
        for(let i=0; i<this.player.pawns.length; i++) {
            this.player.pawns[i].node.on(Node.EventType.TOUCH_START, this.OnPieceClick, this);
        }
    }
    updateState(deltaTime: number): void {  
    }
    exitState(): void {
        console.log("Exited Move Piece State local ", this.player._isLocal);
        //Reset All Pawns::
        this.player.diceRoot.active = false;
        this.player.timer.stop();
        this.player._pawnHandler.Reset(this.stateMachine.currentServerState);
        for(let i=0; i<this.player.pawns.length; i++) {
            this.player.pawns[i].node.off(Node.EventType.TOUCH_START, this.OnPieceClick, this);
        }
        // this.player.ReArrangeBasesAfterMoving();
    }

    getAllMovablePieces(): Piece[] {
        var allMovablePieces = [];
        var allOutComes = this.player.outComesUIManager.getOutComes();
        for(let i=0; i<this.player.pawns.length; i++) {
            var pieceValidOutComes = allOutComes.filter(outCome => this.canMovePiece(this.player.pawns[i].pathNumber, outCome));
            pieceValidOutComes = Array.from(new Set(pieceValidOutComes));
            if(pieceValidOutComes.length > 0) {
                allMovablePieces.push(this.player.pawns[i]);
            }
        }
        return allMovablePieces;
    }
    
    OnPieceClick(event) {
        if(!this.player._isLocal) return;
        if(this.player._pawnHandler.isAnimating()){
            this.player._moveOptioneInput.node.active = false;
            return;
        }
        this.player._moveOptioneInput.node.active = false;
        const piece: Piece = event.target.getComponent(Piece);
        var allOutComes = this.player.outComesUIManager.getOutComes();
        
        var pieceValidOutComes = allOutComes.filter(outCome => this.canMovePiece(piece.pathNumber, outCome));
        pieceValidOutComes = Array.from(new Set(pieceValidOutComes));
        console.log("Piece Valid OutComes: ", pieceValidOutComes);
        if(pieceValidOutComes.length == 0) return;
        if(pieceValidOutComes.length == 1) {
            this.sendMovePieceMessage(piece.pieceNumber, pieceValidOutComes[0]);
        }
        if(pieceValidOutComes.length >= 2){
            //show moves ui input
            this.player._moveOptioneInput.node.active = true;
            this.player._moveOptioneInput.outComeClickedCallback = this.outComeClickedCallback.bind(this);
            this.player._moveOptioneInput.setOutComeSprites(pieceValidOutComes);
            this.player._currentSelectedPieceNumber = piece.pieceNumber;
            this.player._moveOptioneInput.SetPosition(event.target.worldPosition.clone().add(this.moveOptionsOffset));
            return;
        }
    }

    onMovePiece() {
        const json = JSON.parse(this.stateMachine.currentServerState.inputPayload.inputValue);
        console.log("Move Piece From Server: ", json);
        const pieceIndex = json.pieceIndex;
        const rollValue = json.rollValue;
        this.player.timer.stop();
        this.player._moveOptioneInput.node.active = false;
        this.player._pawnHandler.movePiece(pieceIndex, rollValue, true);
    }

    canMovePiece(piecePathNumber: number, diceNumber: number): boolean {
        const gameOptions = this.player._gameOptions;
        if(piecePathNumber == 0 && diceNumber != 6) 
            return false;
        if(piecePathNumber + diceNumber > this.TotalPathCount(gameOptions))
            return false;
        return true;
    }
    TotalPathCount(gameOptions: SNLRoomSettings): number {
        return gameOptions.boardTilesCount;
    }
    

    sendMovePieceMessage(pieceIndex: number, rollValue: number) {
        GameManager.Instance.setInput("MovePiece");
        NetworkManager.Instance.CurrentRoom.send(GameMessageType.MOVE_PIECE, JSON.stringify({
            "pieceIndex": pieceIndex,
            "rollValue" : rollValue,
        }));
        console.log("Move Piece Message Sent : ", "piece ", pieceIndex, "with roll value ", rollValue);
    }

    outComeClickedCallback(value : number){
        // console.log("OUTCOME CLICKED: ", value);
        this.sendMovePieceMessage(this.player._currentSelectedPieceNumber, value);
        this.player._moveOptioneInput.node.active = false;
        this.player._currentSelectedPieceNumber = -1;
    }


    reset(){
        this.exitState();
    }
}