import { Color } from 'cc';
import { color } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { AudioManager } from './AudioManager';
const { ccclass, property } = _decorator;

@ccclass('Timer')
export class Timer extends Component {
    _totalTime : number = 0;
    _currentTime : number = 0;
    @property({ type: Sprite })
    timerSprite : Sprite = null;
    _canRunTimer : boolean = false;
    _isTimerWarning : boolean = false;
    _isLocal : boolean = false;
    protected update(dt: number): void {
        if(!this._canRunTimer) return;
        this._currentTime -= dt;
        if(this._currentTime < 0) {
            this._currentTime = 0;
        }
        this.timerSprite.fillRange = this.getTimeLeftPercentage();
        this.checkAndSetTimerWarning();
    }

    isTimeUp() : boolean {
        return this._currentTime <= 0;
    }

    setTotalTime(time : number) {
        this._totalTime = time;
        this._currentTime = time;
        this.timerSprite.fillRange = this.getTimeLeftPercentage();
    }

    setCurrentTime(time : number) {
        this._currentTime = time;
        this.timerSprite.fillRange = this.getTimeLeftPercentage();
    }

    run() {
        this._canRunTimer = true;
    }

    stop() {
        this._canRunTimer = false;
        this.reset();
    }

    reset() {
        this._isTimerWarning = false; 
        this.timerSprite.fillRange = 0;
        AudioManager.instance.stopSound("TimerWarning");
    }

    getTimeLeftPercentage() : number {
        return this._currentTime / this._totalTime;
    }

    checkAndSetTimerWarning() {
        if(!this._isLocal) return;
        if(this.getTimeLeftPercentage() < 0.2) {
            if(!this._isTimerWarning) {
                AudioManager.instance.playSound("TimerWarning");
                this._isTimerWarning = true;
            }
        }
    }

    setLocal(isLocal : boolean) {
        this._isLocal = isLocal;
    }
}


