import { instantiate } from 'cc';
import { Sprite } from 'cc';
import { Prefab } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { PlayerAssets } from './PlayerAssets';
import { Player } from './Player';
import { PBLogger } from '../pulse/pb-cocos-utils/PBLogger';
const { ccclass, property } = _decorator;

@ccclass('OutComesUIManager')
export class OutComesUIManager extends Component {
    @property({type: Prefab})
    outComesPrefab: Prefab = null;
    @property({type: Sprite})
    outcomesUiBgSprite: Sprite = null;
    currentOutComesUI : Node[] = [];
    currentOutComesValues : number[] = [];
    goldenDiceOutcomes : number = 0;
    player : Player = null;

    setGoldenDiceOutcomes(value: number){
        this.goldenDiceOutcomes = value;
    }

    addOutCome(value: number){
        // console.warn("Adding OutCome : ", value);
        let outComeUI = instantiate(this.outComesPrefab);
        outComeUI.setParent(this.node);
        outComeUI.getComponent(Sprite).spriteFrame = PlayerAssets.instance.getDiceOutComesStackSprite(value);
        if(this.currentOutComesUI.length < this.goldenDiceOutcomes && value == 6){
            outComeUI.getComponent(Sprite).spriteFrame = PlayerAssets.instance.getGoldenDiceOutComeSprite();
        }
        this.currentOutComesUI.push(outComeUI);
        this.currentOutComesValues.push(value);
        this.onOutComesChanged();
    }

    removeOutCome(value : number){
        let index = this.currentOutComesValues.indexOf(value);
        if(index != -1){
            this.currentOutComesUI[index].destroy();
            this.currentOutComesUI.splice(index, 1);
            this.currentOutComesValues.splice(index, 1);
        }
        this.onOutComesChanged();
    }

    clearAllOutComes(){
        // console.warn("Clearing All OutComes");
        this.currentOutComesUI.forEach(outComeUI => {
            outComeUI.destroy();
        });
        this.currentOutComesUI = [];
        this.currentOutComesValues = [];
        this.onOutComesChanged();
    }

    getOutComes(){
        return this.currentOutComesValues;
    }

    onOutComesChanged(){
        // if(!this.player) return;
        // PBLogger.info("parent object name : ", this.node.parent.name);
        if(this.currentOutComesValues.length <= 0){
            this.player.enableName(true);
        }
        else{
            this.player.enableName(false);
        }
    }

    reset(rollsList : number[]){
        this.clearAllOutComes();
        for(let i=0; i<rollsList.length; i++) {
            this.addOutCome(rollsList[i]);
        }
        // console.warn("all out comes ", this.getOutComes());
    }

    setPlayer(player : Player){
        // console.warn("Setting Player : ", player);
        this.player = player;
    }

    enableOutComesBg(isActive : boolean){
        this.outcomesUiBgSprite.enabled = isActive;
    }

}


