import { _decorator, Component, EventTarget } from 'cc';
import { NetworkManager, ConnectionStateChangeEvent, ServerRoomStateChangeEvent } from '../pulse/networking/NetworkManager';
import { ConnectionState, NetworkEvents } from '../pulse/types/NetworkTypes';
import { Board } from './Board';
import { AudioManager } from './AudioManager';
import { BasePlayerData } from '../pulse/schema/BasePlayerData';
import { SNLRoomState } from './schema/SNLRoomState';
import { RoomStatus } from '../pulse/types/RoomTypes';
import { MiloGameSDK } from '../milo-game-sdk/src/core/MiloGameSDK';
import { GameEvents } from '../milo-game-sdk/src/events';
import { GameLoadedPayload, GameStartedPayload, GameEndedPayload } from '../milo-game-sdk/src/types';
import { UIManager } from '../UIPack/Scripts/UIManager';

const { ccclass } = _decorator;

export const RoomStatusChangeEvent = new EventTarget();
@ccclass('GameManager')
export class GameManager extends Component {
    
    static _instance: GameManager | null = null;
    _roomState : RoomStatus = RoomStatus.NONE;
   
    _won: boolean = false;
    _handledGameOverCallbacks: boolean = false;
    
    _serverStateCopy : SNLRoomState = null;
    _missTurnsToKickOut : number = 3;
    
    public miloSDK: MiloGameSDK = new MiloGameSDK();
    private gameStartTime: number = 0;
    
    static get Instance() {
        return this._instance;
    }
    
    protected onLoad(): void {
        if(GameManager._instance != null) {
            this.node.destroy();
        }
        
        GameManager._instance = this;
        
        // Initialize Milo SDK
        this.miloSDK.init({ 
            gameId: 'snl-game',
            debug: true 
        });
        
        // Send game loaded event
        this.sendGameLoaded();
        
        ConnectionStateChangeEvent.on(NetworkEvents.CONNECTION_STATE_CHANGE, this.onConnectionStateChange, this);
        ServerRoomStateChangeEvent.on(NetworkEvents.SERVER_ROOM_STATE_CHANGE, this.onServerStateChange, this);
    }

    private sendGameLoaded(): void {
        const payload: GameLoadedPayload = {
            timestamp: Date.now()
        };
        this.miloSDK.gameLoaded(payload);
    }

    private sendGameStarted(): void {
        this.gameStartTime = Date.now();
        const payload: GameStartedPayload = {
            timestamp: this.gameStartTime
        };
        this.miloSDK.gameStarted(payload);
    }

    private sendGameEnded(): void {
        const endTime = Date.now();
        const duration = this.gameStartTime > 0 ? endTime - this.gameStartTime : 0;
        const payload: GameEndedPayload = {
            duration: duration,
            hasWinningScreen: true,
        };
        //give 2 seconds delay before sending game ended event
        setTimeout(() => {
            this.miloSDK.gameEnded(payload);
        }, 2000);
    }
   
    onConnectionStateChange(oldState: ConnectionState, newState: ConnectionState) {
        if(newState == ConnectionState.DISCONNECTED) {
            if (this._roomState != RoomStatus.END_GAME) {
                NetworkManager.Instance.reconnect();
            }
        }
        else if(newState == ConnectionState.KICKED_OUT){
            this.setState(RoomStatus.KICKED_OUT);
        }
        
    }
    setState(newState: RoomStatus) {
        this.OnRemoteGameStateChange(newState);
        this._roomState = newState;
        RoomStatusChangeEvent.emit(NetworkEvents.ROOM_STATUS_CHANGE, newState);
    }
    
    onServerStateChange(newServerState: SNLRoomState) {
        // console.log("got new serverstate from server " , newServerState);
        if(this._serverStateCopy == null) {
            this._serverStateCopy = new SNLRoomState();
            this._serverStateCopy.copyFrom(newServerState);
            this._missTurnsToKickOut = this._serverStateCopy.gameOptions.MissTurnsToKickOut;
        }
        if(newServerState.currentRoomStatus == RoomStatus.END_GAME) {
            console.log("Game Over");
            this._serverStateCopy = new SNLRoomState();
            this._serverStateCopy.copyFrom(newServerState);
            this.handleWon(newServerState);
        }
        this.setState(newServerState.currentRoomStatus);
    }
    handleWon(newServerState: SNLRoomState) {
        const state = new SNLRoomState();
        state.copyFrom(newServerState);
        const sessionId = NetworkManager.Instance.CurrentRoom.sessionId;
        const playerIndex = state.playersData.findIndex(data => data.sessionId == sessionId);
        if(playerIndex == -1) return;
        const pieces = state.playersData[playerIndex].pieces;
        const finalPosition = Board.Instance.TotalPathCount(state.gameOptions);
        for(let i=0; i<pieces.length; i++) {
            if(pieces[i] == finalPosition)
                this._won = true;
        }

        if(this._won){
            UIManager.instance.showConfetti();
            AudioManager.instance.playSound("Win");
        }
        else {
            AudioManager.instance.playSound("Lose");
        }
    }
    protected onDestroy(): void {
        ConnectionStateChangeEvent.off(NetworkEvents.CONNECTION_STATE_CHANGE, this.onConnectionStateChange, this);
        ServerRoomStateChangeEvent.off(NetworkEvents.SERVER_ROOM_STATE_CHANGE, this.onServerStateChange, this);
    }
    
    GetPlayerData(): any {
        console.log("Player Profiles : ", this._serverStateCopy.playersData);
        // console
        return this._serverStateCopy.playersData.find((player) => player.sessionId == NetworkManager.Instance.CurrentRoom.sessionId);
    }

    GetWinnerPlayerData(): BasePlayerData {
        let winnerPlayerId = -1;
        // console.log("SNLPlayersData : ", this._serverStateCopy.snlPlayersData);
        for(let i = 0; i < this._serverStateCopy.playersData.length; i++) {
            if(this._serverStateCopy.playersData[i].finishPosition >= 0) {
                winnerPlayerId = i;
                break;
            }
        }
        console.log("Winner Player Id : ", winnerPlayerId);
        if(winnerPlayerId == -1) return null;
        // console.log("player profiles : ", this._serverStateCopy.playerProfiles);
        return this._serverStateCopy.playersData.find((player) => player.playerNumber == winnerPlayerId);
    }
    protected OnRemoteGameStateChange(newState : RoomStatus): void {
        console.log("RemoteGameStateChange Message : " + newState);
        if(this._roomState == newState) return;
        if(this._roomState == RoomStatus.NONE && newState == RoomStatus.WAITING_FOR_PLAYERS) {
            
        }
        else if(newState == RoomStatus.COUNTDOWN) {
            
        }
        else if(newState == RoomStatus.GAME_SIMULATION) {
            // Game has started - send game started event
            this.sendGameStarted();
        }
        else if(newState == RoomStatus.END_GAME) {
            // Game has ended - send game ended event
            this.sendGameEnded();
            this.HandleGameOverCallbacks();
        }
    }
    HandleGameOverCallbacks() {
        console.log("HandleGameOverCallbacks");
        if (this._handledGameOverCallbacks) {
            console.log("Already handled game over callbacks");
            return;
        }
        
        this._handledGameOverCallbacks = true;
    }

    setInput(eventName : string) {
        
        // ServerReflectionHandler.setInput(input);
    }
    
}