import { UIManager } from "../../UIPack/Scripts/UIManager";
import { SNLGameState } from "../type/SNLGameState";
import { GameMessageType } from "../type/GameMessageType";  
import { SNLRoomState } from "../schema/SNLRoomState";
import { Player, PlayerState } from "../Player";
import { IdleState } from "./IdleState";
import { MovePieceState } from "./MovePieceState";
import { PlayerBaseState } from "./PlayerBaseState";
import { RollingState } from "./RollingState";
import { RoomStatus } from "db://assets/SNLGame/pulse/types/RoomTypes";

export class PlayerStateMachine {
    _currentState : PlayerBaseState;
    idleState : IdleState;
    rollingState : RollingState;
    movePieceState : MovePieceState;
    player : Player;
    currentServerState : SNLRoomState;
    currentTurnPlayerNumber : number = -1;

    constructor(player: Player) {
        this.player = player;
        this.idleState = new IdleState(this);
        this.rollingState = new RollingState(this);
        this.movePieceState = new MovePieceState(this);
        this._currentState = this.idleState;
    }

    updateState(deltaTime: number) {
        if(this._currentState == null) {
            return;
        }
        this._currentState.updateState(deltaTime);
    }

    changeState(state: PlayerBaseState) {
        this._currentState.exitState();
        this._currentState = state;
        this._currentState.enterState();
    }

    getPlayerNumber() : number {
        return this.player.playerNumber;
    }

    onServerStateChange(newServerState: SNLRoomState) {
        this.currentServerState = newServerState;
        //TODO: remove this if block after checking
        if(this.currentTurnPlayerNumber != newServerState.turnPlayerNumber) {
            this.currentTurnPlayerNumber = newServerState.turnPlayerNumber;
            if(this.player.isMyTurn(newServerState.turnPlayerNumber)){
                console.log("====================================");
                console.log("Player Turn : ", this.currentTurnPlayerNumber);
            }
        }
        if(this.currentServerState.currentRoomStatus !== RoomStatus.END_GAME) {
            this.checkAndUpdateBotScreen();
        }
        if(!this.player.isMyTurn(newServerState.turnPlayerNumber)) {
            if(this._currentState != this.idleState) {
                this.changeState(this.idleState);
            }
            return;
        };
        
        if(newServerState.inputPayload.inputType == GameMessageType.NONE) return;

        switch (newServerState.inputPayload.inputType) {
            case GameMessageType.SET_STATE:
                this.onSetState(newServerState);
                break;
            case GameMessageType.DICE_OUTCOME:
                if(this._currentState == this.rollingState){
                    this.rollingState.onDiceOutCome()
                }
                break;
            case GameMessageType.MOVE_PIECE:
                if(this._currentState == this.movePieceState){
                    this.movePieceState.onMovePiece();
                }
                break;
            default:
                break;
        }
    }

    onReconnection(newServerState: SNLRoomState) {
        this.currentServerState = newServerState;
        this.rollingState.exitState();
        this.movePieceState.exitState();
        if(!this.player.isMyTurn(newServerState.turnPlayerNumber)) {
            this.changeState(this.idleState);
            return;
        };
        let currentPlayerData = newServerState.playersData[this.player.playerNumber];
        this.player.outComesUIManager.reset(currentPlayerData.rollsList);
        if(newServerState.currentGameState == SNLGameState.ROLLING) {
            this._currentState = this.rollingState;
            this._currentState.enterState();
            this.player.timer.setCurrentTime(newServerState.timer);
        }
        else if(newServerState.currentGameState == SNLGameState.MOVING_PIECE) {
            this._currentState = this.movePieceState;
            this._currentState.enterState();
            this.player.timer.setCurrentTime(newServerState.timer);
        }
    }

    onSetState(newServerState: SNLRoomState) {
        let currentPlayerData = newServerState.playersData[this.player.playerNumber];
        this.player.outComesUIManager.reset(currentPlayerData.rollsList);
        if(newServerState.currentGameState == SNLGameState.ROLLING) {
            this.changeState(this.rollingState);
        }
        else if(newServerState.currentGameState == SNLGameState.MOVING_PIECE) {
            this.changeState(this.movePieceState);
        }
    }

    checkAndUpdateBotScreen() {
        if(!this.player._isLocal)return;
        if(this.currentServerState.playersData[this.player.playerNumber].isKickedOut) {
            UIManager.instance.OpenMenu("KickedOutScreen");
        }
        else if(this.currentServerState.playersData[this.player.playerNumber].isBot) {
            UIManager.instance.OpenMenu("BotMadeMoveScreen");
        }
    }
}