import { sys } from 'cc';
import { _decorator } from 'cc';
import { PBLogger } from './PBLogger';
const { ccclass } = _decorator;

@ccclass('PbSaveSystem')
export class PbSaveSystem {

    public static SaveData(key: string, data: unknown) {
        PBLogger.info("============ Saving data in pb save system =========");
        sys.localStorage.setItem(key, JSON.stringify(data));
    }

    public static LoadData(key: string) {
        return JSON.parse(sys.localStorage.getItem(key));
    }

    public static RemoveData(key: string) {
        sys.localStorage.removeItem(key);
    }

    public static ClearAllData() {
        sys.localStorage.clear();
    }
}


