import { _decorator, Component, Label, Node, Tween, tween, Vec3 } from 'cc';
import { Menu } from './Menu';
import { GameManager } from '../../scripts/GameManager';
import { Sprite } from 'cc';
import { ServerRoomStateChangeEvent } from '../../pulse/networking/NetworkManager';
import { NetworkEvents } from '../../pulse/types/NetworkTypes';
import { SNLRoomState } from '../../scripts/schema/SNLRoomState';
import { SNLGameHandler } from '../../scripts/SNLGameHandler';
import { UIManager } from './UIManager';
import { PlayerAvatar } from '../../pulse/pb-cocos-utils/PlayerAvatar';
import { PlayerAssets } from '../../scripts/PlayerAssets';
const { ccclass, property } = _decorator;

@ccclass('EndRoundScreen')
export class EndRoundScreen extends Menu {
    @property({ type: PlayerAvatar})
    playerAvatar: PlayerAvatar = null;
    @property({ type: Label})
    playerName: Label = null;
    _shouldShowThisScreen: boolean = false;
    _optionsInitialized : boolean = false;

    _rotatingGlow: Tween<Node> = null;
    protected onLoad(): void {
        super.onLoad();
        ServerRoomStateChangeEvent.on(NetworkEvents.SERVER_ROOM_STATE_CHANGE, this.ServerStateChanged, this);
    }
    protected onDestroy(): void {
        ServerRoomStateChangeEvent.off(NetworkEvents.SERVER_ROOM_STATE_CHANGE, this.ServerStateChanged, this);
    }
    ServerStateChanged(newServerState: SNLRoomState) {
        if(this._optionsInitialized) return;
        const state = new SNLRoomState();
        if(state.gameOptions) {
            this._shouldShowThisScreen = state.gameOptions.show_result_screen;
            this._optionsInitialized = true;
        }
        state.copyFrom(newServerState);
    }
    
    public Open(): void {
        if(!this._shouldShowThisScreen) {
            return;
        }
        super.Open();
        this.initialize();
    }
    
    public Close(): void {
        super.Close();
        this.stopRotateGlow();
    }

    initialize() {
        super.initialize();
        //close if show result screen is false
        if(!UIManager.instance.showResultScreen) {
            this.Close();
            return;
        }
        const isWinner = GameManager.Instance._won
        const winningPlayerNumber = GameManager.Instance._serverStateCopy.playersData.findIndex(player => player.finishPosition == 1);
        if (winningPlayerNumber == -1) {
            console.error("no winning player");
            return;
        }
        const winningPlayer = SNLGameHandler.Instance.getPlayer(winningPlayerNumber);
        this.playerAvatar.setAvatarFromSpriteFrame(winningPlayer.GetProfileSpriteFrame());
        this.playerAvatar.setOutlineColor(PlayerAssets.instance.getPawnColor(winningPlayerNumber));
        this.playerName.string = this.GetDisplayName(winningPlayer.playerName.string);
    }

    stopRotateGlow() {
        if(this._rotatingGlow) {
            this._rotatingGlow.stop();
            this._rotatingGlow = null;
        }
    }


    GetDisplayName(name: string) {
        if(name.length > 8) {
            return name.substring(0, 8) + "..";
        }
        return name;
    }
}

