import { _decorator, Component, Label, Node, Tween, tween, Vec3 } from 'cc';
import { Menu } from './Menu';
import { UIManager } from './UIManager';
import { GameManager } from '../../scripts/GameManager';
const { ccclass, property } = _decorator;

@ccclass('KickedOutScreen')
export class KickedOutScreen extends Menu {
    @property({type: Label})
    kickedOutLabel: Label = null;
    protected onLoad(): void {
        super.onLoad();
    }

    protected onDestroy(): void {
    }
    public Open(): void {
        super.Open();
        this.kickedOutLabel.string = "You missed " + GameManager.Instance._missTurnsToKickOut + " turns. kicked out of the game!";
    }
    public Close(): void {
        super.Close();
    } 
    public initHandTween() {

    }

    protected update(dt: number): void {
        
    }

    
}

