import { DefaultSNLGameOptions } from "../Config/GameConfig";
import { BaseRoomSettings } from "db://assets/SNLGame/pulse/schema/BaseRoomSettings";

export class SNLRoomSettings extends BaseRoomSettings {
    turnWaitTime: number = DefaultSNLGameOptions.turnWaitTime;
    boardTilesCount: number = DefaultSNLGameOptions.boardTilesCount;
    boardTilesPerRow: number = DefaultSNLGameOptions.boardTilesPerRow;
    piecesPerPlayer: number = DefaultSNLGameOptions.piecesPerPlayer;
    boardIndex: number = DefaultSNLGameOptions.boardIndex;
    diceRollWaitTime: number = DefaultSNLGameOptions.diceRollWaitTime;
    movePiecesWaitTime: number = DefaultSNLGameOptions.movePiecesWaitTime;
    lagDelayCompensationTime: number = DefaultSNLGameOptions.lagDelayCompensationTime;
    animationWaitTime: number = DefaultSNLGameOptions.animationWaitTime;
    rocketSteps: number = DefaultSNLGameOptions.rocketSteps;
    PowerupTypes: number[] = DefaultSNLGameOptions.PowerupTypes;
    show_mute_button: boolean = DefaultSNLGameOptions.show_mute_button;
    show_result_screen: boolean = DefaultSNLGameOptions.show_result_screen;
    show_faq: boolean = DefaultSNLGameOptions.show_faq;
    MissTurnsToKickOut: number = DefaultSNLGameOptions.MissTurnsToKickOut;
    maxReconnectionAttempts: number = DefaultSNLGameOptions.maxReconnectionAttempts;
    snakeAnimationTimePerTile: number = DefaultSNLGameOptions.snakeAnimationTimePerTile;
    ladderAnimationTimePerTile: number = DefaultSNLGameOptions.ladderAnimationTimePerTile;
    copyFrom(source: SNLRoomSettings): void {
      super.copyFrom(source);
      this.turnWaitTime = source.turnWaitTime;
      this.boardTilesCount = source.boardTilesCount;
      this.boardTilesPerRow = source.boardTilesPerRow;
      this.piecesPerPlayer = source.piecesPerPlayer;
      this.boardIndex = source.boardIndex;
      this.diceRollWaitTime = source.diceRollWaitTime;
      this.movePiecesWaitTime = source.movePiecesWaitTime;
      this.lagDelayCompensationTime = source.lagDelayCompensationTime;
      this.animationWaitTime = source.animationWaitTime;
      this.rocketSteps = source.rocketSteps;
      this.PowerupTypes = source.PowerupTypes;
      this.show_mute_button = source.show_mute_button;
      this.show_result_screen = source.show_result_screen;
      this.show_faq = source.show_faq;
      this.MissTurnsToKickOut = source.MissTurnsToKickOut;
      this.maxReconnectionAttempts = source.maxReconnectionAttempts;
      this.snakeAnimationTimePerTile = source.snakeAnimationTimePerTile;
      this.ladderAnimationTimePerTile = source.ladderAnimationTimePerTile;
    }
  }