import { SpriteFrame } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { Sprite } from 'cc';
import { PlayerAssets } from './PlayerAssets';
import { Tween } from 'cc';
import { tween } from 'cc';
import { Color } from 'cc';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { ParticleSystem2D } from 'cc';
import { Vec2 } from 'cc';
import { Vec3 } from 'cc';
import { Animation } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PowerUp')
export class PowerUp extends Component {
    @property({ type: Sprite })
    sprite : Sprite = null;
    pathNumber : number = 0;
    powerupType : number = 0;
    @property({ type: Animation })
    animation : Animation = null;

    start() {
        this.playIdleAnimation();
    }
    setPathNumber(pathNumber: number) {
        this.pathNumber = pathNumber;
    }

    setPowerUpType(powerupType: number) {
        this.powerupType = powerupType;
        this.sprite.spriteFrame = PlayerAssets.instance.getPowerUpSprite(powerupType);
    }

    blinkWithRedColor() {
        let blinkTween = tween(this.sprite)
        .to(0.2, { color: Color.RED },
            {
                onUpdate(target : Sprite, ratio) {
                    target.color = Color.RED;
                },
            }
        )
        .to(0.2, { color: Color.WHITE },
            {
                onUpdate(target : Sprite, ratio) {
                    target.color = Color.WHITE;
                },
            }
        )
        .union()
        .repeatForever()
        .start();
    }


    playIdleAnimation() {
        let randomStartTime = Math.random() * 2000;
        if(this.animation == null){
            this.animation = this.getComponent(Animation);
        }
        setTimeout(() => {
            this.animation?.play();
        }, randomStartTime);
    }

}


