import { 
    _decorator, 
    Animation,
    CCInteger, 
    Component,
    Node,
    Prefab,
    Quat,
    Sprite,
    SpriteFrame,
    Tween,
    Vec3,
    quat,
    tween
} from 'cc';

// Local imports
import { AudioManager } from './AudioManager';
import { Board } from './Board';
import { PlayerAssets } from './PlayerAssets';
import { CCBoolean } from 'cc';
import { PoolManager } from './pooling/PoolManager';
import { GameManager } from './GameManager';
import { UIManager } from '../UIPack/Scripts/UIManager';
import { Player } from './Player';
import { Label } from 'cc';
const { ccclass, property } = _decorator;

const NORMAL_SCALE: number = 0.9;
const PLAYING_SCALE: number = 0.9;
const TWO_STACKED_SCALE: number = 0.65;
const FOUR_STACKED_SCALE: number = 0.4;

@ccclass('Piece')
export class Piece extends Component {
    @property({ type: Sprite })
    pieceSprite: Sprite = null; 
    @property({ type: CCInteger})
    pieceNumber: number = 0;
    @property({ type: CCInteger})
    playerNumber: number = 0;
    @property({type: Sprite})
    labelSprite : Sprite = null;
    @property({type: Node})
    labelNode : Node = null;
    @property({type: Label})
    pieceLabel : Label = null;
    moveData = [];
    previousPathNumber: number = 0;
    pathNumber: number = 0;
    // pieceScaleOnBoard: number = 1;
    // stackedupScale: number = 0.75;
    @property({ type: Animation })
    propellerAnimation : Animation = null;
    @property({ type: Animation })
    selectAnimation : Animation = null;
    @property({ type: Prefab })
    moveRippleEffect : Prefab = null;
    @property({ type: CCBoolean })
    hasShield : boolean;
    @property({ type: Animation })
    shieldAnimation : Animation = null;
    @property({ type: Node })
    snakeBumpNode : Node = null;
    player : Player = null;
    

    Initialize(playerNumber: number, pieceNumber: number, player : Player) {
        this.labelNode.active = false;
        this.playerNumber = playerNumber;
        this.pieceNumber = pieceNumber;
        this.player = player;
        this.pieceSprite.spriteFrame = PlayerAssets.instance.getPawnSprite(playerNumber);
        this.labelSprite.color = PlayerAssets.instance.getPawnColor(playerNumber);
        this.pieceLabel.string = this.getPieceLabel(player.playerData.name);
        this.updatePieceScale();
    }
    
    move(rollValue: number, callBack : Function) : Tween<Node> {
        // this.log(`InitialPathNumber: ${this.pathNumber} RollValue: ${rollValue}`)
        var targetPathNumber = this.pathNumber + rollValue;
        if(this.isOnBase() && rollValue == 6) {
            this.labelNode.active = true;
            targetPathNumber = 1;
        }

        this.setPathNumber(targetPathNumber);
        this.updateParentNode();
        let animation: Tween<Node> = this.getMoveAnimation(targetPathNumber, callBack);
        return animation;
    }

    moveToTargetPathNumber(targetPathNumber: number, callBack : Function, durationPerTile : number = 0.12) : Tween<Node> {
        this.setPathNumber(targetPathNumber);
        this.updateParentNode();
        let animation: Tween<Node> = this.getMoveAnimation(targetPathNumber, callBack, durationPerTile);
        return animation;
    }
    
    getMoveAnimation(targetPathNumber: number, callBack : Function, durationPerTile : number = 0.12): Tween<Node> {
        const initialPosition = this.node.worldPosition.clone();
        const pathPositions = Board.Instance.getPathPositions(this.previousPathNumber, targetPathNumber, this.playerNumber, this.pieceNumber);
        const tile: Node = Board.Instance.getPathTile(targetPathNumber, this.playerNumber, this.pieceNumber);
        let animation : Tween<Node> = tween(this.node)
            .call(() => {
                this.node.setWorldPosition(initialPosition);
            });
        for(let i = 1; i < pathPositions.length; i++) {
            animation.then(this.jumpToNextTileAnimation(this.node, pathPositions[i-1], pathPositions[i], durationPerTile));
        }
        animation.call(() => {
            if(callBack != null){
                console.log("[Piece] Calling on pieces  landed CallBack");
                callBack();
            }
        });
        return animation;
    }

    jumpToNextTileAnimation(sourceNode: Node, startPosition: Vec3, endPosition: Vec3, durationPerTile : number = 0.12) : Tween<Node>{
        let midPosition : Vec3 = startPosition.clone().add(endPosition).multiplyScalar(0.5);
        let startScale = sourceNode.scale.clone();
        let animation = tween(sourceNode)
        .call(() => {
            AudioManager.instance.playSound("Move");
            sourceNode.setWorldPosition(startPosition);
        });
        animation.call(() => {
            this.playMoveRippleEffect(startPosition.clone());
        });
        animation.to(durationPerTile, { worldPosition: midPosition, scale : startScale.clone().multiplyScalar(1.25)});
        animation.to(durationPerTile, { worldPosition: endPosition, scale : startScale});
        return animation;
    }

    setPathNumber(targetPathNumber: number) {
        this.previousPathNumber = this.pathNumber;
        this.pathNumber = targetPathNumber;
    }

    updateParentNode(targetPathNumber: number = -1) {
        if(targetPathNumber == -1) {
            targetPathNumber = this.pathNumber;
        }
        const tile: Node = Board.Instance.getPathTile(targetPathNumber, this.playerNumber, this.pieceNumber);
        this.node.setParent(tile, true);
    }

    //TODO : fix offset while two pieces are on same tile
    updatePiecePosition(targetPathNumber: number = -1) {
        if(targetPathNumber == -1) {
            targetPathNumber = this.pathNumber;
        }
        this.node.setPosition(Vec3.ZERO.clone());
        const tile: Node = Board.Instance.getPathTile(targetPathNumber, this.playerNumber, this.pieceNumber);
        if(Board.Instance.piecesOnTile(targetPathNumber, this.playerNumber) > 1) {
            this.node.setWorldPosition(this.node.worldPosition.add(this.getOffsetPosition()));
        }
        this.updatePieceScale();
    }

    getOffsetPosition() : Vec3 {
        if(this.node.parent.children.length == 1 || this.pathNumber == 0){
            return Vec3.ZERO.clone();
        }
        let childIndex = this.myIndexInTile(this.node.parent);
        let totalPieces = this.node.parent.children.length;
        let spacing = totalPieces > 3 ? 7.5 : 15; // Space between pieces
        
        let offset = (childIndex - (totalPieces - 1) / 2) * spacing;
        
        return new Vec3(offset, 0, 0);
    }

    updatePieceScale() {
        const stackedPawns = Board.Instance.getPawnsCountOnTile(this.pathNumber);
        const baseScale = Vec3.ONE.clone();
        let targetScale: number = NORMAL_SCALE;
        if (this.isOnBase()) {
            targetScale = NORMAL_SCALE;
        }
        else if (stackedPawns > 4) {
            targetScale = FOUR_STACKED_SCALE;
        }
        else if (stackedPawns > 2) {
            targetScale = TWO_STACKED_SCALE;
        }
        else {
            targetScale = PLAYING_SCALE;
        }
        this.node.scale = baseScale.multiplyScalar(targetScale);
    }

    myIndexInTile(parentTile : Node) : number {
        for(let i = 0; i < parentTile.children.length; i++) {
            if(parentTile.children[i] == this.node) {
                return i;
            }
        }
        return -1;
    }

    animatePieceToSize(finalScale : number) {
        tween(this.node)
            .to(0.5, { scale: Vec3.ONE.clone().multiplyScalar(finalScale) })
            .start();
    }

    isOnBase() : boolean{
        return this.pathNumber == 0;
    }

    playPropellerAnimation(direction : Vec3) {
        this.propellerAnimation.node.active = true;
        this.propellerAnimation.node.setPosition(Vec3.ZERO.clone());
        
        this.setNodeUpDirection(this.propellerAnimation.node, direction);
        this.propellerAnimation.node.setWorldPosition(this.node.worldPosition.clone().subtract(this.propellerAnimation.node.up.multiplyScalar(50)));
        this.propellerAnimation.play();
        AudioManager.instance.playSound("Propeller");
    }

    setNodeUpDirection(node: Node, direction: Vec3) {
        const up = direction.clone().normalize();
        const forward = new Vec3(0, 0, 1);
        const right = new Vec3(0, 0, 0);
        Vec3.cross(right, up, forward).normalize();
        const rotation = quat();
        Quat.fromAxes(rotation, right, up, forward);

        node.setWorldRotation(rotation);
    }
    stopPropellerAnimation() {
        this.propellerAnimation.stop();
        this.propellerAnimation.node.active = false;
        AudioManager.instance.stopSound("Propeller");
    }


    playSelectAnimation(){
        this.selectAnimation.node.active = true;
        let sprite : Sprite = this.selectAnimation.node.getComponent(Sprite);
        if(sprite != null){
            sprite.color = PlayerAssets.instance.getPawnColor(this.playerNumber);
        }
        this.selectAnimation.play();
    }

    stopSelectAnimation(){
        this.selectAnimation.stop();
        this.selectAnimation.node.active = false;
    }

    playMoveRippleEffect(worldPosition : Vec3){
        let moveRippleEffect = PoolManager.instance.getObjectFromPool(this.moveRippleEffect);
        moveRippleEffect.setParent(Board.Instance.node, true);
        moveRippleEffect.setWorldPosition(worldPosition);
        let sprite : Sprite = moveRippleEffect.getComponent(Sprite);
        if(sprite != null){
            sprite.color = PlayerAssets.instance.getPawnColor(this.playerNumber);
        }
        let animation : Animation = moveRippleEffect.getComponent(Animation);
        animation.play();
    }

    setShieldActive(active : boolean) {
        if(this.hasShield == active) {
            return;
        }
        if(active) {
            AudioManager.instance.playSound("Power");
        }
        this.hasShield = active;
        let sprite : Sprite = this.shieldAnimation.node.getComponent(Sprite);
        if(sprite != null){
            sprite.color = PlayerAssets.instance.getPawnColor(this.playerNumber);
        }
        if(active) {
            console.log("[Piece] Playing Shield Animation");
            this.shieldAnimation.node.active = true;
            //TODO: Add shield animation
            this.shieldAnimation.play();
        }
        else {
            console.log("[Piece] Stopping Shield Animation");
            this.shieldAnimation.stop();
            this.shieldAnimation.node.active = false;
        }
    }

    reset(PathNumber: number) {
        this.setPathNumber(PathNumber);
        this.updateParentNode();
        this.updatePiecePosition();
        this.stopPropellerAnimation();
    }

    scaleDownPiece(duration : number = 0.1) : Tween<Node> {
        return tween(this.pieceSprite.node)
            .call(() => {
                this.pieceSprite.node.active = true;
            })
            .to(duration, { scale: this.pieceSprite.node.scale.clone().multiplyScalar(0) })
            .call(() => {
                this.pieceSprite.node.active = false;
            });
    }

    scaleDownPieceWithWiggle(duration : number = 0.25) : Tween<Node> {
        return tween(this.pieceSprite.node)
            .call(() => {
                this.pieceSprite.node.active = true;
            })
            // Add a wiggle effect as the piece is being "swallowed"
            .to(duration * 0.3, { scale: this.pieceSprite.node.scale.clone().multiplyScalar(0.8), 
                                  angle: 15 })
            .to(duration * 0.3, { scale: this.pieceSprite.node.scale.clone().multiplyScalar(0.5), 
                                  angle: -15 })
            .to(duration * 0.4, { scale: this.pieceSprite.node.scale.clone().multiplyScalar(0), 
                                  angle: 0 })
            .call(() => {
                this.pieceSprite.node.active = false;
            });
    }

    scaleDownBump(duration : number = 0.1) : Tween<Node> {
        return tween(this.snakeBumpNode)
            .call(() => {
                this.snakeBumpNode.active = true;
                this.snakeBumpNode.scale = Vec3.ONE.clone();
            })
            .to(duration, { scale: Vec3.ONE.clone().multiplyScalar(0.5)})
            .call(() => {
                this.snakeBumpNode.active = false;
            });
    }

    animateSnakeBulge(duration : number = 0.1) : Tween<Node> {
        return tween(this.snakeBumpNode)
            .delay(0.1)
            .call(() => {
                this.snakeBumpNode.active = true;
                // Start with a larger bulge to show "swallowing"
                this.snakeBumpNode.scale = Vec3.ONE.clone().multiplyScalar(1.2);
            })
            // Create a pulsing effect while moving
            .to(duration, { scale: Vec3.ONE.clone().multiplyScalar(0.5) })
            .call(() => {
                this.snakeBumpNode.active = false;
            });
    }

    scaleUpPiece(duration : number = 0.1) : Tween<Node> {
        return tween(this.pieceSprite.node)
            .call(() => {
                this.pieceSprite.node.active = true;
            })
            .to(duration, { scale: Vec3.ONE.clone().multiplyScalar(NORMAL_SCALE)});
    }

    scaleUpPieceWithBounce(duration : number = 0.25) : Tween<Node> {
        return tween(this.pieceSprite.node)
            .call(() => {
                this.pieceSprite.node.active = true;
                this.pieceSprite.node.scale = Vec3.ONE.clone().multiplyScalar(0);
            })
            // Add a bounce effect as the piece reappears
            .to(duration * 0.4, { scale: Vec3.ONE.clone().multiplyScalar(NORMAL_SCALE * 1.3) })
            .to(duration * 0.3, { scale: Vec3.ONE.clone().multiplyScalar(NORMAL_SCALE * 0.8) })
            .to(duration * 0.3, { scale: Vec3.ONE.clone().multiplyScalar(NORMAL_SCALE) });
    }

    onKilled(){
        AudioManager.instance.playSound("Kill");
        if(this.player._isLocal) {
            UIManager.instance.playFlashEffect();
            this.labelNode.active = false;
        }
    }

    getPieceLabel(username:string): string {
        const firstName = username.split(" ")[0];
        const trimmed = firstName.trim();
      
        if (trimmed.length >= 4) {
          return (trimmed[0] + trimmed[2]).toUpperCase(); // first + third
        } else if (trimmed.length >= 2) {
          return trimmed.substring(0, 2).toUpperCase();   // first two letters
        } else if (trimmed.length === 1) {
          return trimmed.toUpperCase();                   // single letter
        } else {
          return "??";                                     // fallback for empty
        }
      }

}

