import { SNLRoomState } from "./schema/SNLRoomState";
import { SNLRoomSettings } from "./schema/SNLRoomSettings";
import { EventTarget } from "cc";
import { SNLGameState } from "./type/SNLGameState";
import { RoomStatus } from "../pulse/types/RoomTypes";
import { AnimationTag } from "./type/AnimationTag";
import { Component, _decorator, Node, Vec3 } from "cc";
import { Player } from "./Player";
import { Board } from "./Board";
import { Tween } from "cc";
import { game } from "cc";
import { Game } from "cc";
import { GameManager } from "./GameManager";
import { AudioManager } from "./AudioManager";
import { NetworkManager, ConnectionStateChangeEvent, ServerRoomStateChangeEvent } from '../pulse/networking/NetworkManager';
import { ConnectionState, NetworkEvents } from '../pulse/types/NetworkTypes';
import { PBLogger } from "../pulse/pb-cocos-utils/PBLogger";
import { PlayerService } from "./PlayerService";
const { ccclass, property } = _decorator;

export const ClientStateChange: EventTarget = new EventTarget();

@ccclass('SNLGameHandler')
export class SNLGameHandler extends Component {
    
    _clientState: SNLRoomState;
    _serverState: SNLRoomState;
    @property({type: Node})
    playersParent: Node = null;
    allPlayers : Player[] = [];
    @property({type: Board})
    board : Board = null;
    myPlayerNumber: number = 0;
    hasReconnected: boolean = false;
    @property({type: Player})
    localPlayer: Player = null;
    static _instance : SNLGameHandler;
    static get Instance() {
        return this._instance;
    }
    onLoad() {
        this.LoadPlayers();
        ConnectionStateChangeEvent.on(NetworkEvents.CONNECTION_STATE_CHANGE, this.onConnectionStateChange, this);
        ServerRoomStateChangeEvent.on(NetworkEvents.SERVER_ROOM_STATE_CHANGE, this.OnServerStateChange, this);
        SNLGameHandler._instance = this;
        game.on(Game.EVENT_HIDE, this.onGameHide, this); 
        game.on(Game.EVENT_SHOW, this.onGameShow, this);
        game.on(Game.EVENT_RESTART, this.onGameRestart, this);
        game.on(Game.EVENT_CLOSE, this.onGameClose, this);
        window.addEventListener("beforeunload", this.onGameClose);
    }

    private LoadPlayers() {
        this.allPlayers = this.playersParent.children.map(child => child.getComponent(Player));
    }

    public Init(gameOptions: SNLRoomSettings) {
    }
    onGameHide() {
        console.log("====== Game Hide ======");
        Tween.stopAll();
    }
    onGameShow() {
        console.log("====== Game Show ======");
        Tween.stopAllByTag(AnimationTag.DICE_ROLL);
        if(GameManager.Instance._roomState != RoomStatus.GAME_SIMULATION)return;
        // Use this.board instead of Board.Instance
        // this.board.reset(this._serverState);
        // this.sendReconnectionStateToAllPlayers(this._serverState);
    }

    onGameRestart() {
        console.log("====== Game Restart ======");
    }

    onGameClose() {
        // leave room
        console.log("====== Game Close ======");
        // NetworkManager.Instance.LeaveRoom(); 
        PlayerService.instance.clearPlayers();
    }

    onGameDisconnect() {
        console.log("====== Game Disconnect ======");
        AudioManager.instance.stopAllSounds();
    }

    onGameReconnect() {
        console.log("====== Game Reconnect ======");
    }
    public OnServerStateChange(newServerState): void {
        console.log("============ OnServerStateChange =========");
        if(this._clientState == null) {
            this._clientState = new SNLRoomState();
            this._clientState.copyFrom(newServerState);
            this._serverState = new SNLRoomState();
            this._serverState.copyFrom(newServerState);
            this.board.Initialize(newServerState);
            this.InitializePlayers(newServerState);
            if(this._serverState.currentGameState != SNLGameState.NONE){
                console.log("========= joined game in between =========");
                // Use this.board instead of Board.Instance
                this.board.reset(this._serverState);
                this.sendReconnectionStateToAllPlayers(this._serverState);
            }
            return
        }
        
        this._serverState = new SNLRoomState();
        this._serverState.copyFrom(newServerState);
        if(newServerState.currentRoomStatus == RoomStatus.END_GAME) {
            console.log("========= Game Over State Change ");
            // Use this.board instead of Board.Instance
            this.board.reset(this._serverState);
            // this.sendReconnectionStateToAllPlayers(this._serverState);
            return;
        }
        if(this.hasReconnected) {
            console.log("========= sending Reconnection State Change ");
            // Use this.board instead of Board.Instance
            this.board.reset(this._serverState);
            this.sendReconnectionStateToAllPlayers(this._serverState);
            this.hasReconnected = false;
            return;
        }
        this.sendStateToAllPlayers(this._serverState);
        // this.log("Server State Change ", newServerState);
    }

    onConnectionStateChange(oldState : ConnectionState, newState : ConnectionState) {
        if(newState == ConnectionState.DISCONNECTED) {
            this.onGameDisconnect();
        }
        else if(oldState == ConnectionState.RECONNECTING && newState == ConnectionState.JOINED_ROOM) {
            // 
            this.hasReconnected = true;
            this.onGameReconnect();
        }
    }

    sendStateToAllPlayers(newServerState: SNLRoomState) {
        for(let i = 0; i < this.allPlayers.length; i++) {
            this.allPlayers[i].onServerStateChange(newServerState);
        }
    }

    sendReconnectionStateToAllPlayers(newServerState: SNLRoomState) {
        console.log("=========Reconnection State Sent to All Players");
        for(let i = 0; i < this.allPlayers.length; i++) {
            if(!this.allPlayers[i].node.active)continue;
            this.allPlayers[i].onReconnection(newServerState);
        }
    }

    
    public InitializePlayers(newServerState: SNLRoomState) {
        this.assignMyPlayerNumber(newServerState);
        // Register player number with the PlayerService
        PlayerService.instance.setMyPlayerNumber(this.myPlayerNumber);
        PBLogger.info("My Player Number : ", this.myPlayerNumber);
        for(let i = 0; i < newServerState.playersData.length; i++) {
            if(i == this.myPlayerNumber) {
                this.localPlayer.node.active = true;
                this.localPlayer.initialize(newServerState, this.board, this.myPlayerNumber);
                this.allPlayers.push(this.localPlayer);
                // Register the local player with PlayerService
                PlayerService.instance.registerPlayer(this.localPlayer, true);
                PBLogger.info("Local Player node name : ", this.localPlayer.node.name);
            }
            else {
                console.log("Initializing Player : ", i);
                console.log("current player : ", this.allPlayers[i]);
                var currentPlayer = this.allPlayers[i];
                currentPlayer.node.active = true;
                currentPlayer.initialize(newServerState, this.board, i);
                // Register the player with PlayerService
                PlayerService.instance.registerPlayer(currentPlayer);
                PBLogger.info("Current Player node name : ", currentPlayer.node.name);
            }
        }
    }

    assignMyPlayerNumber(newServerState: SNLRoomState) {
        for(let i = 0; i < newServerState.playersData.length; i++) {
            if(newServerState.playersData[i].sessionId == NetworkManager.Instance.CurrentRoom.sessionId) {
                this.myPlayerNumber = i;
                break;
            }
        }
    }

    /*rotatePlayerNumbersInProfileUI(count: number) {
        // Normalize count to prevent unnecessary rotations
        count = count % this.players.length;
        
        // No need to rotate if count is 0
        if (count === 0) return;
        // Store original player numbers
        const originalNumbers = this.players.map(player => player.playerNumber);
        // Assign new rotated numbers
        for (let i = 0; i < this.players.length; i++) {
            // Calculate new index with rotation, ensuring positive wrap-around
            const newIndex = (i - count + this.players.length) % this.players.length;
            this.players[i].playerNumber = originalNumbers[newIndex];
        }
    }*/
    getPlayer(playerNumber: number): Player {
        // Use PlayerService for consistency
        return PlayerService.instance.getPlayer(playerNumber);
    }
    getNumberOfProfileRotation(playerNumber: number): number {
        if(playerNumber == 0) {
            return 0;
        }
        else if(playerNumber == 1) {
            return 2;
        }
        else if(playerNumber == 2) {
            return 1;
        }
        else if(playerNumber == 3) {
            return 3;
        }
    }


    allPlayersFinished(): boolean {
        let finishedPlayersCount = 0;
        let totalPlayersCount = 0;
        for(let i = 0; i < this.allPlayers.length; i++) {
            //some players are disabled, not removed from array::
            if(!this.allPlayers[i].node.active) continue;
            totalPlayersCount++;
            if(this.allPlayers[i].isFinished()) {
                finishedPlayersCount++;
            }
        }
        console.log("Finished Players Count ", finishedPlayersCount);
        console.log("Total Players ", this.allPlayers.length);
        return finishedPlayersCount == totalPlayersCount - 1;
    }

    anyPlayerFinished(): boolean {
        for(let i = 0; i < this.allPlayers.length; i++) {
            if(this.allPlayers[i].isFinished()) {
                return true;
            }
        }
        return false;
    }

    /**
     * Gets the player with the most deaths
     * @returns The player with the highest deathsCount, or null if no players exist
     */
    getMostDeathsPlayer(): Player {
        let maxDeaths = -1;
        let mostDeathsPlayerNumber = -1;

        // Find the player with the highest deathsCount
        for (let i = 0; i < this._serverState.playersData.length; i++) {
            const playerData = this._serverState.playersData[i];
            if (playerData.deathsCount > maxDeaths) {
                maxDeaths = playerData.deathsCount;
                mostDeathsPlayerNumber = playerData.playerNumber;
            }
        }
        // Return the player object using PlayerService
        return this.getPlayer(mostDeathsPlayerNumber);
    }

    /**
     * Gets the player with the most ladder climbs
     * @returns The player with the highest ladderClimbCount, or null if no players exist
     */
    getMostLadderClimbsPlayer(): Player {

        let maxLadderClimbs = -1;
        let mostLadderClimbsPlayerNumber = -1;

        // Find the player with the highest ladderClimbCount
        for (let i = 0; i < this._serverState.playersData.length; i++) {
            const playerData = this._serverState.playersData[i];
            if (playerData.ladderClimbCount > maxLadderClimbs) {
                maxLadderClimbs = playerData.ladderClimbCount;
                mostLadderClimbsPlayerNumber = playerData.playerNumber;
            }
        }


        // Return the player object using PlayerService
        return this.getPlayer(mostLadderClimbsPlayerNumber);
    }

    isLocalUserHost(): boolean {
        const hostId = GameManager.Instance.miloSDK.getHostId();
        const localUser = this.localPlayer.playerData.userId;
        return hostId == localUser;
    }

    getHostPlayer(): Player | null {
        const hostId = GameManager.Instance.miloSDK.getHostId();
        if(!hostId) return null;
        const activePlayers = this.allPlayers.filter(player => player.playerData);
        const hostPlayer = activePlayers.find(player => player.playerData.userId == hostId);
        return hostPlayer;
    }

    onDestroy() {
        ConnectionStateChangeEvent.off(NetworkEvents.CONNECTION_STATE_CHANGE, this.onConnectionStateChange, this);
        ServerRoomStateChangeEvent.off(NetworkEvents.SERVER_ROOM_STATE_CHANGE, this.OnServerStateChange, this);
        game.off(Game.EVENT_HIDE, this.onGameHide, this);
        game.off(Game.EVENT_SHOW, this.onGameShow, this);
        game.off(Game.EVENT_RESTART, this.onGameRestart, this);
        game.off(Game.EVENT_CLOSE, this.onGameClose, this);
        window.removeEventListener("beforeunload", this.onGameClose);
        PlayerService.instance.clearPlayers();
        SNLGameHandler._instance = null;
    }

}