import { _decorator, Component, Label, Node, Quat, Tween, tween, Vec3 } from 'cc';
import { Menu } from './Menu';
const { ccclass, property } = _decorator;

@ccclass('TutorialScreen')
export class TutorialScreen extends Menu {
    @property({type: Node})
    handNode : Node = null;
    _showHandGesture: boolean = true;
    private _handAnimationTween: Tween<Node> = null;
    
    protected onLoad(): void {
        super.onLoad();
        // OnServerMessageEvent.on(GameOptionsBroadcastType, this.ServerGameOptionsReceived, this);
    }
    protected start(): void {
        // GameEventsEmitter.on(GameEvents.SelectedLane, this.onSelectedLane, this);
    }
    protected onDestroy(): void {
        // GameEventsEmitter.off(GameEvents.SelectedLane, this.onSelectedLane, this);
        // OnServerMessageEvent.off(GameOptionsBroadcastType, this.ServerGameOptionsReceived, this);
    }
    ServerGameOptionsReceived(gameOptions: any) {
        this._showHandGesture = gameOptions.show_hand_gesture;
    }
    onSelectedLane() {
        console.log("Selected lane so closing tutorial");
        this.Close();
    }
    public Open(): void {
        if(!this._showHandGesture) {
            return;
        }
        super.Open();
        this.initialization();
    }
    public Close(): void {
        super.Close();
        if(this._handAnimationTween) {
            this._handAnimationTween.stop();
        }
    } 
    handClickAnimation() {
        let  clockRotationQ : Quat = new Quat();
        Quat.fromEuler(clockRotationQ, 0, 0, 15);
        let  antiClockRotationQ : Quat = new Quat();
        Quat.fromEuler(clockRotationQ, 0, 0, 15);
        this._handAnimationTween = tween(this.handNode)
        .call(()=>{
            this.handNode.eulerAngles = Vec3.ZERO.clone();
        })
        .delay(.2)
        .to(.3, {rotation: clockRotationQ}, {easing: 'backOut'})
        .delay(.7)
        .union()
        .repeatForever()
        this._handAnimationTween.start();
    }

    
    initialization() {
        this.handClickAnimation();
        // this._handTween.start();
    }
}

