import { _decorator, Component, sys } from 'cc';
import { PBLogger } from '../pb-cocos-utils/PBLogger';
const { ccclass, property } = _decorator;

@ccclass('NetworkConfig')
export class NetworkConfig extends Component {
    @property({ tooltip: "Server host address" })
    serverHost: string = "localhost";

    @property({ tooltip: "Server port number" })
    serverPort: number = 2567;

    @property({ tooltip: "Use SSL for connections" })
    useSSL: boolean = false;

    @property({ tooltip: "Player identifier" })
    userId: string = "";

    @property({ tooltip: "Room identifier" })
    roomId: string = "testroom1";

    @property({ tooltip: "Name of the game" })
    gameName: string = "Pictionary";

    @property({ tooltip: "Whether the data was assigned from query parameters" })
    assignedFromQueryParams: boolean = false;

    /**
     * Parse URL query parameters to override network configuration
     */
    public parseQueryParams(): void {
        PBLogger.debug("parseQueryParams");
        if (!sys.isBrowser) {
            return; // Skip if not running in browser
        }

        const queryParams = new URLSearchParams(window.location.search);
        PBLogger.debug("queryParams", queryParams);
        this.updateConfigFromQueryParam(queryParams, 'userId', (value) => {
            PBLogger.debug("userId", value);
            this.userId = value;
            this.assignedFromQueryParams = true;
        });
        
        this.updateConfigFromQueryParam(queryParams, 'roomId', (value) => {
            PBLogger.debug("roomId", value);
            this.roomId = value;
        });
        
        this.updateConfigFromQueryParam(queryParams, 'apiUrl', (value) => {
            this.serverHost = value;
        });
        
        this.updateConfigFromQueryParam(queryParams, 'apiPort', (value) => {
            const parsedPort = parseInt(value);
            if (!isNaN(parsedPort)) {
                this.serverPort = parsedPort;
            }
        });
        
        this.updateConfigFromQueryParam(queryParams, 'useSSL', (value) => {
            this.useSSL = value === "true";
        });
    }

    /**
     * Helper method to update config from query parameters
     */
    private updateConfigFromQueryParam(
        params: URLSearchParams, 
        paramName: string, 
        updateFn: (value: string) => void
    ): void {
        const value = params.get(paramName);
        if (value !== null && value !== undefined) {
            updateFn(value);
        }
    }
}