import { _decorator, CCBoolean, CCFloat, Component, Node } from 'cc';
import { PoolManager } from './PoolManager';
const { ccclass, property } = _decorator;

@ccclass('AutoReturnToPool')
export class AutoReturnToPool extends Component {
    @property({type: CCFloat})
    timeToReturnToPool : number = 3;
    @property({type: CCBoolean})
    shouldDestroy : boolean;

    protected onEnable(): void {
        this.scheduleOnce(this.returnToPool, this.timeToReturnToPool);
    }
    start() {

    }

    update(deltaTime: number) {
        
    }

    returnToPool(){
        PoolManager.instance.returnObjectToPool(this.node);
        if(this.shouldDestroy)this.node.destroy();
    }
}


