/**
 * Enum for different player types
 */
export enum PlayerType {
    HUMAN,
    BOT,
    SPECTATOR
}
/**
 * Interface for basic player data that can be extended by specific games
 */
export interface IBasePlayerData {
    id: string;
    name: string;
    avatar?: string;
    playerNumber: number;
    type: PlayerType;
    sessionId: string;
    isOnline: boolean;
    isConnected: boolean;
    isReady: boolean;
    score: number;
}

/**
 * Interface for player connection events
 */
export interface IPlayerConnectionEvent {
    playerId: string;
    isConnected: boolean;
    timestamp: number;
} 