import { PlayerType } from "../types/PlayerTypes";

export class BasePlayerData {
    userId: string;
    name: string;
    avatar: string;
    sessionId: string;
    playerNumber: number;
    type: PlayerType;
    isOnline: boolean;
    isConnected: boolean;
    isReplacedByBot: boolean;
    isKickedOut: boolean;
    isReady: boolean;
    score: number;

    copyFrom(other: BasePlayerData): void {
        this.userId = other.userId;
        this.name = other.name;
        this.avatar = other.avatar;
        this.sessionId = other.sessionId;
        this.playerNumber = other.playerNumber;
        this.type = other.type;
        this.isOnline = other.isOnline;
        this.isConnected = other.isConnected;
        this.isReplacedByBot = other.isReplacedByBot;
        this.isKickedOut = other.isKickedOut;
        this.isReady = other.isReady;
        this.score = other.score;
    }
}