import { _decorator, Component, Label, Node, Tween, tween, Vec3 } from 'cc';
import { Menu } from './Menu';
import { UIManager } from './UIManager';
import { NetworkManager } from '../../pulse/networking/NetworkManager';
import { GameMessageType } from '../../scripts/type/GameMessageType';
const { ccclass, property } = _decorator;

@ccclass('BotMadeMoveScreen')
export class BotMadeMoveScreen extends Menu {
    _timer = 0;
    protected onLoad(): void {
        super.onLoad();
        this.content.on(Node.EventType.TOUCH_START, this.OnClick, this);
    }

    protected onDestroy(): void {
    }
    public Open(): void {
        super.Open();
        this._timer = 0;
    }
    public Close(): void {
        super.Close();
    } 
    public initHandTween() {

    }

    initialization() {
        this.initHandTween();
    }

    OnClick() {
        NetworkManager.Instance.sendMessage(GameMessageType.PLAYER_CAMEBACK, {});
        UIManager.instance.CloseMenu(this.menuName);
    }
}

