import { WebViewTransport } from '../transport';
import { GameEvents, InternalGameSdkEvents } from '../events';
import { GameMessage, GameStartedPayload, GameEndedPayload, ScorePayload, VibratePresetPayload, VibratePatternPayload, VibratePreset, GameLoadedPayload, PlayAgainPayload, ChangeGamePayload} from '../types';
import { defaultConfig } from '../config/defaultConfig';
import { EventTarget } from 'cc';

export interface InitOptions {
  gameId?: string;
  debug?: boolean;
  endpoint?: string;
  retryAttempts?: number;
  retryDelayMs?: number;
}

export const HostChangeEvent = new EventTarget();

export class MiloGameSDK {
  private transport = new WebViewTransport();
  private config = { ...defaultConfig };
  private hostId: string | null = null;

  init(options?: InitOptions) {
    if (options) {
      // merge user options onto defaults
      this.config = { ...this.config, ...options };
    }
    if (this.config.debug) {
      console.log('[MiloGameSDK] initialized with', this.config);
    }
    // Inject global function for RN  Cocos
    (window as any).updateHostId = (hostId: string) => {
      console.log("Received hostId from React Native:", hostId);
      const oldHostId = this.hostId;
      this.setHostId(hostId);
      
      // Emit host change event if host actually changed
      if (oldHostId !== hostId) {
        HostChangeEvent.emit(InternalGameSdkEvents.HOST_CHANGED, { oldHostId, newHostId: hostId });
      }
    };
  }

  /** Trigger a named vibration preset */
  vibratePreset(preset: VibratePreset) {
    const payload: VibratePresetPayload = { preset };
    this.sendEvent(GameEvents.VibratePreset, payload);
  }

  /** Trigger a custom vibration pattern */
  vibratePattern(pattern: number[]) {
    const payload: VibratePatternPayload = { pattern };
    this.sendEvent(GameEvents.VibratePattern, payload);
  }

  gameLoaded(payload: GameLoadedPayload) {
    this.sendEvent(GameEvents.GameLoaded, payload);
  }

  gameStarted(payload: GameStartedPayload) {
    this.sendEvent(GameEvents.GameStarted, payload);
  }

  gameEnded(payload: GameEndedPayload) {
    this.sendEvent(GameEvents.GameEnded, payload);
  }

  sendScore(payload: ScorePayload) {
    this.sendEvent(GameEvents.ScoreUpdate, payload);
  }

  playAgain(payload: PlayAgainPayload) {
    this.sendEvent(GameEvents.PlayAgain, payload);
  }

  changeGame(payload: ChangeGamePayload) {
    this.sendEvent(GameEvents.ChangeGame, payload);
  }

  private sendEvent<T>(event: string, data: T) {
    const msg: GameMessage<T> = { event, data, ts: Date.now(), gameId: this.config.gameId };
    this.transport.send(msg);
  }

  getHostId(){
    return this.hostId;
  }

  setHostId(hostId: string) {
    this.hostId = hostId;
  }
}
