/**
 * Enum for different room statuses
 */
export enum RoomStatus {
    NONE,
    WAITING_FOR_PLAYERS,
    COUNTDOWN,
    GAME_SIMULATION,
    END_GAME,
    KICKED_OUT
}

/**
 * Interface for basic room settings that can be extended by specific games
 */
export interface IBaseRoomSettings {
    maxPlayers: number;
    minPlayers: number;
    maxClients: number;
    countdownTimeInSeconds: number;
    allowLateJoin: boolean;
    maxReconnectionAttempts: number;
    reconnectionAttemptInterval: number;
    reconnectionTime: number;
    gameFPS: number;
    serverFPS: number;
    patchRate: number;
}


/**
 * Interface for room creation options
 */
export interface IRoomCreationOptions {
    roomId?: string;
    roomSettings?: Partial<IBaseRoomSettings>;
    players?: any[];
    gamingEnv?: string;
}