import { _decorator, Component, Node, UITransform, Vec3, Size, view, Enum } from 'cc';
import { Layout } from 'cc';
const { ccclass, property } = _decorator;

export enum MiloLayoutType {
    HORIZONTAL = 0,
    VERTICAL = 1,
    GRID = 2,
}

export enum MiloLayoutAlign {
    LEFT_TOP = 0,
    CENTER_TOP = 1,
    RIGHT_TOP = 2,
    LEFT_CENTER = 3,
    CENTER = 4,
    RIGHT_CENTER = 5,
    LEFT_BOTTOM = 6,
    CENTER_BOTTOM = 7,
    RIGHT_BOTTOM = 8,
}

@ccclass('MiloLayout')
export class MiloLayout extends Component {
    @property({
        type: Enum(MiloLayoutType)
    })
    private _layoutType: MiloLayoutType = MiloLayoutType.HORIZONTAL;

    @property({
        type: Enum(MiloLayoutType)
    })
    get layoutType(): MiloLayoutType {
        return this._layoutType;
    }

    set layoutType(value: MiloLayoutType) {
        this._layoutType = value;
        this.updateLayout();
    }

    @property({
        type: Enum(MiloLayoutAlign)
    })
    private _alignment: MiloLayoutAlign = MiloLayoutAlign.LEFT_TOP;

    @property({
        type: Enum(MiloLayoutAlign)
    })
    get alignment(): MiloLayoutAlign {
        return this._alignment;
    }

    set alignment(value: MiloLayoutAlign) {
        this._alignment = value;
        this.updateLayout();
    }

    @property
    private _spacingX: number = 10;

    @property
    get spacingX(): number {
        return this._spacingX;
    }

    set spacingX(value: number) {
        this._spacingX = value;
        this.updateLayout();
    }

    @property
    private _spacingY: number = 10;

    @property
    get spacingY(): number {
        return this._spacingY;
    }

    set spacingY(value: number) {
        this._spacingY = value;
        this.updateLayout();
    }

    @property
    private _columns: number = 3;

    @property({
        visible: function(this: MiloLayout) {
            return this._layoutType === MiloLayoutType.GRID;
        }
    })
    get columns(): number {
        return this._columns;
    }

    set columns(value: number) {
        this._columns = value;
        this.updateLayout();
    }

    @property
    private _padding: number = 0;

    @property
    get padding(): number {
        return this._padding;
    }

    set padding(value: number) {
        this._padding = value;
        this.updateLayout();
    }

    @property
    private _autoResize: boolean = false;

    @property
    get autoResize(): boolean {
        return this._autoResize;
    }

    set autoResize(value: boolean) {
        this._autoResize = value;
        this.updateLayout();
    }

    private _activeChildCount: number = 0;
    private _layoutSize: Size = new Size(0, 0);

    onLoad() {
        // Listen for child added/removed events
        this.node.on(Node.EventType.CHILD_ADDED, this.onChildAdded, this);
        this.node.on(Node.EventType.CHILD_REMOVED, this.onChildRemoved, this);
    }

    onDestroy() {
        // Clean up all listeners
        this.node.off(Node.EventType.CHILD_ADDED, this.onChildAdded, this);
        this.node.off(Node.EventType.CHILD_REMOVED, this.onChildRemoved, this);
        
        // Remove listeners from all children
        this.removeAllChildListeners();
    }
    
    private onChildRemoved(child: Node) {
        // Remove listeners from the child that was removed
        this.removeChildListener(child);
        // Update layout
        this.updateLayout();
    }
    
    private removeAllChildListeners() {
        this.node.children.forEach(child => {
            this.removeChildListener(child);
        });
    }
    
    private removeChildListener(child: Node) {
        if (child['__miloLayoutActiveHandler']) {
            child.off(Node.EventType.ACTIVE_IN_HIERARCHY_CHANGED, child['__miloLayoutActiveHandler']);
            child['__miloLayoutActiveHandler'] = null;
        }
    }

    start() {
        // Initialize child active state tracking
        this._lastChildActiveState = this.node.children.map(child => child.active);
        
        // Update layout
        this.updateLayout();
        
        // Add active change listeners to all children
        this.addChildActiveListeners();
    }
    
    // Add active state change listeners to all children
    private addChildActiveListeners() {
        this.node.children.forEach(child => {
            this.addChildActiveListener(child);
        });
    }
    
    // Add active state change listener to a single child
    private addChildActiveListener(child: Node) {
        // Remove existing listener first to prevent duplicates
        child.off(Node.EventType.ACTIVE_IN_HIERARCHY_CHANGED, this.onChildActiveChanged, this);
        
        // Define a bound handler to preserve context
        const handler = () => {
            // Force update on next frame to ensure active state is fully processed
            this.scheduleOnce(() => {
                this.updateLayout();
            }, 0);
        };
        
        // Store the handler on the node to help with removal
        child['__miloLayoutActiveHandler'] = handler;
        
        // Add new listener with the bound handler
        child.on(Node.EventType.ACTIVE_IN_HIERARCHY_CHANGED, handler);
    }
    
    private onChildActiveChanged(event: any) {
        // This method is no longer used
    }

    onEnable() {
        this.updateLayout();
    }

    getAlignmentOffset(totalSize: Size): Vec3 {
        const offset = new Vec3();
        const nodeSize = this.node.getComponent(UITransform)?.contentSize || new Size(0, 0);
        
        // Horizontal alignment
        switch (this._alignment) {
            case MiloLayoutAlign.LEFT_TOP:
            case MiloLayoutAlign.LEFT_CENTER:
            case MiloLayoutAlign.LEFT_BOTTOM:
                offset.x = this._padding;
                break;
            case MiloLayoutAlign.CENTER_TOP:
            case MiloLayoutAlign.CENTER:
            case MiloLayoutAlign.CENTER_BOTTOM:
                offset.x = (nodeSize.width - totalSize.width) / 2;
                break;
            case MiloLayoutAlign.RIGHT_TOP:
            case MiloLayoutAlign.RIGHT_CENTER:
            case MiloLayoutAlign.RIGHT_BOTTOM:
                offset.x = nodeSize.width - totalSize.width - this._padding;
                break;
        }
        
        // Vertical alignment
        switch (this._alignment) {
            case MiloLayoutAlign.LEFT_TOP:
            case MiloLayoutAlign.CENTER_TOP:
            case MiloLayoutAlign.RIGHT_TOP:
                offset.y = -this._padding;
                break;
            case MiloLayoutAlign.LEFT_CENTER:
            case MiloLayoutAlign.CENTER:
            case MiloLayoutAlign.RIGHT_CENTER:
                offset.y = -(nodeSize.height - totalSize.height) / 2;
                break;
            case MiloLayoutAlign.LEFT_BOTTOM:
            case MiloLayoutAlign.CENTER_BOTTOM:
            case MiloLayoutAlign.RIGHT_BOTTOM:
                offset.y = -nodeSize.height + totalSize.height + this._padding;
                break;
        }
        
        return offset;
    }

    updateLayout() {
        if (!this.node) return;
        
        // Important: Only use active children for layout
        const children = this.node.children.filter(child => child.active);
        this._activeChildCount = children.length;
        
        if (this._activeChildCount === 0) return;
        
        switch (this._layoutType) {
            case MiloLayoutType.HORIZONTAL:
                this.updateHorizontalLayout(children);
                break;
            case MiloLayoutType.VERTICAL:
                this.updateVerticalLayout(children);
                break;
            case MiloLayoutType.GRID:
                this.updateGridLayout(children);
                break;
        }
    }
    
    private updateHorizontalLayout(children: Node[]) {
        let totalWidth = this._padding * 2;
        let maxHeight = 0;
        
        // First pass: calculate total width and max height
        children.forEach((child, index) => {
            const childTransform = child.getComponent(UITransform);
            if (!childTransform) return;
            
            totalWidth += childTransform.width;
            if (index < children.length - 1) {
                totalWidth += this._spacingX;
            }
            
            maxHeight = Math.max(maxHeight, childTransform.height);
        });
        
        const totalSize = new Size(totalWidth, maxHeight + this._padding * 2);
        const startPos = this.getAlignmentOffset(totalSize);
        
        let currentX = startPos.x;
        
        // Second pass: position children
        children.forEach((child) => {
            const childTransform = child.getComponent(UITransform);
            if (!childTransform) return;
            
            const childHeight = childTransform.height;
            const childWidth = childTransform.width;
            
            let posY = startPos.y;
            
            // Vertical alignment for each child
            switch (this._alignment) {
                case MiloLayoutAlign.LEFT_TOP:
                case MiloLayoutAlign.CENTER_TOP:
                case MiloLayoutAlign.RIGHT_TOP:
                    posY = startPos.y - this._padding;
                    break;
                case MiloLayoutAlign.LEFT_CENTER:
                case MiloLayoutAlign.CENTER:
                case MiloLayoutAlign.RIGHT_CENTER:
                    posY = startPos.y - (maxHeight - childHeight) / 2;
                    break;
                case MiloLayoutAlign.LEFT_BOTTOM:
                case MiloLayoutAlign.CENTER_BOTTOM:
                case MiloLayoutAlign.RIGHT_BOTTOM:
                    posY = startPos.y - maxHeight + childHeight;
                    break;
            }
            
            child.position = new Vec3(currentX + childWidth / 2, posY - childHeight / 2, 0);
            currentX += childWidth + this._spacingX;
        });
        
        this._layoutSize = totalSize;
        
        if (this._autoResize) {
            const transform = this.node.getComponent(UITransform);
            if (transform) {
                transform.setContentSize(totalSize);
            }
        }
    }
    
    private updateVerticalLayout(children: Node[]) {
        let totalHeight = this._padding * 2;
        let maxWidth = 0;
        
        // First pass: calculate total height and max width
        children.forEach((child, index) => {
            const childTransform = child.getComponent(UITransform);
            if (!childTransform) return;
            
            totalHeight += childTransform.height;
            if (index < children.length - 1) {
                totalHeight += this._spacingY;
            }
            
            maxWidth = Math.max(maxWidth, childTransform.width);
        });
        
        const totalSize = new Size(maxWidth + this._padding * 2, totalHeight);
        const startPos = this.getAlignmentOffset(totalSize);
        
        let currentY = startPos.y;
        
        // Second pass: position children
        children.forEach((child) => {
            const childTransform = child.getComponent(UITransform);
            if (!childTransform) return;
            
            const childHeight = childTransform.height;
            const childWidth = childTransform.width;
            
            let posX = startPos.x;
            
            // Horizontal alignment for each child
            switch (this._alignment) {
                case MiloLayoutAlign.LEFT_TOP:
                case MiloLayoutAlign.LEFT_CENTER:
                case MiloLayoutAlign.LEFT_BOTTOM:
                    posX = startPos.x + this._padding;
                    break;
                case MiloLayoutAlign.CENTER_TOP:
                case MiloLayoutAlign.CENTER:
                case MiloLayoutAlign.CENTER_BOTTOM:
                    posX = startPos.x + (maxWidth - childWidth) / 2 + this._padding;
                    break;
                case MiloLayoutAlign.RIGHT_TOP:
                case MiloLayoutAlign.RIGHT_CENTER:
                case MiloLayoutAlign.RIGHT_BOTTOM:
                    posX = startPos.x + maxWidth - childWidth + this._padding;
                    break;
            }
            
            child.position = new Vec3(posX + childWidth / 2, currentY - childHeight / 2, 0);
            currentY -= childHeight + this._spacingY;
        });
        
        this._layoutSize = totalSize;
        
        if (this._autoResize) {
            const transform = this.node.getComponent(UITransform);
            if (transform) {
                transform.setContentSize(totalSize);
            }
        }
    }
    
    private updateGridLayout(children: Node[]) {
        if (this._columns <= 0) return;
        
        const rows = Math.ceil(children.length / this._columns);
        let maxCellWidth = 0;
        let maxCellHeight = 0;
        
        // First pass: find maximum cell size
        children.forEach((child) => {
            const childTransform = child.getComponent(UITransform);
            if (!childTransform) return;
            
            maxCellWidth = Math.max(maxCellWidth, childTransform.width);
            maxCellHeight = Math.max(maxCellHeight, childTransform.height);
        });
        
        const totalWidth = this._padding * 2 + maxCellWidth * this._columns + this._spacingX * (this._columns - 1);
        const totalHeight = this._padding * 2 + maxCellHeight * rows + this._spacingY * (rows - 1);
        
        const totalSize = new Size(totalWidth, totalHeight);
        const startPos = this.getAlignmentOffset(totalSize);
        
        // Second pass: position children
        for (let row = 0; row < rows; row++) {
            // Calculate how many items are in this row
            const itemsInRow = Math.min(this._columns, children.length - row * this._columns);
            
            // Calculate row width based on items in this row
            const rowWidth = itemsInRow * maxCellWidth + (itemsInRow - 1) * this._spacingX;
            
            // Calculate row offset based on alignment
            let rowOffsetX = 0;
            switch (this._alignment) {
                case MiloLayoutAlign.CENTER_TOP:
                case MiloLayoutAlign.CENTER:
                case MiloLayoutAlign.CENTER_BOTTOM:
                    // Center the row
                    rowOffsetX = (this._columns * maxCellWidth + (this._columns - 1) * this._spacingX - rowWidth) / 2;
                    break;
                case MiloLayoutAlign.RIGHT_TOP:
                case MiloLayoutAlign.RIGHT_CENTER:
                case MiloLayoutAlign.RIGHT_BOTTOM:
                    // Right align the row
                    rowOffsetX = this._columns * maxCellWidth + (this._columns - 1) * this._spacingX - rowWidth;
                    break;
                default:
                    // Left align (default) - offset is 0
                    break;
            }
            
            // Position children in this row
            for (let col = 0; col < itemsInRow; col++) {
                const index = row * this._columns + col;
                if (index >= children.length) break;
                
                const child = children[index];
                const childTransform = child.getComponent(UITransform);
                if (!childTransform) continue;
                
                const childWidth = childTransform.width;
                const childHeight = childTransform.height;
                
                // Calculate cell position based on column, row, and row offset
                const cellX = startPos.x + this._padding + col * (maxCellWidth + this._spacingX) + rowOffsetX;
                const cellY = startPos.y - this._padding - row * (maxCellHeight + this._spacingY);
                
                // Position within cell based on alignment (for individual item alignment within its cell)
                let posX = cellX;
                let posY = cellY;
                
                // Horizontal alignment within cell
                switch (this._alignment) {
                    case MiloLayoutAlign.LEFT_TOP:
                    case MiloLayoutAlign.LEFT_CENTER:
                    case MiloLayoutAlign.LEFT_BOTTOM:
                        posX = cellX;
                        break;
                    case MiloLayoutAlign.CENTER_TOP:
                    case MiloLayoutAlign.CENTER:
                    case MiloLayoutAlign.CENTER_BOTTOM:
                        posX = cellX + (maxCellWidth - childWidth) / 2;
                        break;
                    case MiloLayoutAlign.RIGHT_TOP:
                    case MiloLayoutAlign.RIGHT_CENTER:
                    case MiloLayoutAlign.RIGHT_BOTTOM:
                        posX = cellX + maxCellWidth - childWidth;
                        break;
                }
                
                // Vertical alignment within cell
                switch (this._alignment) {
                    case MiloLayoutAlign.LEFT_TOP:
                    case MiloLayoutAlign.CENTER_TOP:
                    case MiloLayoutAlign.RIGHT_TOP:
                        posY = cellY;
                        break;
                    case MiloLayoutAlign.LEFT_CENTER:
                    case MiloLayoutAlign.CENTER:
                    case MiloLayoutAlign.RIGHT_CENTER:
                        posY = cellY - (maxCellHeight - childHeight) / 2;
                        break;
                    case MiloLayoutAlign.LEFT_BOTTOM:
                    case MiloLayoutAlign.CENTER_BOTTOM:
                    case MiloLayoutAlign.RIGHT_BOTTOM:
                        posY = cellY - maxCellHeight + childHeight;
                        break;
                }
                
                child.position = new Vec3(posX + childWidth / 2, posY - childHeight / 2, 0);
            }
        }
        
        this._layoutSize = totalSize;
        
        if (this._autoResize) {
            const transform = this.node.getComponent(UITransform);
            if (transform) {
                transform.setContentSize(totalSize);
            }
        }
    }

    // Add a backup polling mechanism to check for changes
    private _lastChildActiveState: boolean[] = [];
    
    update(deltaTime: number) {
        // Create a backup polling mechanism for active state changes
        // This ensures layout updates even if the events aren't firing properly
        if (this.node && this.node.children.length > 0) {
            const currentActiveState = this.node.children.map(child => child.active);
            
            // Check if active states have changed
            let changed = false;
            if (this._lastChildActiveState.length !== currentActiveState.length) {
                changed = true;
            } else {
                for (let i = 0; i < currentActiveState.length; i++) {
                    if (this._lastChildActiveState[i] !== currentActiveState[i]) {
                        changed = true;
                        break;
                    }
                }
            }
            
            // Update layout if active states changed
            if (changed) {
                this._lastChildActiveState = currentActiveState;
                this.updateLayout();
            }
        }
    }
        
    private onChildAdded(child: Node) {
        // Add active change listener to the new child
        this.addChildActiveListener(child);
        // Update layout
        this.updateLayout();
    }
}


