import { _decorator, Component, Label, Node, Tween, tween, Vec3 } from 'cc';
import { Menu } from './Menu';
const { ccclass, property } = _decorator;

@ccclass('CountdownScreen')
export class CountdownScreen extends Menu {
    _countdownTween: Tween<Node> = null;
    @property({type: Label})
    countdownText: Label = null;
    @property({type : Node})
    blackBG: Node = null;

    protected onLoad(): void {
        super.onLoad();
    }
    protected onDestroy(): void {
    }
    public Open(): void {
        super.Open();
        this.initialization();
    }
    public Close(): void {
        super.Close();
        if(this._countdownTween != null)
            this._countdownTween.stop();
        
    } 
    initCountdownTween() {
        this.countdownText.node.active = true;
        this._countdownTween = tween(this.countdownText.node);
        for(let i=3; i>=1; i--) {
            this._countdownTween.call(() => {
                this.countdownText.string = i.toString();
                this.countdownText.node.scale = Vec3.ONE.clone().multiplyScalar(2);
            })
            .to(1, { scale: Vec3.ONE.clone().multiplyScalar(1) }, { easing: 'sineIn' });
        }
        this._countdownTween.call(() => {
            this.deactivateScreen();
        });

    }
    deactivateScreen() {
        this.countdownText.string = "";
        this.countdownText.node.active = false;
        this.blackBG.active = false;        
    }
    initialization() {
        this.initCountdownTween();
        this._countdownTween.start();
    }
}

