import { _decorator, Component, Label, Node, Tween, tween, Vec3 } from 'cc';
import { Menu } from './Menu';
const { ccclass, property } = _decorator;

@ccclass('ReconnectingScreen')
export class ReconnectingScreen extends Menu {
    @property({ type: Node })
    public spinner: Node = null;
    _spinnerTween: Tween<Node> = null;
    protected onLoad(): void {
        super.onLoad();
        
    }

    protected onDestroy(): void {
    }

    public Open(): void {
        super.Open();
        this.startSpinner();
    }
    public Close(): void {
        super.Close();
        this.stopSpinner();
    } 
    startSpinner() {
        this._spinnerTween = tween(this.spinner)
        .repeatForever(
            tween()
                .by(1, { angle: 360 })
        ).start();
    }
    stopSpinner() {
        this._spinnerTween?.stop();
        this._spinnerTween = null;
    }
    public initHandTween() {

    }
    
    initialization() {
        this.initHandTween();
    }
}

