import { UITransform } from 'cc';
import { _decorator, Component, Node, Sprite, SpriteFrame, Vec3, tween, RichText, UIOpacity } from 'cc';
import { Board } from './Board';
import { DeathType } from './type/DeathType';
import { Layout } from 'cc';
import { Tween } from 'cc';

const { ccclass, property } = _decorator;

@ccclass('DeathPopup')
export class DeathPopup extends Component {
    static instance: DeathPopup = null;

    @property({ type: RichText })
    deathText: RichText = null;
    
    @property({ type: Sprite })
    deathIcon: Sprite = null;

    @property({type: Node})
    deathPopupNode: Node = null;

    @property({type: SpriteFrame})
    deathIconSprites: SpriteFrame[] = [];

    private scaleTween: Tween<Node> | null = null;
    private opacityTween: Tween<UIOpacity> | null = null;
    private pendingScheduleId: number | null = null;

    protected onLoad(): void {
        DeathPopup.instance = this;
    }

    /**
     * Static method to show a death popup at the specified position
     * @param position - The world position where the death occurred
     * @param popupRichText - Text to display in the popup
     * @param killType - Type of death
     */
    static showDeathPopup(position: Vec3, popupRichText: string, killType: DeathType): void {
        if(!DeathPopup.instance.deathPopupNode) {
            console.log("DeathPopup: deathPopupNode not assigned");
            return;
        }
        
        DeathPopup.instance.deathPopupNode.active = true;
        
        // Set the text and icon content
        DeathPopup.instance.deathText.string = popupRichText;
        DeathPopup.instance.deathIcon.spriteFrame = DeathPopup.instance.getDeathIconSprite(killType);
        
        // Update layout to ensure proper sizing
        const layout = DeathPopup.instance.deathPopupNode.getComponent(Layout);
        if (layout) {
            layout.updateLayout();
        }
        
        // Get the popup's UITransform for bounds calculation
        const popupUITransform = DeathPopup.instance.deathPopupNode.getComponent(UITransform);
        if (!popupUITransform) {
            console.error("DeathPopup: Missing UITransform component on deathPopupNode");
            return;
        }
        
        // Calculate and adjust position with bounds checking
        const adjustedPosition = DeathPopup.instance.calculateAdjustedPosition(position, popupUITransform, killType);
        DeathPopup.instance.deathPopupNode.setWorldPosition(adjustedPosition);
        
        // Play the death popup animation
        DeathPopup.instance.playDeathPopupAnimation();
    }

    /**
     * Calculate adjusted position with bounds checking
     * @param targetPosition - The target position where popup should appear
     * @param popupUITransform - The popup's UITransform component
     * @param killType - Type of death for height offset calculation
     * @returns Adjusted position with bounds compensation applied
     */
    private calculateAdjustedPosition(targetPosition: Vec3, popupUITransform: UITransform, killType: DeathType): Vec3 {
        // Get board bounds
        const boardNode = Board.Instance.boardSpriteNode;
        const boardUITransform = boardNode.getComponent(UITransform);
        
        if (!boardUITransform) {
            console.error("DeathPopup: Board node missing UITransform component");
            return targetPosition;
        }
        
        // Calculate board world bounds
        const boardWorldPosition = boardNode.worldPosition;
        const boardWidth = boardUITransform.width;
        const boardLeftBound = boardWorldPosition.x - (boardWidth * 0.5);
        const boardRightBound = boardWorldPosition.x + (boardWidth * 0.5);
        
        // Calculate popup bounds with center anchor (0.5, 0.5)
        const popupWidth = popupUITransform.width;
        const popupLeftBound = targetPosition.x - (popupWidth * 0.5);
        const popupRightBound = targetPosition.x + (popupWidth * 0.5);
        
        let adjustedX = targetPosition.x;
        
        // Check if popup overflows left bound
        if (popupLeftBound < boardLeftBound) {
            const overflowAmount = boardLeftBound - popupLeftBound;
            adjustedX += overflowAmount;
        }
        // Check if popup overflows right bound
        else if (popupRightBound > boardRightBound) {
            const overflowAmount = popupRightBound - boardRightBound;
            adjustedX -= overflowAmount;
        }
        
        // Calculate height offset
        const popupHeight = popupUITransform.height;
        const heightOffset = (popupHeight * 0.5) + (killType === DeathType.SNAKE_BITE ? 40 : 50);
        
        return new Vec3(adjustedX, targetPosition.y + heightOffset, targetPosition.z);
    }

    /**
     * Play the death popup animation
     * Scale: 0.75 -> 1.0 -> 0.75
     * Opacity: 0 -> 1 -> 0
     * Duration: 0.5 seconds for each phase with 0.6 second pause
     */
    private playDeathPopupAnimation(): void {
        if (!this.deathPopupNode) {
            console.error("DeathPopup: deathPopupNode not assigned for animation");
            return;
        }
    
        // Stop previous tweens and scheduled timeouts
        this.stopCurrentAnimation();
    
        // Get or add UIOpacity component
        let uiOpacity = this.deathPopupNode.getComponent(UIOpacity);
        if (!uiOpacity) {
            uiOpacity = this.deathPopupNode.addComponent(UIOpacity);
        }
    
        // Set initial state
        this.deathPopupNode.setScale(0.75, 0.75, 1);
        uiOpacity.opacity = 0;
    
        // Phase 1: Scale up and fade in
        this.scaleTween = tween(this.deathPopupNode)
            .to(0.3, { scale: new Vec3(1, 1, 1) })
            .start();
    
        this.opacityTween = tween(uiOpacity)
            .to(0.3, { opacity: 255 })
            .call(() => {
                // Pause before phase 2
                this.pendingScheduleId = window.setTimeout(() => {
                    this.scaleTween = tween(this.deathPopupNode)
                        .to(0.5, { scale: new Vec3(0.75, 0.75, 1) })
                        .start();
    
                    this.opacityTween = tween(uiOpacity)
                        .to(0.5, { opacity: 0 })
                        .call(() => {
                            this.deathPopupNode.active = false;
                            this.scaleTween = null;
                            this.opacityTween = null;
                            this.pendingScheduleId = null;
                        })
                        .start();
                }, 2000);
            })
            .start();
    }

    /**
     * Stop current animation and clean up resources
     */
    private stopCurrentAnimation(): void {
        if (this.scaleTween) {
            this.scaleTween.stop();
            this.scaleTween = null;
        }
        if (this.opacityTween) {
            this.opacityTween.stop();
            this.opacityTween = null;
        }
        if (this.pendingScheduleId !== null) {
            clearTimeout(this.pendingScheduleId);
            this.pendingScheduleId = null;
        }
    }

    getDeathIconSprite(killType: DeathType) {
        return this.deathIconSprites[killType];
    }
}


