// Extend the Window interface to include ReactNativeWebView
declare global {
    interface Window {
        ReactNativeWebView?: {
            postMessage(message: string): void;
        };
    }
}

export enum LogLevel {
    Debug,
    Info,
    Warning,
    Error
}

// Logger class to encapsulate logging logic
export class PBLogger {
    // Current log level; logs with a lower level will not be output
    private static currentLevel: LogLevel = LogLevel.Debug;
    
    // Flag to determine if we're in production mode
    private static isProduction: boolean = false;

    // Method to log debug messages
    static debug(message: string, ...params: unknown[]) {
        this.log(LogLevel.Debug, message, params);
    }

    // Method to log informational messages
    static info(message: string, ...params: unknown[]) {
        this.log(LogLevel.Info, message, params);
    }

    // Method to log warnings
    static warning(message: string, ...params: unknown[]) {
        this.log(LogLevel.Warning, message, params);
    }

    // Method to log errors
    static error(message: string, ...params: unknown[]) {
        this.log(LogLevel.Error, message, params);
    }

    // Private method to handle the actual logging
    private static log(level: LogLevel, message: string, params: unknown[]) {
        if (level >= this.currentLevel) {
            const output = `${LogLevel[level]}: ${message}`;
            
            // Always log to console
            console.log(output, ...params);
            
            // If in production mode, also send to ReactNativeWebView
            if (this.isProduction && window.ReactNativeWebView) {
                try {
                    const logData = {
                        event: 'log',
                        level: LogLevel[level],
                        message: output,
                        params: params
                    };
                    window.ReactNativeWebView.postMessage(JSON.stringify(logData));
                } catch (e) {
                    console.error('Failed to send log to ReactNativeWebView:', e);
                }
            }
        }
    }

    // Method to set the logger's level
    static setLevel(level: LogLevel) {
        this.currentLevel = level;
    }
    
    // Method to set production mode
    static setProductionMode(isProduction: boolean) {
        this.isProduction = isProduction;
    }
}