import { _decorator, Component, Node, Prefab } from 'cc';
import { Pooler } from './Pooler';
const { ccclass, property } = _decorator;

@ccclass('PoolManager')
export class PoolManager extends Component {
    
    static instance : PoolManager = null;
    pools : Pooler[] = [];

    protected onLoad(): void {
        PoolManager.instance = this;
    }
    
    createPool(prefab : Prefab) {
        let pool = new Pooler(prefab.name);
        pool.objectPrefab = prefab;
        this.pools.push(pool);
    }
    getObjectFromPool(prefab : Prefab) : Node {
        var pool = this.getPool(prefab.name);
        if(pool != null) {
            return pool.getObject();
        }
        this.createPool(prefab);
        return this.pools[this.pools.length - 1].getObject();
    }

    getPool(poolName : string) : Pooler {
        for(let i = 0; i < this.pools.length; i++) {
            if(this.pools[i].poolName == poolName) {
                return this.pools[i];
            }
        }
        return null;
    }

    returnObjectToPool(obj : Node) {
        if(!obj.active)return;
        var poolName = obj.name.split(" ")[0];
        var pool = this.getPool(poolName);
        if(pool != null) {
            pool.returnObject(obj);
        }
        else {
            console.warn("No pool found for object " + obj.name);
        }
    }
}


