import { SNLGameState } from "../type/SNLGameState";
import { BaseRoomState } from "../../pulse/schema/BaseRoomState";
import { BasePlayerData } from "db://assets/SNLGame/pulse/schema/BasePlayerData";
import { SNLRoomSettings } from "./SNLRoomSettings";
import { SNLBoard } from "./SNLBoard";
import { SNLPlayerData } from "./SNLPlayerData";
export class SNLInputPayload {
  inputType: string;
  inputValue: string;
  copyFrom(source: SNLInputPayload): void {
    this.inputType = source.inputType;
    this.inputValue = source.inputValue;
  }
}
export class SNLRoomState extends BaseRoomState<SNLRoomSettings, SNLPlayerData> {
  turnPlayerNumber: number = 0;
  finishedCount: number = 0;
  gameOptions: SNLRoomSettings = new SNLRoomSettings();
  inputPayload: SNLInputPayload = new SNLInputPayload();
  currentGameState : number = SNLGameState.NONE;
  previousGameState : number = SNLGameState.NONE;
  syncStateNumber : number = 0;
  board: SNLBoard = new SNLBoard();
  hasMissedTurn = true;
  timer = 0;

  constructor() {
    super(new SNLRoomSettings(), []);
  }
  
  copyFrom(source: SNLRoomState): void {
    console.log("============ CopyFrom snl room state =========");
    super.copyFrom(source);
    this.playersData = source.playersData.map(player => {
      const newPlayer = new SNLPlayerData();
      newPlayer.copyFrom(player);
      return newPlayer;
    });
    this.spectatorsData = source.spectatorsData.map(spectator => {
        const newSpectator = new BasePlayerData();
        newSpectator.copyFrom(spectator);
        return newSpectator;
    });
    this.turnPlayerNumber = source.turnPlayerNumber;
    this.finishedCount = source.finishedCount;
    this.gameOptions = new SNLRoomSettings();
    this.gameOptions.copyFrom(source.gameOptions);
    this.syncStateNumber = source.syncStateNumber;
    this.inputPayload = new SNLInputPayload();
    this.inputPayload.copyFrom(source.inputPayload);
    this.board = new SNLBoard();
    this.board.copyFrom(source.board);
    this.hasMissedTurn = source.hasMissedTurn;
    this.timer = source.timer;

    this.currentGameState = source.currentGameState;
    this.previousGameState = source.previousGameState;
  }
}
