import { Player, PlayerState } from "../Player";
import { PlayerBaseState } from "./PlayerBaseState";
import { PlayerStateMachine } from "./PlayerStateMachine";

export class IdleState extends PlayerBaseState {
    stateMachine: PlayerStateMachine;
    _stateName : PlayerState = PlayerState.IDLE;
    player: Player;
    constructor(stateMachine: PlayerStateMachine) {
        super();
        this.stateMachine = stateMachine;
        this.player = this.stateMachine.player;
    }
    enterState() {
        // console.log("Entered Idle State");
        this.clearPlayerData();
    }
    updateState(deltaTime) {
    }
    exitState() {
        // console.log("Exited Idle State");
    }

    clearPlayerData(){
        this.player.timer.stop();
        this.player.timer.reset();
        this.player.outComesUIManager.setGoldenDiceOutcomes(0);
        this.player.outComesUIManager.clearAllOutComes();
        this.player.outComesUIManager.enableOutComesBg(false);
        this.player.dice.reset();
        this.player.diceRoot.active = false;
        if(this.player._moveOptioneInput != null){
            this.player._moveOptioneInput.node.active = false;
        }
    }
}