import { sys } from 'cc';
import { view } from 'cc';
import { _decorator, Camera, Component, Node, UITransform, Vec3, Size, screen, math, rect } from 'cc';
const { ccclass, property, executionOrder } = _decorator;

executionOrder(-1)
@ccclass('CameraManger')
export class CameraManger extends Component {
    static instance: CameraManger = null;
    @property({ type: UITransform })
    canvasUITransform: UITransform = null;
    
    @property
    designHeight: number = 1920;
    
    @property
    designWidth: number = 1080;
    
    @property
    defaultOrthoHeight: number = 820;
    
    @property
    minOrthoHeight: number = 750;
    
    @property
    maxOrthoHeight: number = 1080;
    
    @property
    safeAreaPadding: number = 50; // Add padding to ensure content isn't at the very edge
    
    designAspectRatio: number;
    currentAspectRatio: number;
    camera: Camera = null;
    windowWidth: number = 1080;
    windowHeight: number = 1920;

    protected onLoad(): void {
        CameraManger.instance = this;
        this.designAspectRatio = this.designWidth / this.designHeight;
        this.camera = this.node.getComponent(Camera);
    }

    protected start(): void {
        this.updateScreenSize();
        this.adaptCameraToScreen();
        
        // Listen for screen size changes (orientation changes)
        window.addEventListener('resize', () => {
            this.updateScreenSize();
            this.adaptCameraToScreen();
        });
    }
    
    protected update(dt: number): void {
    }
    
    private updateScreenSize(): void {
        this.windowWidth = screen.windowSize.width || window.innerWidth;
        this.windowHeight = screen.windowSize.height || window.innerHeight;
        this.currentAspectRatio = this.windowWidth / this.windowHeight;
        console.log("Window Size: " + this.windowWidth + "x" + this.windowHeight + ", Aspect Ratio: " + this.currentAspectRatio.toFixed(2));
    }

    adaptCameraToScreen(): void {
        if(!this.camera) return;
        
        // Get safe area info if available
        let safeAreaTop = 0;
        let safeAreaBottom = 0;
        let safeAreaLeft = 0;
        let safeAreaRight = 0;
        
        if(sys.os === sys.OS.IOS || sys.os === sys.OS.ANDROID) {
            // Use view.getVisibleSize() and visibleOrigin instead of getSafeAreaRect
            const visibleSize = view.getVisibleSize();
            const visibleOrigin = view.getVisibleOrigin();
            
            if(visibleSize) {
                safeAreaTop = visibleOrigin.y;
                safeAreaBottom = this.windowHeight - (visibleOrigin.y + visibleSize.height);
                safeAreaLeft = visibleOrigin.x;
                safeAreaRight = this.windowWidth - (visibleOrigin.x + visibleSize.width);
            }
        }
        
        // Calculate the effective height of the screen (considering safe area)
        const effectiveHeight = this.windowHeight - safeAreaTop - safeAreaBottom - (this.safeAreaPadding * 2);
        const effectiveWidth = this.windowWidth - safeAreaLeft - safeAreaRight - (this.safeAreaPadding * 2);
        const effectiveAspectRatio = effectiveWidth / effectiveHeight;
        
        console.log("Effective Size: " + effectiveWidth + "x" + effectiveHeight + ", Aspect Ratio: " + effectiveAspectRatio.toFixed(2));
        
        // Determine if we're in portrait or landscape mode
        const isLandscape = this.currentAspectRatio >= 1.0;
        
        let orthoHeight = this.defaultOrthoHeight;
        
        if(isLandscape) {
            console.log("=== Landscape Mode ===");
            // For landscape, use a larger ortho height to show more content vertically
            orthoHeight = this.defaultOrthoHeight * (1.0 / this.currentAspectRatio) * 1.5;
        } else {
            console.log("=== Portrait Mode ===");
            // For portrait, adjust ortho height based on aspect ratio to ensure content fits
            // The narrower the screen, the smaller the ortho height needs to be
            const aspectRatioFactor = 0.6 / Math.min(this.currentAspectRatio, 0.6);
            orthoHeight = this.defaultOrthoHeight * aspectRatioFactor;
        }
        
        // Ensure ortho height stays within reasonable bounds
        orthoHeight = math.clamp(orthoHeight, this.minOrthoHeight, this.maxOrthoHeight);
        
        this.camera.orthoHeight = orthoHeight;
        console.log("Camera Ortho Height: " + orthoHeight.toFixed(2));
    }

    getWidth(): number {
        return this.camera.orthoHeight * 2 * this.currentAspectRatio;
    }

    getCurrentOrthoHeight(): number {
        return this.camera.orthoHeight;
    }
    
    // Call this method if game content is getting cut off after layout changes
    public refreshCameraAdaptation(): void {
        this.updateScreenSize();
        this.adaptCameraToScreen();
    }
}
