// src/types.ts

/** Named presets for haptic feedback */
export type VibratePreset = 
  | 'short' 
  | 'long' 
  | 'double'
  | 'impactLight'
  | 'impactMedium'
  | 'impactHeavy'
  | 'rigid'
  | 'soft'
  | 'light'
  | 'medium'
  | 'heavy'
  | 'notificationSuccess'
  | 'notificationWarning'
  | 'notificationError'
  | 'selection'
  | 'clockTick'
  | 'contextClick'
  | 'keyboardPress'
  | 'keyboardRelease'
  | 'keyboardTap'
  | 'longPress'
  | 'textHandleMove'
  | 'virtualKey'
  | 'virtualKeyRelease'
  | 'effectClick'
  | 'effectDoubleClick'
  | 'effectHeavyClick'
  | 'effectTick';

/** Payload for a preset vibration */
export interface VibratePresetPayload {
  preset: VibratePreset;
}

/** Payload for a custom vibration pattern (ms intervals) */
export interface VibratePatternPayload {
  pattern: number[]; // e.g. [100,50,100]
}

/** Payload when the game is loaded */
export interface GameLoadedPayload {
    timestamp: number;
    // add other fields as needed
  }

/** Payload when the game starts */
export interface GameStartedPayload {
    timestamp: number;
    // add other fields as needed
  }
  
  /** Payload when the game ends */
  export interface GameEndedPayload {
    duration: number;
    hasWinningScreen: boolean
    // add other fields as needed
  }
  
  /** Payload when score changes */
  export interface ScorePayload {
    score: number;
    level?: number;
    // add other fields as needed
  }
  
  /** 
   * Generic message sent from the game to React Native 
   * `T` is the type of the payload (e.g. GameStartedPayload)
   */
  export interface GameMessage<T = any> {
    /** one of the strings in GameEvents */
    event: string;
    /** the game name */
    gameId: string;
    /** the event-specific data */
    data: T;
    /** timestamp when it was sent */
    ts: number;
  }

  export interface PlayAgainPayload {
    timestamp: number;
  }

  export interface ChangeGamePayload {
    timestamp: number;
  }