import { _decorator, Component, Node } from 'cc';
import { Menu } from '../UIPack/Scripts/Menu';
import { PlayerAvatar } from '../pulse/pb-cocos-utils/PlayerAvatar';
import { Label } from 'cc';
import { GameManager } from './GameManager';
import { SNLGameHandler } from './SNLGameHandler';
import { ServerRoomStateChangeEvent } from '../pulse/networking/NetworkManager';
import { NetworkEvents } from '../pulse/types/NetworkTypes';
import { SNLRoomState } from './schema/SNLRoomState';
import { HostChangeEvent } from '../milo-game-sdk/src/core/MiloGameSDK';
import { InternalGameSdkEvents } from '../milo-game-sdk/src/events';
const { ccclass, property } = _decorator;

/**
 * RoundCompleted Menu - Displays end-of-game statistics and results
 * 
 * This menu shows:
 * - The winning player (first to finish)
 * - The player with the most deaths
 * - The player with the most ladder climbs
 * - Associated statistics counts
 */
@ccclass('RoundCompleted')
export class RoundCompleted extends Menu {
    /** Avatar display for the winning player */
    @property({type: PlayerAvatar})
    winnerAvatar: PlayerAvatar = null;

    /** Text label showing the winning player's name */
    @property({type: Label})
    winnerName: Label = null;

    /** Avatar display for the player with most deaths */
    @property({type: PlayerAvatar})
    mostDeathsPlayerAvatar: PlayerAvatar = null;

    /** Text label showing the name of player with most deaths */
    @property({type: Label})
    mostDeathsPlayerName: Label = null;

    /** Avatar display for the player with most ladder climbs */
    @property({type: PlayerAvatar})
    mostLadderClimbPlayerAvatar: PlayerAvatar = null;

    /** Text label showing the name of player with most ladder climbs */
    @property({type: Label})
    mostLadderClimbPlayerName: Label = null;

    /** Text label displaying the death count for the most deaths player */
    @property({type: Label})
    deathsCountLabel: Label = null;

    /** Text label displaying the ladder climb count for the most ladder climbs player */
    @property({type: Label})
    ladderClimbCountLabel: Label = null;

    @property({type: Node})
    actionsButtonContainer: Node = null;

    @property({type: Node})
    infoContainer: Node = null;

    _shouldShowThisScreen: boolean = false;
    _optionsInitialized : boolean = false;

    protected onLoad(): void {
        super.onLoad();
        ServerRoomStateChangeEvent.on(NetworkEvents.SERVER_ROOM_STATE_CHANGE, this.ServerStateChanged, this);
        HostChangeEvent.on(InternalGameSdkEvents.HOST_CHANGED, this.onHostChanged, this);
    }

    protected onDestroy(): void {
        ServerRoomStateChangeEvent.off(NetworkEvents.SERVER_ROOM_STATE_CHANGE, this.ServerStateChanged, this);
        HostChangeEvent.off(InternalGameSdkEvents.HOST_CHANGED, this.onHostChanged, this);
    }

    ServerStateChanged(newServerState: SNLRoomState) {
        if(this._optionsInitialized) return;
        const state = new SNLRoomState();
        if(state.gameOptions) {
            this._shouldShowThisScreen = state.gameOptions.show_result_screen;
            this._optionsInitialized = true;
        }
        state.copyFrom(newServerState);
    }

    public Open(): void {
        if(!this._shouldShowThisScreen) {
            return;
        }
        super.Open();
        this.initialize();
    }

    public Close(): void {
        super.Close();
    }

    /**
     * Initializes the round completion screen with game statistics
     * 
     * This method:
     * 1. Finds the winning player (first to reach finish position 1)
     * 2. Gets the player with most deaths using SNLGameHandler
     * 3. Gets the player with most ladder climbs using SNLGameHandler
     * 4. Updates all UI elements with player avatars, names, and statistics
     */
    initialize(){
        super.initialize();
        // Find the winning player by checking who has finishPosition == 1
        const winningPlayerNumber = GameManager.Instance._serverStateCopy.playersData.findIndex(player => player.finishPosition == 1);
        if (winningPlayerNumber == -1) {
            console.error("no winning player");
            return;
        }

        // Get the winning player object and update winner UI
        const winningPlayer = SNLGameHandler.Instance.getPlayer(winningPlayerNumber);
        this.winnerAvatar.setAvatarFromSpriteFrame(winningPlayer.GetProfileSpriteFrame());
        this.winnerName.string = winningPlayer.playerData.name;

        // Get and display the player with most deaths
        const mostDeathsPlayer = SNLGameHandler.Instance.getMostDeathsPlayer();
        this.mostDeathsPlayerAvatar.setAvatarFromSpriteFrame(mostDeathsPlayer.GetProfileSpriteFrame());
        this.mostDeathsPlayerName.string = mostDeathsPlayer.playerName.string;

        // Get and display the player with most ladder climbs
        const mostLadderClimbPlayer = SNLGameHandler.Instance.getMostLadderClimbsPlayer();
        this.mostLadderClimbPlayerAvatar.setAvatarFromSpriteFrame(mostLadderClimbPlayer.GetProfileSpriteFrame());
        this.mostLadderClimbPlayerName.string = mostLadderClimbPlayer.playerName.string;

        // Display the actual statistics counts
        this.deathsCountLabel.string = `Most Deaths: ${mostDeathsPlayer.playerData.deathsCount}`;
        this.ladderClimbCountLabel.string = `Most Ladders: ${mostLadderClimbPlayer.playerData.ladderClimbCount}`;

        this.updateHostUI();
    }

    /**
     * Updates the host-specific UI elements based on current host status
     */
    private updateHostUI(): void {
        const isHost = SNLGameHandler.Instance.isLocalUserHost();
        const hostPlayer = SNLGameHandler.Instance.getHostPlayer();
        
        if(isHost) {
            if(hostPlayer) {
                this.infoContainer.active = false;
                this.actionsButtonContainer.active = true;
            }
        } else {
            if(hostPlayer) {
                this.actionsButtonContainer.active = false;
                const label = this.infoContainer.getComponent(Label);
                label.string = `Ask ${hostPlayer.playerData.name}(Host) to play again or change game`;
                this.infoContainer.active = true;
            }
        }
    }

    /**
     * Handles host change events and updates UI if menu is open
     */
    private onHostChanged(data: { oldHostId: string | null, newHostId: string }): void {
        console.log("RoundCompleted: Host changed from", data.oldHostId, "to", data.newHostId);
        
        // Only update if the menu is currently open
        if (this.node.active) {
            this.updateHostUI();
        }
    }

    public onPlayAgainButtonClicked() {
        const timestamp = Date.now();
        GameManager.Instance.miloSDK.playAgain({timestamp: timestamp});
        console.log("play again button clicked");
    }

    public onChangeGameButtonClicked() {
        const timestamp = Date.now();
        GameManager.Instance.miloSDK.changeGame({timestamp: timestamp});
        console.log("change game button clicked");
    }
}


