import { Color } from 'cc';
import { _decorator, Component, Node, Sprite, SpriteFrame, Texture2D, ImageAsset, assetManager } from 'cc';

const { ccclass, property } = _decorator;

/**
 * PlayerAvatar component that handles player avatar display and interaction
 */
@ccclass('PlayerAvatar')
export class PlayerAvatar extends Component {
    // UI Components
    @property({ type: Sprite, tooltip: "Player Avatar" })
    readonly playerAvatar: Sprite = null!;

    @property({ type: Sprite, tooltip: "Player Avatar Outline" })
    readonly outline: Sprite = null!;

    // State
    private enableOnClick: boolean = false;
    public userId: string = "";

    /**
     * Initialize the avatar with user data
     */
    public initialize(enableOnClick: boolean, userId: string): void {
        this.enableOnClick = enableOnClick;
        this.userId = userId;
    }

    /**
     * Lifecycle method triggered when component is enabled
     */
    protected onEnable(): void {
        this.registerTouchEvents();
    }

    /**
     * Lifecycle method triggered when component is disabled
     */
    protected onDisable(): void {
        this.unregisterTouchEvents();
    }

    /**
     * Lifecycle method called when component starts
     */
    start() {
        // Initialization logic if needed
    }

    /**
     * Lifecycle method called every frame
     */
    update(deltaTime: number) {
        // Frame update logic if needed
    }

    /**
     * Register touch event listeners
     */
    private registerTouchEvents(): void {
        this.playerAvatar.node.on(Node.EventType.TOUCH_START, this.handleTouchStart, this);
    }

    /**
     * Unregister touch event listeners
     */
    private unregisterTouchEvents(): void {
        this.playerAvatar.node.off(Node.EventType.TOUCH_START, this.handleTouchStart, this);
    }

    /**
     * Handle touch start event
     */
    private handleTouchStart(): void {
        if (!this.enableOnClick) return;
        // Add touch handling logic here
    }

    /**
     * Set outline visibility
     */
    public setOutline(outline: boolean): void {
        this.outline.enabled = outline;
    }

    public setOutlineColor(color: Color): void {
        this.outline.color = color;
    }

    /**
     * Set avatar sprite from a URL
     */
    public setAvatarFromUrl(url: string): void {
        console.log("Setting avatar from url: ", url);
        const cachedTexture = this.getFromCache(url);
        if (cachedTexture) {
            this.createSpriteFrameFromTexture(cachedTexture);
            return;
        }
        
        this.loadRemoteImage(url);
    }

    /**
     * Set avatar sprite from a SpriteFrame
     */
    public setAvatarFromSpriteFrame(spriteFrame: SpriteFrame): void {
        this.playerAvatar.spriteFrame = spriteFrame;
    }

    /**
     * Get texture from cache if available
     */
    private getFromCache(url: string): Texture2D | null {
        const cachedTexture = assetManager.assets.get(url) as Texture2D;
        return cachedTexture || null;
    }

    /**
     * Create sprite frame from texture and apply to avatar
     */
    private createSpriteFrameFromTexture(texture: Texture2D): void {
        const spriteFrame = new SpriteFrame();
        spriteFrame.texture = texture;
        this.playerAvatar.spriteFrame = spriteFrame;
    }

    /**
     * Load image from remote URL
     */
    private loadRemoteImage(url: string): void {
        assetManager.loadRemote<ImageAsset>(
            url, 
            { ext: '.png' },
            (error, imageAsset) => {
                if (error) {
                    console.error(`[Player] Failed to load profile image: ${JSON.stringify(error)}`);
                    if (window.ReactNativeWebView) {
                        window.ReactNativeWebView.postMessage(JSON.stringify({ message: "player_avatar_load_failed", data: { userId: this.userId, url: url, errorName: error.name, errorMessage: error.message, errorStack: error.stack } }));
                    }
                    return;
                }
                
                try {
                    const spriteFrame = new SpriteFrame();
                    const texture = new Texture2D();
                    texture.image = imageAsset;
                    spriteFrame.texture = texture;
                    this.playerAvatar.spriteFrame = spriteFrame;
                } catch (e) {
                    console.error(`[Player] Error creating sprite frame: ${e}`);
                    if (window.ReactNativeWebView) {
                        window.ReactNativeWebView.postMessage(JSON.stringify({ message: "player_sprite_frame_creation_failed", data: { userId: this.userId, url: url, error: e } }));
                    }
                }
            }
        );
    }
}
