import { GameMessageType } from "./type/GameMessageType";
export  class InputManager {
    private static _instance: InputManager;
    public static get Instance(): InputManager {
        if (this._instance == null) {
            this._instance = new InputManager();
        }
        return this._instance;
    }
    private constructor() {
    }
    private _isInputGiven = false;
    private _inputType: GameMessageType = GameMessageType.MOVE_PIECE;
    private _movedPieceIndex: number = -1;

    public get IsInputGiven() {
        return this._isInputGiven;
    }
    public get InputType() {
        return this._inputType;
    }
    public get MovedPieceIndex() {
        return this._movedPieceIndex;
    }
    public ResetInput() {
        this._isInputGiven = false;
        this._inputType = GameMessageType.MOVE_PIECE;
        this._movedPieceIndex = -1;
    }
    public SetInput(inputType: GameMessageType, movedPieceIndex: number=-1) {
        this._isInputGiven = true;
        this._inputType = inputType;
        this._movedPieceIndex = movedPieceIndex;
    }
}