    @property({ type: Timer })
    timer : Timer = null;
    @property({ type: Sprite })
    timerSprite : Sprite = null;
    @property({ type: Dice })
    dice: Dice = null;
    @property({ type: Node })
    diceRoot: Node = null;
    @property({ type: Sprite })
    diceBgSprite : Sprite = null;
    @property({ type: Sprite })
    namePlateSprite : Sprite = null;
    @property({ type: Node })
    piecesBase : Node = null;
    @property({ type: PlayerAvatar })
    playerAvatar : PlayerAvatar = null;
    bases: Node[] = [];
    sortedBasesHorizontalPosition: Node[] = [];
    pawns : Piece[] = [];
    playerData : SNLPlayerData = null;
    state : PlayerState = PlayerState.IDLE;
    _isLocal = false;
    _isInitialized = false;
    _currentPiecesData: number[] = [0,0,0,0];
    _currentDiceValue = 1;
    _pieceAnimations: Tween<Node>[] = [];
    _isProfileInitialized = false;
    _board: Board = null;
    _myBasesParent: Node = null;
    _pawnHandler: PawnHandler = null;
    @property({type: OutComesUIManager})
    outComesUIManager: OutComesUIManager = null;
    @property({type: Node})
    dicePointingArrow : Node = null;
    diceArrowInitialPosition : Vec3 = new Vec3(0,0,0);
    _currentSelectedPieceNumber : number = -1;
    _moveOptioneInput : MoveOptionsInput = null;
    _gameOptions : SNLRoomSettings = null;
    _stateMachine : PlayerStateMachine;
    private profileImageUrl: string = "";

    protected onLoad(): void {
        this._pawnHandler = new PawnHandler(this.pawns, this);
        this.diceArrowInitialPosition = this.dicePointingArrow.getPosition();
        if(this._stateMachine == null)this._stateMachine = new PlayerStateMachine(this);
    }

    protected start(): void {
        this.createMoveOptionsUI();
    }
    
    protected update(dt: number): void {
        this._stateMachine.updateState(dt);
    }

    createMoveOptionsUI(){
        var moveOptionsUI = instantiate(PlayerAssets.instance.moveOptionsUI);
        moveOptionsUI.setParent(this.node);
        moveOptionsUI.setPosition(new Vec3(0,0,0));
        this._moveOptioneInput = moveOptionsUI.getComponent(MoveOptionsInput);
        this._moveOptioneInput.node.active = false;
    }

    initialize(newState: SNLRoomState, board : Board, playerNumber : number) {
        console.log("[Player] Initializing Player : ", this.playerNumber);
        this._board = board;
        this.playerNumber = playerNumber;
        var myProfileData = newState.playersData.find(p => p.playerNumber == this.playerNumber);
        this.playerData = myProfileData;
        console.log("[Player] My Profile Data : ", myProfileData);
        if(myProfileData == null){
            return;
        }
        if(myProfileData.sessionId == NetworkManager.Instance.CurrentRoom.sessionId) {
            console.log("Setting Local Player");
            this._isLocal = true;
            this.timer.setLocal(this._isLocal);
        }
        this.loadProfile(myProfileData);
        this._myBasesParent = board.getPlayerBase(this.playerNumber);
        const numberOfPawns = newState.gameOptions.piecesPerPlayer;
        // this.createBases(numberOfPawns);
        this.createPawns(numberOfPawns);
        
        this._gameOptions = newState.gameOptions;
        this.setDiceBgColor();
        this.setBorderAndTimerColor();
        this.outComesUIManager.setPlayer(this);
    }

    loadProfile(playerProfileData : BasePlayerData) {
        if(this._isProfileInitialized) return;
        if(playerProfileData == null) return;
        let playerName = playerProfileData.name;
        if(this._isLocal) {
            playerName = "You"
        }
        this.playerName.string = this.truncateName(playerName, 8);
        this.profileImageUrl = playerProfileData.avatar;
        
        this._isInitialized = true;
        
        this.loadProfileImage();
        this.profileImage.node.on(Node.EventType.TOUCH_START, this.openProfile, this);
    }

