        //             const texture = new Texture2D();
        //             texture.image = imageAsset;
        //             spriteFrame.texture = texture;
        //             this.profileImage.spriteFrame = spriteFrame;
        //         } catch (e) {
        //             console.error(`[Player] Error creating sprite frame: ${e}`);
