import { _decorator, Component, Label, Node, Tween, tween, Vec3 } from 'cc';
import { Menu } from './Menu';
import { Button } from 'cc';
import { PageView } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('HowToPlay')
export class HowToPlay extends Menu {
    @property({type: Button})
    nextButton : Button;
    @property({type: PageView})
    pageView : PageView;
    protected onLoad(): void {
        super.onLoad();
    }

    protected onDestroy(): void {
    }
    public Open(): void {
        super.Open();
    }
    public Close(): void {
        super.Close();
    } 
    public initHandTween() {

    }

    onNextButtonClick() {
        this.log("Next button clicked");
        this.pageView.scrollToPage(1);
    }
}

