import { SpriteFrame } from 'cc';
import { Prefab } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { SNLTileType } from './type/SNLTileType';
import { Color } from 'cc';
import { CCFloat } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PlayerAssets')
//TODO: Add the player assets here
export class PlayerAssets extends Component {
    static instance: PlayerAssets = null;

    @property({type: SpriteFrame})
    pawnSprites : SpriteFrame[] = [];
    @property({type: Color})
    pawnColors : Color[] = [];
    @property({type: Color})
    DiceBgColors : Color[] = [];
    @property({type: Prefab})
    pawnPrefab : Prefab = null;
    @property({type: CCFloat})
    pawnWidth : number = 50;
    @property({type: Prefab})
    basePrefab : Prefab = null;
    @property({type: SpriteFrame})
    diceOutComeSprites : SpriteFrame[] = [];
    @property({type: SpriteFrame})
    diceOutComesStackSprites : SpriteFrame[] = [];
    @property({type: SpriteFrame})
    goldenDiceOutComeSprite : SpriteFrame = null;
    @property({type: Prefab})
    moveOptionsUI : Prefab = null;
    @property({type : [SpriteFrame]})
    powerUpSprites : SpriteFrame[] = [];
    @property({type : Prefab})
    powerUpPrefab : Prefab = null;
    @property({type : [Prefab]})
    snakePrefabs : Prefab[] = [];
    @property({type : [Prefab]})
    ladderPrefabs : Prefab[] = [];

    protected onLoad(): void {
        PlayerAssets.instance = this;
    }

    public getPawnSprite(playerNumber: number) {
        // this.log("PLAYER PAWN SPRITE: " + playerNumber);
        return this.pawnSprites[playerNumber];
    }

    // public getProfilePicHolderSprite(playerNumber: number) {
    //     return this.profilePicHolderSprites[playerNumber];
    // }
    // public getProfileNamePlateSprite(playerNumber: number) {
    //     return this.profileNamePlateSprite[playerNumber];
    // }
        // public getHomeBaseSprite(playerNumber: number) {
        //     return this.homeBaseSprite[playerNumber];
        // }
    public getDiceOutComeSprite(outCome: number) {
        return this.diceOutComeSprites[outCome];
    }

    public getDiceOutComesStackSprite(outCome: number) {
        return this.diceOutComesStackSprites[outCome];
    }

    public getGoldenDiceOutComeSprite() {
        return this.goldenDiceOutComeSprite;
    }

    public getPawnWidth() : number {
        return this.pawnWidth;
    }

    public getPowerUpSprite(powerUpType: number) {
        let isShield = powerUpType & SNLTileType.SHIELD;
        let isRocket = powerUpType & SNLTileType.ROCKET;

        if(isShield) return this.powerUpSprites[0];
        if(isRocket) return this.powerUpSprites[1];
        
    }

    getPawnColor(playerNumber: number) : Color {
        return this.pawnColors[playerNumber];
    }

    getDiceBgColor(playerNumber: number) : Color {
        return Color.WHITE.clone();//this.DiceBgColors[playerNumber];
    }
}


