//Vec3 extensions::
import { Vec3 } from "cc";
function clampMagnitude(this: Vec3, min: number, max: number) : Vec3{
    if(min > max){
        console.error("min should be less than max");
        return;
    }
    const magnitude = this.length();
    if(magnitude > max){
        this.normalize();
        this.multiplyScalar(max);
    }
    else if(magnitude < min){
        this.normalize();
        this.multiplyScalar(min);
    }
    return this;
}
declare module 'cc' {
    interface Vec3 {
        clampMagnitude(min : number, max: number): Vec3;
    }
  }

Vec3.prototype.clampMagnitude = clampMagnitude;

//converct point to world position::
import {Node } from "cc";

function transformPoint(this: Node) : Vec3{
    return this.worldPosition.clone();
}

//add extension function to component to print the name of the component
import { Component } from "cc";
import { PBLogger } from "./PBLogger";

declare module 'cc' {
    interface Component {
        log(...params: any[]): void;
    }
}

Component.prototype.log = function(...params: any[]): void {
    const componentName = this.constructor.name;
    PBLogger.info(`${componentName} `, ...params);
};
