import { _decorator, Component, Node, RichText, EventTarget, Vec3, tween, Prefab, Label, Sprite, SpriteFrame} from 'cc';
import { Menu } from './Menu';
import { AudioManager } from '../../scripts/AudioManager';
import { ServerRoomStateChangeEvent } from '../../pulse/networking/NetworkManager';
import { NetworkEvents } from '../../pulse/types/NetworkTypes';
import { UIManager } from './UIManager';
import { SNLRoomState } from '../../scripts/schema/SNLRoomState';

const { ccclass, property } = _decorator;

@ccclass('InGameMenu')
export class InGameMenu extends Menu {
    @property({type: SpriteFrame})
    public soundOnSprite: SpriteFrame = null;
    @property({type: SpriteFrame})
    public soundOffSprite: SpriteFrame = null;
    @property({type: Sprite})
    public soundButton: Sprite = null;
    @property({type: Node})
    public faqButton: Node = null;
    protected onLoad(): void {
        super.onLoad();
        ServerRoomStateChangeEvent.on(NetworkEvents.SERVER_ROOM_STATE_CHANGE, this.ServerStateChanged, this);
    }
    protected onDestroy(): void {
        ServerRoomStateChangeEvent.off(NetworkEvents.SERVER_ROOM_STATE_CHANGE, this.ServerStateChanged, this);
    }
    ServerStateChanged(newServerState: SNLRoomState) {
        // const serverState = new SNLRoomState();
        // serverState.CopyFrom(newServerState);
        // serverState.gameOptions.show_mute_button
        this.soundButton.node.active = newServerState.gameOptions.show_mute_button;
    }
    protected start(): void {
    }
    public Open(): void {
        console.log("InGameMenu open called");
        super.Open();
        this.initialize();
    }
    public Close(): void {
        super.Close();
    }
    public initialize() : void {
        super.initialize();
        this.UpdateAudioSprite();
        this.soundButton.node.active = UIManager.instance.showMuteButton;
        this.faqButton.active = UIManager.instance.showFAQ;
    }
    public UpdateAudioSprite() {
        if(AudioManager.instance.IsAudioEnabled == 'true') {
            this.soundButton.spriteFrame = this.soundOnSprite;
        }
        else {
            this.soundButton.spriteFrame = this.soundOffSprite;
        }
    }
    public ToggleSound() {
        AudioManager.instance.ToggleAudio();
        this.UpdateAudioSprite();
    }

    
}



