import { UITransform } from 'cc';
import { _decorator, Component, director, Director, Node } from 'cc';
import { CameraManger } from './CameraManger';
import { Widget } from 'cc';
import { CCBoolean } from 'cc';
import { screen } from 'cc';
import { view } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('FitToScreen')
export class FitToScreen extends Component {
    @property({type: CCBoolean})
    updateWidgetsRecursively: boolean;
    
    protected onEnable(): void {
        // Also refresh after one frame to ensure layout is updated
        director.once(Director.EVENT_AFTER_UPDATE, () => {
            this.refresh();
        });
    }
    // Call this method to set the size of the UI element to the screen size
    setUiTransformToScreenSize(){
        const myUiTransform = this.node.getComponent(UITransform);
        const orthoHeightFactor = CameraManger.instance.getCurrentOrthoHeight() / CameraManger.instance.defaultOrthoHeight;
        const windowWidth = screen.windowSize.width || window.innerWidth;
        const windowHeight = screen.windowSize.height || window.innerHeight;
        const height = CameraManger.instance.designHeight * orthoHeightFactor;
        const width = height * windowWidth / windowHeight;
        
        console.log("windowWidth", windowWidth, "windowHeight", windowHeight);
        myUiTransform.setContentSize(width, height);
    }
    
    // Call this method to refresh the size of the UI element
    public refresh(): void {
        this.setUiTransformToScreenSize();
        if(this.updateWidgetsRecursively){
            this.updateWidgetsInHierarchy();
        }
    }

    public updateWidgetsInHierarchy(){
        // Recursively update all descendant widgets
        this.updateWidgetAndDescendants(this.node);
    }

    private updateWidgetAndDescendants(node: Node) {
        // Update this node's widget if it has one
        const widget = node.getComponent(Widget);
        if (widget && this.node.uuid != node.uuid) {
            widget.updateAlignment();
        }
        
        // Recursively update all children
        node.children.forEach(child => {
            this.updateWidgetAndDescendants(child);
        });
    }
}

