import { Tween, Node, tween, Vec3, math, Color, instantiate, Prefab } from "cc";
import { Piece } from "./Piece";
import { SNLRoomState } from "./schema/SNLRoomState";
import { Board } from "./Board";
import { SNLTileType } from "./type/SNLTileType";
import { GameMessageType } from "./type/GameMessageType";
import { NetworkManager } from "../pulse/networking/NetworkManager";
import { PowerUp } from "./PowerUp";
import { AudioManager } from "./AudioManager";
import { Player } from "./Player";
import { SNLGameHandler } from "./SNLGameHandler";
import { AnimationTag } from "./type/AnimationTag";
import { AnimationPoints } from "./AnimationPoints";
import { GetEndTile, LadderPreset, LadderPresets, SnakePreset, SnakePresets } from "./Config/BoardData";
import { Snake } from "./Board";
import { UIManager } from "../UIPack/Scripts/UIManager";
import { SNLSnakeData } from './schema/SNLBoard';
import { PlayerAssets } from './PlayerAssets';
import { DeathPopup } from './DeathPopup';
import { DeathType } from "./type/DeathType";
import { PlayerService } from "./PlayerService";

const enum PawnHandlerState {
    IDLE,
    MOVING,
}
export class PawnHandler {
    player : Player;
    pawns: Piece[];
    state = PawnHandlerState.IDLE;
    moveDataQueue = [];
    pawnAnimation : Tween<Node> = null;
    currentHandlingPieceIndex = -1;
    currentHandlingMoveData = null;
    // currentRunningAnimations : Tween<Node>[] = [];
    pieceAnimationsTag = AnimationTag.PIECE_MOVE;
    hasKilledInThisMove = false;
    constructor(pawns: Piece[], player : Player) {
        this.pawns = pawns;
        this.player = player;
    }

    setState(state: PawnHandlerState) {
        this.state = state;
    }

    onPawnMoveAnimationComplete() {
        Board.Instance.updateAllPiecePositions();
        // this.handleData();
    }

    handleData() {
        this.hasKilledInThisMove = false;
        if(this.moveDataQueue.length == 0){
            if(this.state == PawnHandlerState.MOVING) {
                this.setState(PawnHandlerState.IDLE);
                this.StartSelectAnimationsForValidMovePieces();
            }
            return;
        }
        this.StopPieceSelectAnimations();
        const data = this.moveDataQueue.shift();
        this.setState(PawnHandlerState.MOVING);
        this.player.outComesUIManager.removeOutCome(data.rollValue);
        this.currentHandlingPieceIndex = data.pieceIndex;
        this.currentHandlingMoveData = data;
        console.log("Handling Move Data: ", "piece ", data.pieceIndex, "with roll value ", data.rollValue);
        this.pawnAnimation = this.pawns[data.pieceIndex].move(data.rollValue, this.onPieceLandedOnTargetTile.bind(this));
        this.pawnAnimation.tag(this.pieceAnimationsTag);
        this.pawnAnimation.start();
    }

    onPieceLandedOnTargetTile(){
        console.log("Moved Piece to Target Tile: ", this.pawns[this.currentHandlingPieceIndex].pathNumber);
        let killAnimation = this.killOpponentPieces(this.pawns[this.currentHandlingPieceIndex]);
        if(killAnimation != null) {
            
            killAnimation.call(() => {
                this.onPieceLandedOnTargetTile();
            });
            killAnimation.start();
            return;
        }

        
        let PowerupAnimation = this.handlePowerUps(this.pawns[this.currentHandlingPieceIndex]);
        if(PowerupAnimation != null) {
            AudioManager.instance.playSound("Power");
            PowerupAnimation.start();
            return;
        }
        let SnakeAnimation = this.handleSnake(this.pawns[this.currentHandlingPieceIndex]);
        if(SnakeAnimation != null) {
            SnakeAnimation.start();
            return;
        }
        let LadderAnimation = this.handleLadder(this.pawns[this.currentHandlingPieceIndex]);
        if(LadderAnimation != null) {
            LadderAnimation.start();
            return;
        }
        this.sendMoveAck();
        this.onPawnMoveAnimationComplete();
    }

    handlePowerUps(piece: Piece): Tween<Node> | null {
        const landedPowerupType: SNLTileType = Board.Instance.getPowerUpType(piece);
    
        switch (landedPowerupType) {
            case SNLTileType.ROCKET:
                console.log("[PawnHandler] Landed on Rocket Powerup");
                return this.getRocketAnimation(piece);
    
            case SNLTileType.SHIELD:
                console.log("[PawnHandler] Landed on Shield Powerup");
                return this.getShieldAnimation(piece);
    
            default:
                console.warn(`[PawnHandler] Unhandled PowerUp Type: ${SNLTileType[landedPowerupType]}`);
                return null;
        }
    }

    handleSnake(piece: Piece): Tween<Node> | null {
        // Early return if not on snake tile
        if ((Board.Instance.tileTypes[piece.pathNumber] & SNLTileType.SNAKE) === 0) {
            return null;
        }
    
        console.log("---------------------Landed on Snake");
    
        // Handle shield protection
        if (piece.hasShield) {
            console.log("[PawnHandler] Piece has Shield. Skipping Snake Animation");
            this.handleShield(piece, false);
            return null;
        }
    
        // Find snake at current position
        const currentBitingSnake = Board.Instance.snakesData.find(snake => snake.startTile === piece.pathNumber);
        if (!currentBitingSnake) {
            console.error("Couldn't Find Snake. Check Board Preset Data");
            return null;
        }
    
        // Get snake movement data
        const bitingSnakePreset = SnakePresets[currentBitingSnake.snakePresetIndex];
        const endTile = GetEndTile(
            currentBitingSnake.startTile,
            bitingSnakePreset.offset.x,
            bitingSnakePreset.offset.y,
            Board.Instance.gameOptions.boardTilesCount,
            Board.Instance.gameOptions.boardTilesPerRow
        );
    
        // Calculate final position and get animation points
        const finalPosition = Board.Instance.getPathTile(
            endTile, 
            piece.playerNumber, 
            piece.pieceNumber
        ).worldPosition.clone();
        
        const pieceOwnerName = this.player.playerData.name;
        const pieceOwnerTruncatedName = this.player.truncateName(pieceOwnerName, 8);
        const snakeBiteMessage = `<color=#FF0000><b>${pieceOwnerTruncatedName}</b> got bitten</color>`;

        const snake = Board.Instance.getSnake(currentBitingSnake.snakePresetIndex);
        if (!snake) {
            console.error("Couldn't Find Snake. Check Board Preset Data");
            return null;
        }
    
        // Apply shield and return animation
        this.handleShield(piece, false);
        
        // Show death popup at the snake bite position
        DeathPopup.showDeathPopup(piece.node.worldPosition.clone(), snakeBiteMessage, DeathType.SNAKE_BITE);
        
        return this.getSnakeAnimationFromPoints(piece, finalPosition, snake, endTile, bitingSnakePreset);
    }

    handleLadder(piece: Piece) : Tween<Node>{
        const isLadder = (Board.Instance.tileTypes[piece.pathNumber] &  SNLTileType.LADDER) != 0;
        if(!isLadder) return null;
        console.log("--------------------- Landed on Ladder");
        const ladders = Board.Instance.ladders;
        const currentLadder = ladders.find((ladder) => {
            return ladder.startTile == piece.pathNumber;
        });
        if(currentLadder == null) {
            console.error("Couldnt FInd Ladder. Check Board Preset Data");
            return null;
        }
        const ladderPreset = LadderPresets[currentLadder.ladderPresetIndex];
        const endTile = GetEndTile(currentLadder.startTile, ladderPreset.offset.x, ladderPreset.offset.y, Board.Instance.gameOptions.boardTilesCount, Board.Instance.gameOptions.boardTilesPerRow);
        const finalPosition = Board.Instance.getPathTile(endTile, piece.playerNumber, piece.pieceNumber).worldPosition.clone();
        
        console.log("[Ladder] Final Position: ", finalPosition);
        return this.getLadderMoveAnimation(piece, piece.node.worldPosition.clone(), finalPosition, endTile, ladderPreset);
    }

    getSnakeAnimationFromPoints(piece: Piece, endPosition: Vec3, snake: Snake, endTile: number, snakePreset: SnakePreset) : Tween<Node>{
        const points = snake.animationPoints.points;
        const timePerTile = Board.Instance.gameOptions.snakeAnimationTimePerTile;
        const pawnScaleDuration = 0.25; // Faster scale down
        const totalAnimationDuration = snakePreset.offset.length * timePerTile;
        const pieceMovingDuration = totalAnimationDuration * 0.9; // Slightly faster movement to make animation feel more snappy
        const pieceSpeed = snakePreset.offset.length / pieceMovingDuration;
        
        let animation = tween(piece.node);
        
        // Add 1 second delay before starting snake animation
        animation.delay(1);
        
        // Start with a slight bounce before being swallowed
        animation.call(() => {
            if(this.player._isLocal) {
                console.log("[PawnHandler] Playing Flash Effect");
                UIManager.instance.playFlashEffect();
            }
            // Bounce effect before being swallowed
            tween(piece.node)
                .to(0.1, { scale: piece.node.scale.clone().multiplyScalar(1.2) })
                .to(0.1, { scale: piece.node.scale.clone().multiplyScalar(0.9) })
                .start();
        });
        
        // Short delay before scaling down to simulate snake preparing to swallow
        animation.delay(0.2);
        
        // Scale down piece with a wiggle effect
        animation.call(() => {
            AudioManager.instance.playSound("ChompSnakeBite");
            piece.scaleDownPieceWithWiggle(pawnScaleDuration).start();
        });
        animation.call(() => {
            this.AnimateSnakeHead(snake.node).start();
        });
        // Short delay after piece disappears
        animation.delay(pawnScaleDuration + 0.1);
        
        // Show and animate the snake bulge
        animation.call(() => {
            AudioManager.instance.stopSound("ChompSnakeBite");
            AudioManager.instance.playSound("SnakeBite");
            piece.animateSnakeBulge(pieceMovingDuration).start();
        });
        
        var midPoint = Math.floor(points.length/2);
        // Move along the path points
        for(let i=0; i<points.length-1; i++) {
            let duration = Vec3.distance(points[i].worldPosition, points[i+1].worldPosition) / pieceSpeed;
            animation.to(duration, { worldPosition: points[i+1].worldPosition });
            if(i == midPoint) {
                
            }
        }
        
        // Final movement to end position
        let duration = Vec3.distance(points[points.length-1].worldPosition, endPosition) / pieceSpeed;
        animation.to(duration, { worldPosition: endPosition });
        
        // Pop the piece back out with a bounce effect
        animation.call(() => {
            piece.scaleUpPieceWithBounce(pawnScaleDuration).start();
            // AudioManager.instance.playSound("Kill");
        });
        
        animation.delay(pawnScaleDuration + 0.1);
        
        return animation.call(() => {
            piece.setPathNumber(endTile);
            piece.updateParentNode();
            this.onPieceLandedOnTargetTile();
        });
    }

    getDistanceBetweenTwoTiles(startTile: number, endTile: number) : number {
        const startRow = Math.floor(startTile / Board.Instance.gameOptions.boardTilesPerRow);
        const endRow = Math.floor(endTile / Board.Instance.gameOptions.boardTilesPerRow);
        const startColumn = startTile % Board.Instance.gameOptions.boardTilesPerRow;
        const endColumn = endTile % Board.Instance.gameOptions.boardTilesPerRow;
        return Math.sqrt(Math.pow(startRow - endRow, 2) + Math.pow(startColumn - endColumn, 2));
    }

    getRocketAnimation(piece : Piece){
        let nextLandingPathNumber = Board.Instance.getRocketLandingPathNumber(piece);
        console.log("Rocket Landing Path Number: ", nextLandingPathNumber);
        let rocketPowerup : Node = Board.Instance.getPowerupNode(piece);
        Board.Instance.removePowerUp(rocketPowerup.getComponent(PowerUp));
        piece.setPathNumber(nextLandingPathNumber);
        piece.updateParentNode();
        let nextLandingPosition = Board.Instance.getPathTile(nextLandingPathNumber, piece.playerNumber, piece.pieceNumber).worldPosition.clone();
        let duration = (Vec3.distance(piece.node.worldPosition, nextLandingPosition) / 100) * .15;
        let direction = nextLandingPosition.clone().subtract(piece.node.worldPosition).normalize();
        duration = Math.max(duration, .3);
        let animation = tween(piece.node).call(() => {
            piece.playPropellerAnimation(direction);
        })
        .to(duration, { worldPosition: nextLandingPosition });
        animation.call(() => {
            // piece.updatePiecePosition(nextLandingPathNumber);
            piece.stopPropellerAnimation();
            this.onPieceLandedOnTargetTile();
        });
        animation.tag(this.pieceAnimationsTag);
        return animation;
    }

    handleShield(piece: Piece, active: boolean) {
        console.log("[PawnHandler] Handling Shield for Piece: ", piece.pieceNumber, "with active: ", active);
        piece.setShieldActive(active);
    }
    
    getShieldAnimation(piece: Piece) : Tween<Node>{
        piece.setShieldActive(true);
        return null;
    }
    

    getLadderMoveAnimation(piece: Piece, startPosition: Vec3, endPosition: Vec3, endTile: number, ladderPreset: LadderPreset) : Tween<Node>{
        const startTile = piece.pathNumber;
        const totalTime = ladderPreset.offset.length * Board.Instance.gameOptions.ladderAnimationTimePerTile;
        console.log("[Ladder] Distance: ", ladderPreset.offset.length, "totalTime: ", totalTime);
        // let midPosition : Vec3 = startPosition.clone().add(endPosition).multiplyScalar(0.5);
        let startScale = piece.node.scale.clone();
        let animation = tween(piece.node)
        .call(() => {
            if(this.player._isLocal) {
                UIManager.instance.playFlashEffect(new Color(15, 156, 4));
            }
            AudioManager.instance.playSound("Ladder");
            piece.node.setWorldPosition(startPosition);
        });
        // animation.to(totalTime/2, { worldPosition: midPosition, scale : startScale.clone().multiplyScalar(1.5)});
        animation.to(totalTime, { worldPosition: endPosition, scale : startScale});
        animation.call(() => {
            console.log("[Ladder] Climb Animation complete");
            piece.setPathNumber(endTile);
            piece.updateParentNode();
            this.onPieceLandedOnTargetTile();
        });
        return animation;
    }

    killOpponentPieces(myPiece: Piece) : Tween<Node> {
        let opponentPiece = Board.Instance.getKillablePieces(myPiece)
        if(opponentPiece == null) return;
        console.log("Killing Opponent Piece: ", "piece ", opponentPiece.pieceNumber , "of player ", opponentPiece.playerNumber);
        const pieceOwnerName = this.player.playerData.name;
        const pieceOwnerTruncatedName = this.player.truncateName(pieceOwnerName, 8);
        const opponentPiecePlayerNumber = opponentPiece.playerNumber;
        const opponentPlayer = PlayerService.instance.getPlayer(opponentPiecePlayerNumber);
        const opponentPieceOwnerName = opponentPlayer.playerData.name;
        const opponentPieceOwnerTruncatedName = opponentPlayer.truncateName(opponentPieceOwnerName, 8);
        const killMessage = `<color=#FF0000><b>${pieceOwnerTruncatedName}</b> killed <b>${opponentPieceOwnerTruncatedName}!</b></color>`;
        // Show death popup for the killed piece
        DeathPopup.showDeathPopup(opponentPiece.node.worldPosition.clone(), killMessage, DeathType.PAWN_KILL);
        
        let pathPositions = Board.Instance.getPathPositions(opponentPiece.pathNumber, 0, opponentPiece.playerNumber, opponentPiece.pieceNumber);
        // let animation = opponentPiece.moveToTargetPathNumber(0, null, durationPerTile);
        opponentPiece.setPathNumber(0);
        opponentPiece.updateParentNode();
        opponentPiece.updatePieceScale();
        let durationPerTile = .15;//opponentPiece.previousPathNumber
        let totalDuration = math.clamp((Vec3.distance(opponentPiece.node.worldPosition, pathPositions[pathPositions.length-1]) / 50) * durationPerTile, 0.3, 1);
        console.log("kill animation duration per tile: ", totalDuration);
        this.handleShield(opponentPiece, false);
        let animation = tween(opponentPiece.node);
        
        // Add 1 second delay before starting kill animation
        animation.delay(1);
        
        animation.call(() => {
            opponentPiece.onKilled();
            opponentPiece.node.setWorldPosition(pathPositions[0]);
        });
        animation.to(totalDuration, { worldPosition: pathPositions[pathPositions.length-1]},
            {
                progress: (start: number, end: number, current: number, ratio: number): number => {
                    return math.lerp(start, end, this.easeOutBack(ratio));
                }
            }
        );
        animation.call(() => {
            // opponentPiece.updatePiecePosition();
        });
        this.hasKilledInThisMove = true;
        return animation;
    }
     easeOutBack(x: number): number {
        const c1 = 1.70158 * 1.5;
        const c3 = c1 + 1;
        
        return 1 + c3 * Math.pow(x - 1, 3) + c1 * Math.pow(x - 1, 2);
    }

    movePiece(pieceIndex: number, rollValue: number, sendAck: boolean) {
        // console.log("Pushing Move Data to Queue: ", "piece ", pieceIndex, "with roll value ", rollValue);
        this.moveDataQueue.push({pieceIndex, rollValue, sendAck});
        if(this.state == PawnHandlerState.IDLE) {
            this.handleData();
        }
    }

    updateShields(serverState: SNLRoomState) {
        for(let i=0; i<this.pawns.length; i++) {
            this.handleShield(this.pawns[i], serverState.playersData[this.player.playerNumber].hasShield[i]);
        }
    }

    Reset(serverState: SNLRoomState) {
        this.stopAllAnimations();
        this.moveDataQueue = [];
        this.setState(PawnHandlerState.IDLE);
        this.ResetPawnPositions(serverState);
        this.updateShields(serverState);
        Board.Instance.reset(serverState);
    }

    ResetPawnPositions(serverState: SNLRoomState) {
        const playerData = serverState.playersData[this.player.playerNumber];
        for(let j = 0; j < playerData.pieces.length; j++) {
            //kill all the tween animations:
            this.pawns[j].reset(playerData.pieces[j]);
        }
        console.log("Reset Pawn Positions of Player: ", this.player.playerNumber);
    }

    stopAllAnimations(){
        this.StopPieceSelectAnimations();
        Tween.stopAllByTag(this.pieceAnimationsTag);
    }

    sendMoveAck() {
        NetworkManager.Instance.CurrentRoom.send(GameMessageType.MOVE_PIECE_ACK, JSON.stringify({}));
    }

    isAnimating(){
        return this.state == PawnHandlerState.MOVING;
    }

    StartSelectAnimationsForValidMovePieces(){
        if(!this.player._isLocal) return;
        let validPieces = this.player._stateMachine.movePieceState.getAllMovablePieces();
        for(let i=0; i<validPieces.length; i++) {
            validPieces[i].playSelectAnimation();
        }
    }

    StopPieceSelectAnimations(){
        if(!this.player._isLocal) return;
        for(let i=0; i<this.pawns.length; i++) {
            this.pawns[i].stopSelectAnimation();
        }
    }

    AnimateSnakeHead(snakeNode: Node) : Tween<Node>{
        //Search for "SnakeHead" in the children of the snakeNode
        let snakeHead = snakeNode.children[0].getChildByName("SnakeHead");
        if(snakeHead == null) return;
        console.log("Animating Snake Head: ", snakeHead);
        //Stretch animation like swallowing - expand Y and squish X
        let animation = tween(snakeHead);
        let originalScale = snakeHead.scale.clone();
        animation.to(0.15, { 
            scale: new Vec3(
                originalScale.x * 0.6,  // Squish in X direction
                originalScale.y * 1.8,  // Stretch in Y direction 
                originalScale.z
            )
        });
        animation.to(0.2, { 
            scale: new Vec3(
                originalScale.x * 0.85, // Start returning to normal
                originalScale.y * 1.3,
                originalScale.z
            )
        });
        animation.to(0.15, { scale: originalScale }); // Return to original scale
        return animation;
    }
}