import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { PlayerAssets } from './PlayerAssets';
import { UITransform } from 'cc';
import { Vec3 } from 'cc';
import { CameraManger } from '../pulse/pb-cocos-utils/CameraManger';
const { ccclass, property } = _decorator;


@ccclass('MoveOptionsInput')
export class MoveOptionsInput extends Component {
    @property({type: Sprite})
    firstOutComeSprite : Sprite = null;
    @property({type: Sprite})
    secondOutComeSprite : Sprite = null;
    @property({type: Sprite})
    thirdOutComeSprite : Sprite = null;
    outComeValues : number[] = [6, 6, 6];
    @property({type: Node})
    pointer : Node = null;
    outComeClickedCallback : Function = null;
    uiTransform : UITransform   = null;
    @property({type: UITransform})
    optionsUITransform : UITransform = null;
    protected onLoad(): void {
        this.uiTransform = this.node.getComponent(UITransform);
    }

    protected onEnable(): void {
        this.firstOutComeSprite.node.on(Node.EventType.TOUCH_START, this.onFirstOutComeClick, this);
        this.secondOutComeSprite.node.on(Node.EventType.TOUCH_START, this.onSecondOutComeClick, this);
        this.thirdOutComeSprite.node.on(Node.EventType.TOUCH_START, this.onThirdOutComeClick, this);
    }

    protected onDisable(): void {
        this.firstOutComeSprite.node.off(Node.EventType.TOUCH_START, this.onFirstOutComeClick, this);
        this.secondOutComeSprite.node.off(Node.EventType.TOUCH_START, this.onSecondOutComeClick, this);
        this.thirdOutComeSprite.node.on(Node.EventType.TOUCH_START, this.onThirdOutComeClick, this);
    }

    setOutComeSprites(outComeValues : number[]){
        // this.secondOutComeValue = value;
        this.outComeValues = outComeValues;

        if(outComeValues.length >= 1)
        this.firstOutComeSprite.spriteFrame = PlayerAssets.instance.getDiceOutComesStackSprite(this.outComeValues[0]);

        if(outComeValues.length >= 2){
            console.log("Setting Second OutCome");
            this.secondOutComeSprite.node.active = true;
            this.secondOutComeSprite.spriteFrame = PlayerAssets.instance.getDiceOutComesStackSprite(this.outComeValues[1]);
        }
        else
        {
            this.secondOutComeSprite.node.active = false;
        }
        
        
        if(outComeValues.length >= 3){
            console.log("Setting Third OutCome");
            this.thirdOutComeSprite.node.active = true;
            this.thirdOutComeSprite.spriteFrame = PlayerAssets.instance.getDiceOutComesStackSprite(this.outComeValues[2]);
        }
        else{
            this.thirdOutComeSprite.node.active = false;
        }
    }

    private onFirstOutComeClick(){
        // this.log('First Outcome Clicked');
        if(this.outComeClickedCallback == null) return;
        this.outComeClickedCallback(this.outComeValues[0]);
    }

    private onSecondOutComeClick(){
        // this.log('Second Outcome Clicked');
        if(this.outComeClickedCallback == null) return;
        this.outComeClickedCallback(this.outComeValues[1]);
    }

    private onThirdOutComeClick(){
        // this.log('Third Outcome Clicked');
        if(this.outComeClickedCallback == null) return;
        this.outComeClickedCallback(this.outComeValues[2]);
    }

    SetPosition(worldPosition : Vec3){
        this.node.setWorldPosition(worldPosition);
        let screenWidth = CameraManger.instance.getWidth();
        let localOptionsPos = this.optionsUITransform.node.position;
        localOptionsPos.set(0,7.5,0);
        console.log("setting worldPosintion : ", worldPosition);
        let myXleftBound = worldPosition.x - this.optionsUITransform.width * .5;
        let myXRightBound = worldPosition.x + this.optionsUITransform.width * .5;

        if(myXleftBound < 0){
            console.log("My X Left Bound: ", myXleftBound, "Screen Width: ", screenWidth);
            localOptionsPos.add(new Vec3(this.optionsUITransform.width * .5 - 45,0,0));
        }
        else if(myXRightBound > screenWidth){
            console.log("My X Right Bound: ", myXRightBound, "Screen Width: ", screenWidth);
            localOptionsPos.add(new Vec3(-this.optionsUITransform.width * .5 + 45,0,0));
        }
        this.optionsUITransform.node.setPosition(localOptionsPos);
        
    }

}


