export class SNLSnakeData {
  startTile: number;
  snakePresetIndex: number;
}
export class SNLLadderData {
  startTile: number;
  ladderPresetIndex: number;
}

export class SNLBoard {
  boardTiles: number[] = [];
  snakes: SNLSnakeData[] = [];
  ladders: SNLLadderData[] = [];
  copyFrom(source: SNLBoard): void {
    this.boardTiles = [];
    for (let i = 0; i < source.boardTiles.length; i++) {
      this.boardTiles.push(source.boardTiles[i]);
    }
    this.snakes = [];
    for (let i = 0; i < source.snakes.length; i++) {
      this.snakes.push(source.snakes[i]);
    }
    this.ladders = [];
    for (let i = 0; i < source.ladders.length; i++) {
      this.ladders.push(source.ladders[i]);
    }
  }
}
