import { BasePlayerData } from "./BasePlayerData";
import { RoomStatus } from "../types/RoomTypes";
import { BaseRoomSettings, DefaultRoomSettings } from "./BaseRoomSettings";

/**
 * Base room state class that can be extended by specific games
 * This provides common state properties that all game rooms need
 */
export class BaseRoomState<BRS extends BaseRoomSettings, BPD extends BasePlayerData> {
    // Room status
    currentRoomStatus: RoomStatus = RoomStatus.WAITING_FOR_PLAYERS;
    previousRoomStatus: RoomStatus = RoomStatus.NONE;
    roomSettings: BRS;
    playersData: BPD[];
    spectatorsData: BasePlayerData[];

    constructor(roomSettings: BRS, playersData: BPD[]) {
        this.roomSettings = roomSettings;
        this.playersData = playersData;
        this.spectatorsData = [];
    }  
    
    copyFrom(other: BaseRoomState<BRS, BPD>): void {
        this.currentRoomStatus = other.currentRoomStatus;
        this.previousRoomStatus = other.previousRoomStatus;
        this.roomSettings.copyFrom(other.roomSettings);
    }
} 