import { _decorator, Component, Node, Sprite, SpriteFrame, tween, Tween } from 'cc';
import { AudioManager } from './AudioManager';
import { AnimationTag } from './type/AnimationTag';
const { ccclass, property } = _decorator;

@ccclass('Dice')
export class Dice extends Component {
    @property({ type: [SpriteFrame] })
    diceFaces: SpriteFrame[] = [];
    @property({ type: [SpriteFrame] })
    rollingFrames: SpriteFrame[] = [];
    
    @property({ type: Sprite })
    diceSprite: Sprite = null;

    onDiceRollDone: Function = () => {};

    _currentFace: number = 0;
    _rollingTween: Tween<any> = null;
    _rollDuration: number = 0.5;

    protected onLoad(): void {
        this._rollingTween = tween(this.diceSprite.node);
    }
    rollDice(result: number) {
        AudioManager.instance.playSound("DiceRoll");
        this.rollAnimation(result);
    }
    setDiceFace(face: number) {
        // console.log("Setting Dice Face: ", face);
        if(face < 0 || face >= this.diceFaces.length) return;
        this._currentFace = face;
        this.diceSprite.spriteFrame = this.diceFaces[face];
    }
    rollAnimation(finalFace: number) {
        this.stopRolling();
        
        // console.log("Rolling");
        this._rollingTween = tween({ value: 0 })
            .to(.5, { value: 13 }, {
                onUpdate:((target: { value: number }) => {
                    const index = Math.floor(target.value)%this.rollingFrames.length;
                    this.diceSprite.spriteFrame = this.rollingFrames[index];
                    // Do something with the updated value
                })
            })
            .call(() => {
                this.setDiceFace(finalFace);
                console.log("~~~ Dice Rolling Done ~~~");
                this.onDiceRollDone();
            })
        this._rollingTween.tag(AnimationTag.DICE_ROLL);
        this._rollingTween.start();
    }
    stopRolling() {
        // console.log("STOP ROLLING");
        if(this._rollingTween) this._rollingTween.stop();
    }

    reset(){
        this.diceSprite.spriteFrame = this.diceFaces[0];
    }
}

