import { IBaseRoomSettings } from "../types/RoomTypes";
/**
 * Default room settings that can be overridden by specific games
 */
export const DefaultRoomSettings: IBaseRoomSettings = {
    maxPlayers: 8,
    minPlayers: 2,
    maxClients: 8,
    countdownTimeInSeconds: 3,
    allowLateJoin: true,
    maxReconnectionAttempts: 3,
    reconnectionAttemptInterval: 1000,
    reconnectionTime: 10000,
    gameFPS: 30,
    serverFPS: 30,
    patchRate: 1000
}

export class BaseRoomSettings {
    maxPlayers: number;
    minPlayers: number;
    maxClients: number;
    countdownTimeInSeconds: number;
    allowLateJoin: boolean;
    maxReconnectionAttempts: number;
    reconnectionAttemptInterval: number;
    reconnectionTime: number;
    gameFPS: number;
    serverFPS: number;
    patchRate: number;

    copyFrom(other: BaseRoomSettings): void {
        this.maxPlayers = other.maxPlayers;
        this.minPlayers = other.minPlayers;
        this.maxClients = other.maxClients;
        this.countdownTimeInSeconds = other.countdownTimeInSeconds;
        this.allowLateJoin = other.allowLateJoin;
        this.reconnectionTime = other.reconnectionTime;
        this.gameFPS = other.gameFPS;
        this.serverFPS = other.serverFPS;
        this.patchRate = other.patchRate;
    }
}
