import { Input, Node } from "cc";
import { Player, PlayerState } from "../Player";
import {PlayerBaseState } from "./PlayerBaseState";
import { PlayerStateMachine } from "./PlayerStateMachine";
import { GameMessageType } from "../type/GameMessageType";
import { AudioManager } from "../AudioManager";
import { GameManager } from "../GameManager";
import { NetworkManager } from "../../pulse/networking/NetworkManager";

export class RollingState extends PlayerBaseState{
    stateMachine: PlayerStateMachine
    _stateName : PlayerState = PlayerState.ROLLING;
    player: Player;
    currentDiceOutcome: number = 0;
    goldenDiceOutcomes: number = 0;
    
    constructor(stateMachine: PlayerStateMachine) {
        super();
        this.stateMachine = stateMachine;
        this.player = this.stateMachine.player;
        this.player.dice.onDiceRollDone = this.onDiceRollDone.bind(this);
    }

    enterState(): void {
        console.log("Entered Rolling State");
        this.player.diceRoot.active = true;
        this.player.timer.stop();
        this.player.outComesUIManager.enableOutComesBg(true);
        this.player.timer.setTotalTime(this.stateMachine.currentServerState.gameOptions.diceRollWaitTime);
        this.player.timer.run();
        this.player._pawnHandler.Reset(this.stateMachine.currentServerState);
        this.player.dice.reset();
        const currentPlayerData = this.stateMachine.currentServerState.playersData[this.player.playerNumber];
        this.player.outComesUIManager.reset(currentPlayerData.rollsList);
        if(this.player._isLocal){
            console.log("Setting Local Player Dice Click");
            this.player.dice.node.on(Node.EventType.TOUCH_START, this.OnDiceClick, this);
            this.player.arrowPointToDiceAnimation(true);
            GameManager.Instance.miloSDK.vibratePreset('light');
        }
    }
    
    updateState(deltaTime: number): void {      
    }

    exitState(): void {
        this.player.diceRoot.active = true;
        this.player.timer.stop();
        console.log("Exited Rolling State local ", this.player._isLocal);
        // const rollsList = this.stateMachine.currentServerState.snlPlayersData[this.player.playerNumber].rollsList;
        // this.player.outComesUIManager.reset(rollsList);
        if(this.player._isLocal){
            this.player.dice.node.off(Input.EventType.TOUCH_START, this.OnDiceClick, this);
        }
    }

    OnDiceClick(event: any) {
        console.log("OnDiceClick");
        if(!this.player._isLocal) return;
        console.log("OnDiceClick 2");
        //switch off continues dice clicks spamming::
        this.player.dice.node.off(Input.EventType.TOUCH_START, this.OnDiceClick, this);
        GameManager.Instance.setInput("DiceRoll");
        this.player.arrowPointToDiceAnimation(false);
        console.log("Clicked on Dice")
        NetworkManager.Instance.CurrentRoom.send(GameMessageType.CLIENT_DICE_ROLL, JSON.stringify({}));
    }

    onDiceRollDone() {
        this.player.outComesUIManager.addOutCome(this.currentDiceOutcome);
        NetworkManager.Instance.CurrentRoom.send(GameMessageType.ROLLING_ACK, JSON.stringify({}));
        console.log("Roll Ack Sent");
    }
    
    onDiceOutCome() {
        const json = JSON.parse(this.stateMachine.currentServerState.inputPayload.inputValue);
        this.player.dice.rollDice(json.value);
        this.currentDiceOutcome = json.value;
        console.log("Rolled : ", json.value);
        this.player.timer.stop();
    }

    reset(){
        this.exitState();
    }
    
}