import { _decorator, Camera, Component, Node, Sprite, Color, tween, Vec3, quat } from 'cc';
import { Menu } from './Menu';
import { NetworkEvents } from '../../pulse/types/NetworkTypes';
import { RoomStatus } from '../../pulse/types/RoomTypes';
import { CameraManger } from '../../pulse/pb-cocos-utils/CameraManger';
import { GameManager, RoomStatusChangeEvent } from '../../scripts/GameManager';
import { UITransform } from 'cc';
import { Widget } from 'cc';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { input, Input } from 'cc';
import { KeyCode } from 'cc';
import { EventKeyboard } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('UIManager')
export class UIManager extends Component {

    public static instance: UIManager = null;
    @property({type: Camera})
    public uiCamera : Camera = null;
    @property({type: [Menu]})
    public menus : Menu[] = [];
    @property({type: Node})
    public flashNode : Node = null;
    @property({type: Prefab})
    public showerConfettiPrefab : Prefab = null;
    @property({type: Node})
    public confettiPositionNode : Node = null;

    showMuteButton: boolean = true;
    showResultScreen: boolean = true;
    showFAQ: boolean = true;

    protected onLoad(): void {
        UIManager.instance = this;
        RoomStatusChangeEvent.on(NetworkEvents.ROOM_STATUS_CHANGE, this.onGameStateChange, this);
    }

    protected start(): void {
        this.FixWidthOfUICamera();
    }
    protected onDestroy(): void {
        RoomStatusChangeEvent.off(NetworkEvents.ROOM_STATUS_CHANGE, this.onGameStateChange, this);
    }

    public OpenHowToPlay(){
        this.OpenMenu("HowToPlay");
    }
    
    public OpenMenu(menuName : string) {
        // console.log("Got call to open menu: " + menuName);
        for (let i = 0; i < this.menus.length; i++) {
            // console.log("Menu name: " + this.menus[i].menuName);
            if (this.menus[i].menuName == menuName) {
                this.menus[i].Open();
            }
        }
    }
    public CloseMenu(menuName : string) {
        for (let i = 0; i < this.menus.length; i++) {
            if (this.menus[i].menuName == menuName) {
                this.menus[i].Close();
            }
        }
    }
    public GetMenu(menuName : string) : Menu {
        for (let i = 0; i < this.menus.length; i++) {
            if (this.menus[i].menuName == menuName) {
                return this.menus[i];
            }
        }
        return null;
    }
    public CloseAllMenus() {
        for (let i = 0; i < this.menus.length; i++) {
            this.menus[i].Close();
        }
    }
    
    public onGameStateChange(newState : RoomStatus) {
        console.log("[UIManager] Game State Change: ", newState);
        if(newState == RoomStatus.WAITING_FOR_PLAYERS) {
            console.log("Opening waiting screen");
            this.updateUiBasedOnConfig();
            this.OpenMenu("WaitingScreen");
        }
        else if (newState == RoomStatus.COUNTDOWN) {
            console.log("Opening countdown screen");
            this.CloseAllMenus();
            this.OpenMenu("CountdownScreen");
        }
        else if(newState == RoomStatus.GAME_SIMULATION) {
            this.scheduleOnce(() => {
                this.CloseMenu("CountdownScreen");
                this.OpenMenu("InGameMenu");
            }, 1);
        }
        else if(newState == RoomStatus.END_GAME) {
            console.log("[UIManager] End Game State handling in ui manager");
            this.CloseAllMenus();
            console.log("[UIManager] Opening end round screen");
            this.OpenMenu("RoundCompletedScreen");
        }
        else if(newState == RoomStatus.KICKED_OUT) {
            this.CloseAllMenus();
            this.OpenMenu("KickedOutScreen");
        }
    }

    FixWidthOfUICamera() {
        console.log("Fixing width of UI Camera");
        this.FixCanvasSize();
    }

    FixCanvasSize() {
        let myUiTransform = this.node.getComponent(UITransform);
        myUiTransform.setContentSize(CameraManger.instance.getWidth(), CameraManger.instance.camera.orthoHeight * 2);
        console.log("UI Width: ", myUiTransform.width, "UI Height: ", myUiTransform.height);
        let widget = this.node.getComponent(Widget);
        widget.top = 1920/2 - myUiTransform.height/2;
        widget.bottom = 1920/2 - myUiTransform.height/2;
        widget.updateAlignment();
        let allWidgets = this.node.getComponentsInChildren(Widget);
        for(let i = 0; i < allWidgets.length; i++) {
            allWidgets[i].updateAlignment();
        }
        // this.getAllChildWidgets(this.node);
    }

    getAllChildWidgets(node : Node) {
        let allWidgets = node.getComponentsInChildren(Widget);
        console.log("All widgets: ", allWidgets.length);
    }

    updateUiBasedOnConfig(){
        this.showMuteButton = GameManager.Instance._serverStateCopy.gameOptions.show_mute_button;
        this.showResultScreen = GameManager.Instance._serverStateCopy.gameOptions.show_result_screen;
        this.showFAQ = GameManager.Instance._serverStateCopy.gameOptions.show_faq;
        //initialize all menus:
        for (let i = 0; i < this.menus.length; i++) {
            this.menus[i].initialize();
        }
    }


    playFlashEffect(color : Color = new Color(197, 8, 8)){
        this.flashNode.active = true;
        
        // Get the sprite component
        const sprite = this.flashNode.getComponent(Sprite);
        if (!sprite) {
            console.error("No Sprite component found on flashNode");
            return;
        }
        
        // Set the sprite color to red (#c50808) and initial alpha to 0
        sprite.color = new Color(color.r, color.g, color.b, 0); // Red color #c50808 with 0 alpha
        
        // Create tween animation: fade in to full opacity, then fade out
        tween(sprite)
            .to(0.2, { color: new Color(color.r, color.g, color.b, 255) }) // Fade in quickly to full red
            .delay(0.05)
            .to(0.3, { color: new Color(color.r, color.g, color.b, 0) })   // Fade out slowly
            .call(() => {
                this.flashNode.active = false; // Hide the node when animation completes
            })
            .start();
    }

    showConfetti(){
        let confetti = instantiate(this.showerConfettiPrefab);
        this.confettiPositionNode.addChild(confetti);
        confetti.setPosition(new Vec3(0, 150, 0));
    }

}


