import { BasePlayerData } from "db://assets/SNLGame/pulse/schema/BasePlayerData";

export class SNLPlayerData extends BasePlayerData {
    pieces: number[] = [0, 0];
    rollsList: number[] = [];
    timeLeft: number = 0;
    finishPosition : number = -1;
    isBot : boolean = false;
    hasKilled : boolean = false;
    remainingTurnSkips: number = 0;
    deathsCount: number = 0;
    ladderClimbCount: number = 0;
    hasShield: boolean[] = [false, false];
    copyFrom(source: SNLPlayerData): void {
      super.copyFrom(source);
      this.pieces = [];
      for (let i = 0; i < source.pieces.length; i++) {
        this.pieces.push(source.pieces[i]);
      }
      this.rollsList = [];
      for (let i = 0; i < source.rollsList.length; i++) {
        this.rollsList.push(source.rollsList[i]);
      }
      this.timeLeft = source.timeLeft;
      this.finishPosition = source.finishPosition;
      this.isBot = source.isBot;
      this.hasShield = [];
      for (let i = 0; i < source.hasShield.length; i++) {
        this.hasShield.push(source.hasShield[i]);
      }
      this.hasKilled = source.hasKilled;
      this.remainingTurnSkips = source.remainingTurnSkips;
      this.deathsCount = source.deathsCount;
      this.ladderClimbCount = source.ladderClimbCount;
    }
  }