import { _decorator, Component, instantiate, Node, Prefab } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Pooler')
export class Pooler {
    poolName : string = "NodePool";
    pooledObjects : Node[] = [];
    objectPrefab : Prefab = null;
    pooledObjectCount : number = 0;
    private tempNode : Node = null;
    constructor(poolName : string) {
        this.poolName = poolName;
    }

    getObject() : Node {
        if(this.pooledObjects.length > 0) {
            this.tempNode = this.pooledObjects.pop();
            this.tempNode.active = true;
            // console.log("Returning object from pool " + this.poolName);
            return this.tempNode;
        }
        return this.createObject()
    }

    returnObject(obj : Node) {
        this.pooledObjects.push(obj);
        obj.active = false;
    }

    createObject() : Node {
        this.pooledObjectCount++;
        let obj = instantiate(this.objectPrefab);
        obj.name = this.poolName + " " + this.pooledObjectCount;
        // console.log("Creating new object for pool " + this.poolName);
        return obj;
    }
}


