import { Vec3 } from 'cc';
import { Vec2 } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('AnimationPoints')
export class AnimationPoints extends Component {
    @property([Node])
    points: Node[] = [];

    start() {
        this.log(this.node.name + " totalLength ", this.getTotalLength());
    }
    getTotalLength() : number {
        let totalLength = 0;
        for(let i=0; i<this.points.length-1; i++) {
            totalLength += Vec3.distance(this.points[i].worldPosition, this.points[i+1].worldPosition);
        }
        return totalLength;
    }
}

