import { Node } from 'cc';
import { Piece } from './Piece';
import { Player } from './Player';
import { EventTarget } from 'cc';

export const PlayerServiceEvents = new EventTarget();

export class PlayerService {
    private static _instance: PlayerService = null;
    
    public static get instance(): PlayerService {
        if (!this._instance) {
            this._instance = new PlayerService();
        }
        return this._instance;
    }
    
    private _players: Player[] = [];
    private _localPlayer: Player = null;
    private _myPlayerNumber: number = 0;

    private constructor() {}

    public registerPlayer(player: Player, isLocal: boolean = false): void {
        this._players.push(player);
        if (isLocal) {
            this._localPlayer = player;
        }
    }

    public setMyPlayerNumber(playerNumber: number): void {
        this._myPlayerNumber = playerNumber;
    }

    public getMyPlayerNumber(): number {
        return this._myPlayerNumber;
    }

    public getPlayer(playerNumber: number): Player {
        if (playerNumber === this._myPlayerNumber && this._localPlayer) {
            return this._localPlayer;
        }
        return this._players.find(p => p.playerNumber === playerNumber);
    }

    public getPlayerBase(playerNumber: number): Node {
        const player = this.getPlayer(playerNumber);
        return player ? player.piecesBase : null;
    }

    public getPlayerPiece(playerNumber: number, pieceIndex: number): Piece {
        const player = this.getPlayer(playerNumber);
        return player && player.pawns.length > pieceIndex ? player.pawns[pieceIndex] : null;
    }

    public clearPlayers(): void {
        this._players = [];
        this._localPlayer = null;
    }
} 