import { _decorator, Component, Node } from 'cc';
import { SNLRoomState } from './schema/SNLRoomState';
import { SNLRoomSettings } from './schema/SNLRoomSettings';
import { NetworkManager } from '../pulse/networking/NetworkManager';
import { Vec3 } from 'cc';
import { Piece } from './Piece';
import { SNLTileType } from './type/SNLTileType';
import { PowerUp } from './PowerUp';
import { instantiate } from 'cc';
import { PlayerAssets } from './PlayerAssets';
import { Vec2 } from 'cc';
import { LadderData, SnakeData } from './Config/BoardData';
import { AnimationPoints } from './AnimationPoints';
import { PlayerService } from './PlayerService';
const { ccclass, property } = _decorator;

@ccclass('HomeTiles')
class HomeTiles {
    @property({ type: [Node] })
    homeTiles: Node[] = [];
}
@ccclass('Snake')
export class Snake{
    data : SnakeData;
    animationPoints: AnimationPoints;
    node : Node = null;
    constructor(data: SnakeData, node: Node, animationPoints: AnimationPoints) {
        this.data = data;
        this.node = node;
        this.animationPoints = animationPoints;
    }
}

@ccclass('Board')
export class Board extends Component {
    // Static instance for singleton pattern
    private static _instance: Board = null;
    public static get Instance(): Board {
        return Board._instance;
    }

    // Board properties
    @property({ type: [Node]})
    pathTiles: Node[] = [];
    
    @property({ type: [HomeTiles] })
    homeTiles: HomeTiles[] = [];
    
    @property({ type: Vec2 })
    powerupOffsetFromTileCenter: Vec2 = new Vec2(0, 0);
    
    @property({ type: Node })
    rotationPivot: Node = null;
    
    @property({ type: Node })
    boardSpriteNode: Node = null;
    
    @property({ type: Node })
    snakesAndLaddersParent: Node = null;
    
    @property({ type: Node })
    powerupsParent: Node = null;

    // Game state
    private _initialOffset = 0;
    private _isInitialized = false;
    gameOptions: SNLRoomSettings = null;
    rocketSteps: number = 8;
    tileTypes: number[] = [];
    
    // Game elements
    pieces: Piece[] = [];
    powerUps: PowerUp[] = [];
    snakesData: SnakeData[] = [];
    ladders: LadderData[] = [];
    snakesAnimationPoints: AnimationPoints[] = [];
    laddersAnimationPoints: AnimationPoints[] = [];
    snakes: Snake[] = [];

    protected onLoad(): void {
        if (Board._instance != null) {
            this.node.destroy();
            return;
        }
        Board._instance = this;
        this._initialOffset = 2 * 13;
    }

    protected onDestroy(): void {
        if (Board._instance === this) {
            Board._instance = null;
        }
    }

    // Initialization methods
    public Initialize(state: SNLRoomState): void {
        if (this._isInitialized) return;
        
        this.gameOptions = state.gameOptions;
        const playerData = state.playersData.find(p => p.sessionId == NetworkManager.Instance.getMySessionId());
        if(playerData != null) {
            this.rotationPivot.setRotationFromEuler(0, 0, 180 + this.getPlayerPositionOnBoard(playerData.playerNumber) * 90);
        }
        this._isInitialized = true;
        this.rocketSteps = this.gameOptions.rocketSteps;
        if(playerData != null) {
            console.log("Creating board preset for player ", playerData.playerNumber);
        }
        this.createTiles(state);
        this.createLadders(state);
        this.createSnakes(state);
    }

    public reset(state: SNLRoomState): void {
        this.createTiles(state);
    }

    private createTiles(state: SNLRoomState): void {
        this.tileTypes = [];
        for (let i = 0; i < state.board.boardTiles.length; i++) {
            this.tileTypes.push(state.board.boardTiles[i]);
        }
        this.createPowerups();
    }

    private createSnakes(state: SNLRoomState): void {
        const snakes = state.board.snakes;
        console.log("[Snakes] snakes count: ", snakes.length);
        
        for (let i = 0; i < snakes.length; i++) {
            const snake = snakes[i];
            const snakePrefab = PlayerAssets.instance.snakePrefabs[snake.snakePresetIndex];
            const snakeNode = instantiate(snakePrefab);
            
            snakeNode.setParent(this.snakesAndLaddersParent, true);
            const tile = this.pathTiles[snake.startTile - 1];
            snakeNode.setWorldPosition(tile.worldPosition.clone());
            snakeNode.setScale(1, 1, 1);
            
            this.snakesData.push(snake);
            var animationPoints = snakeNode.getComponent(AnimationPoints);
            this.snakesAnimationPoints.push(animationPoints);
            this.snakes.push(new Snake(snake, snakeNode, animationPoints));
        }
        
        console.log("[Snakes] snakes animation points count: ", this.snakesAnimationPoints.length);
    }

    private createLadders(state: SNLRoomState): void {
        const ladders = state.board.ladders;
        
        for (let i = 0; i < ladders.length; i++) {
            const ladder = ladders[i];
            const ladderPrefab = PlayerAssets.instance.ladderPrefabs[ladder.ladderPresetIndex];
            const ladderNode = instantiate(ladderPrefab);
            
            ladderNode.setParent(this.snakesAndLaddersParent, true);
            const tile = this.pathTiles[ladder.startTile - 1];
            ladderNode.setWorldPosition(tile.worldPosition.clone());
            ladderNode.setScale(1, 1, 1);
            
            this.ladders.push(ladder);
            this.laddersAnimationPoints.push(ladderNode.getComponent(AnimationPoints));
        }
    }

    // Powerup methods
    private createPowerups(): void {
        const newPowerupsArray: PowerUp[] = [];
        
        for (let i = 0; i < this.tileTypes.length; i++) {
            for (let j = 0; j < this.gameOptions.PowerupTypes.length; j++) {
                if (this.tileTypes[i] & this.gameOptions.PowerupTypes[j]) {
                    // Powerup found
                    let oldPowerup = this.popPowerupON(i);
                    if (oldPowerup != null) {
                        newPowerupsArray.push(oldPowerup);
                        break;
                    }
                    
                    let newPowerUp = instantiate(PlayerAssets.instance.powerUpPrefab).getComponent(PowerUp);
                    newPowerUp.node.setParent(this.powerupsParent, true);
                    newPowerUp.node.worldPosition = this.pathTiles[i-1].worldPosition.clone().add(new Vec3(0, -15, 0));
                    newPowerUp.setPathNumber(i);
                    newPowerUp.setPowerUpType(this.tileTypes[i]);
                    newPowerupsArray.push(newPowerUp);
                    break;
                }
            }
        }
        
        // Destroy all old powerups
        for (let i = 0; i < this.powerUps.length; i++) {
            this.powerUps[i].node.destroy();
        }
        
        this.powerUps = newPowerupsArray;
    }

    private popPowerupON(pathNumber: number): PowerUp {
        const index = this.powerUps.findIndex(p => p.pathNumber === pathNumber);
        if (index !== -1) {
            const powerUp = this.powerUps[index];
            this.powerUps.splice(index, 1);
            return powerUp;
        }
        return null;
    }

    public getPowerUpType(piece: Piece): SNLTileType {
        const tileType = this.tileTypes[piece.pathNumber];
        
        for (let i = 0; i < this.gameOptions.PowerupTypes.length; i++) {
            if (tileType & this.gameOptions.PowerupTypes[i]) {
                return this.gameOptions.PowerupTypes[i];
            }
        }
        
        return SNLTileType.DEFAULT;
    }

    public getPowerupNode(piece: Piece): Node {
        const powerUp = this.powerUps.find(p => p.pathNumber === piece.pathNumber);
        return powerUp ? powerUp.node : null;
    }

    public removePowerUp(powerUp: PowerUp): void {
        this.tileTypes[powerUp.pathNumber] = SNLTileType.DEFAULT;
        const index = this.powerUps.indexOf(powerUp);
        
        if (index !== -1) {
            this.powerUps.splice(index, 1);
        }
        
        powerUp.node.destroy();
    }

    public removePowerUpByPathNumber(powerUpPathNumber: number): void {
        this.tileTypes[powerUpPathNumber] = SNLTileType.DEFAULT;
        const index = this.powerUps.findIndex(p => p.pathNumber === powerUpPathNumber);
        
        if (index !== -1) {
            this.powerUps[index].node.destroy();
            this.powerUps.splice(index, 1);
        }
    }

    // Path and tile methods
    public getPathTile(pathNumber: number, playerNumber: number, pieceIndex: number): Node {
        if (pathNumber === 0) {
            const playerBase = this.getPlayerBase(playerNumber);
            return playerBase;
        }
        
        const index = this.getGlobalTileIndex(pathNumber, playerNumber);
        return this.pathTiles[index];
    }

    public getPathPositions(startPathNumber: number, endPathNumber: number, playerNumber: number, pieceIndex: number): Vec3[] {
        const pathPositions: Vec3[] = [];
        let pathTile: Node = null;
        
        if (endPathNumber === 0) {
            for (let i = startPathNumber; i >= 0; i--) {
                pathTile = this.getPathTile(i, playerNumber, pieceIndex);
                if (!pathTile) {
                    console.error("Path tile not found for path number: ", i, " player number: ", playerNumber, " piece index: ", pieceIndex);
                }
                pathPositions.push(pathTile.worldPosition.clone());
            }
            
            console.log("Piece going to home: path positions length ", pathPositions.length, 
                        " start path number: ", startPathNumber, " end path number: ", endPathNumber);
            return pathPositions;
        }
        
        for (let i = startPathNumber; i <= endPathNumber; i++) {
            pathTile = this.getPathTile(i, playerNumber, pieceIndex);
            if (pathTile == null) continue;
            pathPositions.push(pathTile.worldPosition.clone());
        }
        
        return pathPositions;
    }

    public getGlobalTileIndex(piecePosition: number, playerNumber: number): number {
        return piecePosition - 1;
    }

    public isValidPathNumber(pathNumber: number, playerNumber: number): boolean {
        return pathNumber >= 1 && pathNumber <= this.TotalPathCount(this.gameOptions);
    }

    public TotalPathCount(gameOptions: SNLRoomSettings): number {
        return gameOptions.boardTilesCount;
    }

    public isSafeTile(pathNumber: number): boolean {
        const isSafe: number = this.tileTypes[pathNumber] & SNLTileType.SAFE;
        return isSafe > 0;
    }

    // Piece related methods
    public isPathOccupied(pathNumber: number, currentPiece: Piece): boolean {
        if (pathNumber === 0) {
            return false;
        }
        
        return this.pieces.some(p => p.pathNumber === currentPiece.pathNumber && p !== currentPiece);
    }

    public getPawnsCountOnTile(pathNumber: number): number {
        if (pathNumber === 0) return 0;
        return this.pieces.filter(p => p.pathNumber === pathNumber).length;
    }

    public getPawnsOnTile(pathNumber: number): Piece[] {
        if (pathNumber === 0) return [];
        return this.pieces.filter(p => p.pathNumber === pathNumber);
    }

    public canKillOtherPieces(pathNumber: number, playerNumber: number, pieceIndex: number): boolean {
        if (pathNumber <= 0) return false;
        if (this.isSafeTile(pathNumber)) return false;
        
        const tile = this.getPathTile(pathNumber, playerNumber, pieceIndex);
        if (tile.children.length >= 3) return false;
        
        return tile.children.some(child => child.getComponent(Piece).playerNumber !== playerNumber);
    }

    public piecesOnTile(pathNumber: number, playerNumber: number): number {
        if (pathNumber <= 0) return 0;
        return this.pieces.filter(p => p.pathNumber === pathNumber).length;
    }

    public getKillablePieces(killerPiece: Piece): Piece {
        if (killerPiece.pathNumber <= 0) return null;
        if (this.isSafeTile(killerPiece.pathNumber)) return null;
        
        const piecesOnTile = this.pieces.filter(
            p => p !== killerPiece && p.pathNumber === killerPiece.pathNumber
        );
        
        if (piecesOnTile.length === 1) {
            const opponentPiece = piecesOnTile[0];
            if (opponentPiece.playerNumber !== killerPiece.playerNumber) {
                return opponentPiece;
            }
        }
        
        return null;
    }

    public doesEnemyExist(piece: Piece, pathNumber: number): boolean {
        if (pathNumber === 0 || pathNumber > this.TotalPathCount(this.gameOptions)) return false;
        
        return this.pieces.some(
            p => p.pathNumber === piece.pathNumber && p.playerNumber !== piece.playerNumber
        );
    }

    public doesAnyPieceExistOnPath(piece: Piece, pathNumber: number): boolean {
        if (pathNumber === 0 || pathNumber > this.TotalPathCount(this.gameOptions)) return false;
        
        return this.pieces.some(
            p => p !== piece && p.pathNumber === pathNumber
        );
    }

    public getRocketLandingPathNumber(piece: Piece): number {
        let currentPathNumber = piece.pathNumber;
        
        for (let i = 0; i < this.rocketSteps; i++) {
            if (!this.isValidPathNumber(currentPathNumber + 1, piece.playerNumber)) {
                console.log("Invalid Path Number: ", currentPathNumber + 1);
                return currentPathNumber;
            }
            currentPathNumber += 1;
        }
        
        return currentPathNumber;
    }

    // Player related methods
    public getPlayerPositionOnBoard(playerNumber: number): number {
        const positions = [0, 2, 3, 1];
        return positions[playerNumber] || 0;
    }

    public getPlayerBase(playerNumber: number): Node {
        return PlayerService.instance.getPlayerBase(playerNumber);
    }

    public getPlayerPiece(playerNumber: number, pieceIndex: number): Piece {
        return PlayerService.instance.getPlayerPiece(playerNumber, pieceIndex);
    }

    // Piece management methods
    public addPieces(pieces: Piece[]): void {
        this.pieces.push(...pieces);
    }

    public addPiece(piece: Piece): void {
        this.pieces.push(piece);
    }

    public updateAllPiecePositions(): void {
        this.pieces.forEach(piece => piece.updatePiecePosition());
    }

    // Animation related methods
    public getLadderAnimationPoints(ladderPresetIndex: number): AnimationPoints {
        const ladderIndex = this.ladders.findIndex(l => l.ladderPresetIndex === ladderPresetIndex);
        return this.laddersAnimationPoints[ladderIndex];
    }

    public getSnakeAnimationPoints(snakePresetIndex: number): AnimationPoints {
        const snakeIndex = this.snakesData.findIndex(s => s.snakePresetIndex === snakePresetIndex);
        return this.snakesAnimationPoints[snakeIndex];
    }

    public getSnake(snakePresetIndex: number): Snake {
        const snakeIndex = this.snakesData.findIndex(s => s.snakePresetIndex === snakePresetIndex);
        return this.snakes[snakeIndex];
    }
}

