export function GetEndTile(startTile: number, xOffset: number, yOffset: number, totalTilesCount: number, tilesPerRow: number): number {

    // Get row and column of start tile
    let startRow = Math.floor((startTile - 1) / tilesPerRow);
    let startCol = (startTile - 1) % tilesPerRow;
    if (startRow % 2 === 1) {
        // Odd rows go right to left
        startCol = tilesPerRow - 1 - startCol;
    }

    // Apply offsets
    let endRow = startRow + yOffset;
    let endCol = startCol + xOffset;

    // Check bounds
    if (endRow < 0 || endRow >= Math.ceil(totalTilesCount / tilesPerRow) || 
        endCol < 0 || endCol >= tilesPerRow) {
        return -1;
    }

    // Convert back to tile number
    let endTile;
    if (endRow % 2 === 0) {
        // Even rows go left to right
        endTile = endRow * tilesPerRow + endCol + 1;
    } else {
        // Odd rows go right to left
        endTile = endRow * tilesPerRow + (tilesPerRow - 1 - endCol) + 1;
    }

    // Check if tile number is valid
    if (endTile < 1 || endTile > totalTilesCount) {
        return -1;
    }

    return endTile;
}